

.script-box {

    .matching-parenthesis {
        position: absolute;
        height: 19px;
        width: 9px;
        background-color: #dcdcdc6b;
        z-index: 20000;
    }

    .user-script-code {
        font-family: monaco, monospace;

    }

    textarea {
        outline: none !important;
        min-height: 200px;
    }

    pre {
        min-height: 180px;
        font-size: 11pt !important;

        .parameter {
            font-size: 11pt;
        }
    }


    .token.keyword {
        color: #34a1ff;
    }
    .token.selector, .token.attr-name, .token.string, .token.char, .token.builtin, .token.inserted, .token.operator {
        color: #23ffecf2;
        background-color: #44ddff33;
    }

    .token.property, .token.tag, .token.boolean, .token.number, .token.constant, .token.symbol, .token.deleted {
        color: #4ebdff;
    }

    .token.function {
        color: #4cff7c;
    }

    .token.punctuation {
        color: #91a8ff;
    }

    .syntax-highlighter {
        white-space: pre-wrap;
        pointer-events: none;
        width: 100%;
        word-break: break-all;
        overflow: hidden;
        position: absolute;
        top: -10px;
        left: 0px;
        font-size: 11pt;
        padding: 8px;
        padding-right: 16px;
        font-family: monaco, monospace;
        z-index: 10000;
    }

    .syntax-highlighter.keyword {
        color: #034046;
    }

    .syntax-highlighter.letter {
        color: #ffad9a;
    }

    .syntax-highlighter.number {
        color: #9cf738;
    }
}
