.accumulator, .higher-order-parameters, .zmacro {
    display: flex;
    
    .acc-column {
        .parameter-box {
            width: 120px !important;
            .slider.slider-horizontal {
                width: 120px !important;
            }
        }

        .random-map:active {
            box-shadow: 0px 0px 2px 2px white;
        }

        .random-map {
            cursor: pointer;
            padding: 2px;
            background-color: #3c3c3c;
            width: 65px;
            border-radius: 5px;
            margin-left: auto;
        }
    }

    .acc-column.mappings {
        min-width: 150px;
        margin-left: 5px;
        max-height: 300px;
        overflow-y: scroll;
    }

    .accumulator-mapper {
        display: flex;
        margin-bottom: 2px;

       
        .mapped-parameter {
            color: #8dd4ff;
            background-color: black;
            padding-left: 5px;
            padding-right: 5px;
            margin-left: 3px;
            cursor: pointer;
            max-width: 100px;
            white-space: nowrap;
            overflow: hidden;
            margin-right: 5px;
        }

        .mapped-parameter.selected {
            animation: fadeparam 1s 0.1s infinite;
            animation-direction: alternate;
        }

        .macro-map-button {
            background-color: black;
            width: 40px;
            border-radius: 0px;
            width: 40px;
            text-align: center;
            cursor: pointer;
        }

        .delete-parameter-btn {
            margin-left: 5px;
            background-color: #080808;
            color: white;
            border-radius: 15px;
            height: 15px;
            width: 15px;
            text-align: center;
            line-height: 15px;
            margin-top: 4px;
            cursor: pointer;
        }

        .acc-parameter-value, .depth {
            padding-left: 3px;
            padding-right: 3px;
            width: 30px;
            overflow: hidden;
            background-color: #ffffff42;
            line-height: 20px;
            height: 20px;
            font-size: 7pt;
            margin-right: 3px;
        }

        .volume-box:active {
            cursor: unset;
            .volume-indicator {
                cursor: unset;
            }
        }

        input.volume-box {
            width: 45px;
        }

        .volume-box {
            height: 20px;
            line-height: 20px;
            margin: unset;

            .volume-indicator {
                height: 18px;
                line-height: 20px;
                background-color: #1392ff;
                opacity: 0.7;
            }
        }

        .acc-parameter-value {
            padding-left: 3px;
            padding-right: 3px;
            width: 30px;
            overflow: hidden;
            background-color: #ffffff42;
            line-height: 20px;
            height: 20px;
            margin-left: auto;
            font-size: 7pt;
        }
    }
}


.accumulator-mapper.disable {
    pointer-events: none;
    opacity: 0.2;
}

.accumulator-mapper.mapping {
    .macro-map-button.mapping {
        background-color: #31d76b;
        color: black;
    }

}

