.z-object.z-message {
    border: 2px solid transparent;
    border-radius: 5px !important;
    background-color: #201f1feb;
    min-width: 50px;
    max-width: 300px;
    white-space: wrap;
    min-height: 20px;
    height: unset;
    // width: unset !important;
    font-size: 10pt;

    .message-content {
        cursor: pointer;
        z-index: 100;
        flex: 1;
        color: #ffffff;
        max-height: 400px;
        overflow-y: scroll;
        color: white;
        width: 100%;
        border-radius: 5px;
        padding-left: 1px;
    }
    input {
        font-size: 10pt;
        background-color: transparent ;
        color: white;
        border-radius: 5px !important;
        margin-top: 0px;
        margin-bottom: 0px;
    }
}

.z-object.custom {
    min-width: 30px;
    min-height: 20px;
    width: unset;
    height: unset;
    background-color: #28282bf7;
    border: 2px solid #6f6f6f0f;
}

.z-object {
    width: 40px;
    height: 30px;
    background-color: #4a4949;
    z-index: 100;
    position: absolute;
    display: flex;
    border: 2px solid transparent;

    .basic {
        width: 100%;
        margin-left: -2px;
        flex: 1;
    }

    .hide-basic {
        display: none;
    }
    
    .possibilities {
        top: 30px !important;
        left: 0px !important;
        width: 400px;
        z-index: 100000000000000000000000;
        max-height: 400px;
        overflow-y: scroll;
    }

    .z-object-input {
        cursor: pointer;
    }

    .z-object-input.editing {
        cursor: text;
    }

    input {
        margin-top: 1px;
        font-size: 10pt;
        border-radius: 0px !important;
        flex: 1 1;
        background-color: #262626;
        width: 100%;
        color: white;
        min-width: 100%;
        /* height: 90%; */
        border: none !important;
        outline: none !important;
        margin-bottom: 5px;
    }
}

