        }
        svg.rack-canvas {
            width: 100%;
            height: 100%;
        }
    }

    .drum-router {
        flex: 1;
        overflow: hidden;
        width: 100%;
        height: 100%;
    }

    .jaki {
        width: 100%;
        height: 100%;
        flex: 1;

        .figures {
            width: unset;
            margin: 0px;
            margin-top: 10px;
            margin-bottom: 10px;

