.z-object {
    .ztoggle.toggled {
        .inner-x {
            &:before,
            &:after {
                background-color: white;
            }
        }
    }

    .ztoggle:active {
        box-shadow: 0px 0px 2px 2px white;
    }
    .ztoggle {
        cursor: pointer;
        width: 100%;
        height: 100%;
        display: flex;

        .inner-x {
            position: relative;
            width: 100%;
            height: 100%;
            color: black;
            margin: auto;
            margin-top: 0px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            &:before {
                transform: rotate(45deg);
            }

            &:after {
                transform: rotate(-45deg);
            }

            &:before,
            &:after {
                position: absolute;
                content: '';
                width: 100%;
                height: 4px; /* cross thickness */
                background-color: black;
            }
        }
    }
}

