}

@keyframes node-flash{
    from{
        box-shadow: 0px 0px 12px 12px #ffa43aff;
    }
    to{
        box-shadow: 0px 0px 12px 12px #ffa43a00;
    }
}



.z-object.has-inlet-fields {
    .inlets {
        display: none;
    }

    .inner-custom {
        overflow: visible !important;
    }

    .inlet-field {
        position: relative;

        .inlet:hover {
            background-color: red;
            border-radius: 1.2ch;
            margin-top: 0px;
            margin-bottom: 0px;
            height: 1.2ch;
            transform: scale(1.5);
        }
     
        .inlet.connected {
            background-color: #404040;
        }

        .inlet {
            width: 0.8ch;
            height: 1.2ch;
            border-radius: 0 1.2ch 1.2ch 0;
            background: white;
            cursor: pointer;
            position: absolute;
            left: -8px;
            top: 6px;
        }
    }
}

