.spore-editor {

    .slider.slider-horizontal .slider-track {
        height: 5px;
    }

    .modals-container {
        z-index: 1000000000000000000000;
    }

    .export-info {
        position: absolute;
        left: 0px;
        right: 0px;
        top: -240px;
        bottom: 0px;
        margin: auto;

        width: 200px;
        height: 200px;
        background-color: black;
        color: white;
    }
    .export-for-mint {
        background-image: linear-gradient(45deg, #0ecdb2, transparent);
        position: absolute;
        padding: 10px;
        font-size: 16pt;
        bottom: 30px;
        right: 150px;
        background-color: #38dd38;
        border-radius: 15px;
        color: black;
    }
    
    .slider.slider-horizontal .slider-selection, .slider.slider-horizontal .slider-track-low, .slider.slider-horizontal .slider-track-high {
        background-color: #27262d;
    }

    .slider-handle {
        background-color: #000000c2;
        width: 13px !important;
        height: 13px !important;
        top: 0px !important;
        border: 1px solid white;
        background-image: linear-gradient(#d0d0d000, #76b5d41f) !important;
    }

    .mix-buttons {
        position: absolute;
        bottom: 15px;
        right: 15px;
        padding: 10px;

        
        .playpause input[type="checkbox"]:checked + label {
            border-style: solid;
            height: 30px;
            border-width: 14px 0 14px 24px;
        }
        .stop-button {
            height: 30px;
            width: 30px;
        }
        .playpause label {
            height: 30px;
            border-width: 0px 0 0px 24px;
        }

        .record-btn, .piano-roll-button, .loop-icon {
            display: none;
        }
    }
    img.spores-icon {
        position: absolute;
        right: 15px;
        top: 15px;
    }
    flex: 1;
    display: flex;
    width: 100%;
    height: 100%;
    background-color: #2c2f40;
    background-image: repeating-linear-gradient(90deg, rgba(201, 201, 201, 0.06) 0px, rgba(201, 201, 201, 0.06) 1px, transparent 1px, transparent 96px), repeating-linear-gradient(0deg, rgba(201, 201, 201, 0.06) 0px, rgba(201, 201, 201, 0.06) 1px, transparent 1px, transparent 96px), repeating-linear-gradient(0deg, rgba(201, 201, 201, 0.09) 0px, rgba(201, 201, 201, 0.09) 1px, transparent 1px, transparent 12px), repeating-linear-gradient(90deg, rgba(201, 201, 201, 0.09) 0px, rgba(201, 201, 201, 0.09) 1px, transparent 1px, transparent 12px), linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0));

    .presets-editor {
        .key-scaling-toggle {
            display: none;
        }

        z-index: 10000000;
        position: relative;
        display: flex;
        align-items: flex-start;

        .formants-wrapper {
            .formant-presets-icon {
                width: 12px;
                height: 12px;
            }
        }

        .formant-parameters {
            .parameter-lfo-toggle, .parameter-envelope-toggle {
                width: 8px;
                height: 8px;
                margin-left: 2px;
                margin-right: 2px;
            }
        }

        .no-chain-selected{
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: 5;
            background-color: #8080805c;
            display: flex;
            backdrop-filter: blur(16px);
            span {
                font-size: 32pt;
                margin: auto;
            }
            
        }
        .chain-selector {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            .save-and-load {
                display: flex;
                width: 100%;
            }

            .load-chain {
                align-items: flex-start;
                background-color: white;
                color: black;
                padding: 5px;
                padding-left: 10px;
                padding-right: 10px;
                border-radius: 12px;
                margin-top: 15px;
            }
            .save-chain {
                align-items: flex-start;
                background-color: red;
                padding: 5px;
                padding-left: 10px;
                padding-right: 10px;
                border-radius: 12px;
                margin-top: 15px;
                margin-left: auto;
            }
            .chain-inner {
                display: flex;
            }
            position: absolute;
            right: -280px;
            background-color: gray;
            padding: 15px;

            .chain-option.selected {
                background-color: white;
                color: black;
            }
            .chain-option {
                cursor: pointer;
                margin-left: 15px;
            }
        }

        .effect.enabled {
            margin: 0px;
            background-color: #acacacad;
        }
        .effect.racked {
            .rack-toolbar {
                position: absolute;
            }
            .add-macro-button, .show-macros-button {
                display: none;
            }
            .load-preset-button {
                display: none;
            }
            .horizontal {
                flex-direction: column;
                .rack-canvas {
                    height: 300px;
                }


                .draw-component {
                }
            }

            .fx-button {
                display: none;
            }
            .audio-effect-container {
                border-top: 2px solid white;

                .effect.racked {
                    .horizontal {
                        flex-direction: row;

                        .rack-canvas {
                            height: 300px;
                        }
                    }
                }
                .effect {
                    background-color: #0000008a;
                    border-radius: 0px;
                    .effect-header {
                        border-radius: 0px;
                    }
                }
                .effect-header {
                    display: flex;
                }
            }
            .effect-header, .rack-toolbar.documented, .effect-lock, .detach-effect-icon {
                display: none;
            }
        }

    }
    .v3.nft-mode {
        position: absolute;
        width: 400px;
        right: 0px;
        .editor {
            .click-to-start {
                background-color: unset !important;
                //transform: scale(0.8) translate(40px, 0px);

                .image-wrapper {
                    margin-right: 30px;
                }
                    span {
                        margin-right: 30px !important;
                    }
            }
        }
    }
    .nft-viewer {
        .expand-icon {
            display: none;
        }
        background-color: unset;
        margin-left: auto;

        .z-object.main-nft {
            box-shadow: rgb(87 84 84 / 40%) 0px 12px 28px 0px, rgb(0 0 0 / 35%) 0px 2px 4px 0px, rgb(0 0 0 / 16%) 0px 0px 0px 1px inset !important;
            margin-right: 20px;
        }
    }
}
