
.synth-browser.sampler-browser {
    align-items: normal;
    .synth-thumbnail {
        width: 100px;
        height: 100px;
        background-color: rgb(102, 214, 255);
    }

    .samples-breadcrumb {
        height: 90px;
    }

    .info-text {
        float: none;
    }

    .blank-patch {
        color: #5d5d5d;
    }
    
    .name-input-container {
        display: flex;

        .preset-name-input {
            background-color: #1d1d27;
            color: #bdbdd4;
            width: 250px;
            height: 30px;
            padding: 2px;
        }
        
        ::-webkit-input-placeholder { /* Edge */
            color: #4d4d54 !important;
        }
        
        :-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: #4d4d54 !important;
        }
        
        ::placeholder {
            color: #4d4d54 !important;
        }
        
        .preset-save-button {
            float: none;
            margin-left: 0px;
            font-size: 14pt;
            color: black;
            margin-top: 1px;
            background-color: #56ff82;
            border: none;
            text-align: center;

            line-height: 25px;
            padding: 2px;
            height: 28px;
            cursor: pointer;
        }

        .preset-save-button.disabled {
            pointer-events: none;
            opacity: 0.1;
        }

        .preset-save-button:active {
            box-shadow: 0px 0px 4px 4px #6d6d6d;
        }
    }

}

.presets-explorer-container {
    display: flex;

    .sample-dropdown {
        background-color: #26226fc7;
        position: absolute;

        .menu-item:hover {
            .packs {
                left: 30px;
            }
        }
    }

    .preset-tags-chooser {
        height: 90%;
        width: 200px;
        display: flex;
        flex-wrap: wrap;
        overflow-y: scroll;
        align-items: flex-start;
        align-content: flex-start;

        .preset-tag.selected {
            color: black;
            background-color: white;
            box-shadow: 0px 0px 2px 2px gray;
        }

        .preset-tag {
            cursor: pointer;
            padding: 3px;
            font-size: 9pt;
            background-color: #33334c;
            color: white;
            margin: 3px;
        }
    }
}

.sample-explorer.selected {
    .presets-list {
        .preset-option.selected {
            background-color: white;
            color: black;
        }
    }
}

.sample-explorer {
    font-size: 10pt;
    width: 250px;

    .presets-search-container {
        position: relative;

        input {
            width: 100%;
        }

        .search-clear-button {
            color: black;
            position: absolute;
            right: 5px;
            top: 2px;
            z-index: 10;
            cursor: pointer;
        }
    }

    .explorer-top {
        display: flex;
        white-space: nowrap;

        .override-label {
            margin-top: 4px;
            color: #797979;
        }

        .explorer-option {
            margin-left: 5px;
            background-color: #302f2f;
            padding-left: 3px;
            padding-right: 3px;
            cursor: pointer;
        }

        .explorer-option.selected {
            background-color: white;
            color: black;
            box-shadow: 0px 0px 2px 2px #636161;
        }
    }

    .presets-toolbar {
        .heart {
            margin-top: 3px;
        }

        .packs-btn {
            filter: hue-rotate(326deg) contrast(2);
            margin-left: 5px;
            margin-top: 3px;
            width: 15px;
            height: 15px;
            cursor: pointer;
        }
    }

    .presets-list {
        margin-top: 10px;
        font-size: 9pt;
        height: 90%;
        overflow-y: scroll;
        overflow-x: hidden;

        .pack-presets {
            .pack-header {
                display: flex;
                cursor: pointer;
                padding: 2px;
                .pack-thumb {
                    margin-top: 4px;
                    width: 10px;
                    height: 10px;
                    margin-right: 5px;
                }

                .pack-name {
                    font-size: 9pt;
                    margin-left: 5px;
                }
            }

            .preset-option {
                padding-left: 10px;
            }
        }

        .preset-option.showing-dropdown {
            border: 1px dashed white;
        }

        .preset-option {
            line-height: 13px;
            white-space: nowrap;
            cursor: pointer;
            padding: 3px;
            font-size: 13px;
            display: flex;
            position: relative;
            color: #d2d2d2;
            
            .pack-thumb {
                margin-top: 3px;
                width: 8px;
                height: 8px;
            }

           .favorited.heart:before,  .favorited.heart:after {
                background: #fc2e5eba;
            }

            .heart {
                right: 0px;
                top: 0px;
                transform: scale(0.65);
                margin-top: 0px;
                height: 10px;
                cursor: pointer;
            }

            .empty-preset {
                width: 15px;
            }

            .preset-type {
                width: 12px;
                margin-right: 7px;
                border-left: 6px solid transparent;
                border-right: 6px solid transparent;
                border-bottom: 6px solid black;
                margin-bottom: 4px;
            }

            .preset-tags {
                margin-left: 5px;

                .preset-tag {
                    margin-left: 3px;
                    color: #68dcd1;
                }
            }
        }

        .preset-option.selected {
            background-color: gray;
            color: black;
        }
    }
}

