$synthColor: #0808086e;
$fmBtnColor: #151414;
$fmFillOld: black;
$fmFill: black;

.instrument.rack-synth {

    .rack-canvas {
        rect {
            rx: 8px;
        }

        path {
            stroke-dasharray:  unset !important;
        }
    }
    margin-left: 0px !important;
    .slider.slider-horizontal .slider-track {
        height: 5px;
    }
    
    .slider.slider-horizontal .slider-selection, .slider.slider-horizontal .slider-track-low, .slider.slider-horizontal .slider-track-high {
        background-color: #27262d;
    }

    .slider-handle {
        background-color: #000000c2;
        width: 13px !important;
        height: 13px !important;
        top: 0px !important;
        border: 1px solid white;
        background-image: linear-gradient(#d0d0d000, #76b5d41f) !important;
    }

    .global-parameters {
        display: flex;
    }
}

.instrument.rack-synth, .pulled-instrument  {
    background-color: #2a28282b;

    .mods-inner {
        .envelope-component {
            min-width: unset;
            width: unset;
            padding: 0px;
            .knobs {
                margin-top: unset;
            }
            .instrument-subsection-name {
                display: none;
            }
        }
    }

    svg.harmonicity-mod {
        width: 256px;
        rect {
            width: 256px;
        }
    }
    svg {
        .oscillator-edge {
            stroke: #2eabff;
            stroke-dasharray: 18, 2; 
        }

        .oscillator-edge.fm-output-edge {
            stroke: #005dff;
            
        }

        .rack-tooltip {
            fill: #3837374f;
        }

        text.modulator-node {
            fill: white;
        }

        .node-text {
            fill: black;
        }
        
        text {
            //fill: black;
        }
    }

    .aux-parameters {
        margin-left: 5px;
        margin-top: auto;
        background-color: $synthColor;
    }

    .horizontal {

        .parameter-mod-max, .parameter-mod-min {
            display: flex;
            width: 210px;
            .number-input-container {
                float: none;
                margin-left: auto;
            }
        }
    }

    .instrument-subsection.envelopes {
        width: 240px;
        margin-right: 0px;
        flex: 1;
    }


    .parameter-lfo-toggle, .parameter-envelope-toggle {
        margin-right: 1.5px !important;
    }

    .formant-effect {
        background-color: $synthColor;
        padding: 5px;
        margin: 5px;
        min-width: 235px;
        .sub-header {
            font-size: 10pt;
        }

        .Dropdown-control {
            padding: 3px;
            width: 100px;
        }

        .Dropdown-option {
            padding: 3px;
            font-size: 8pt;
        }

        .effect {
            margin: 0px;
        }
    }

    .instrument-section {
        display: flex;
        flex-direction: column;
    }

   
    .harmonics-editor {
        height: 100px;
        overflow: scroll;
    }

    .fm-operator-control {
        .osc-type-dropdown {
            width: 110px;
            .Dropdown-control {
                width: 110px;
            }
        }
    }

    .sections {
        display: flex;
    }

    .keyboard-control {
        width: 100%;
        text-align: center;
        display: flex;
    }

    .frequency-parameters {
        display: flex;

        .slider.slider-horizontal {
            width: 90px;
        }
    }

    .automation-icon {
        cursor: pointer;
    }
    
    .Dropdown-control {
        border-color: #444444 !important;
        background-color: white !important;
        color: black !important;
    }

    .algo-container {
        margin-bottom: 3px;
    }

    .harmonicity-mod {
        g.selected {
            z-index: 5;
        }
        g {
            z-index: 4;
        }
        circle.selected {
            opacity: 1;
        }
        circle {
            opacity: 0.9;
        }

        text.fm-text {
        }
        text.fm-text.selected {
        }

        rect {
            fill: #000000b3;
        }

        text {
            fill: black;
        }

        line.operator-line {
            stroke: #42c3b9;
            stroke-dasharray: 4,2;
        }

        guide-line {
            color: blue;
        }

        .fm-guide-line {
            stroke: #1a1c31;
        }
    }

    .partials {
        margin-left: 5px;

        .slider-horizontal.slider {
            width: 80px !important;
        }
    }

    .envelopes.instrument-section {
        .env-toolbar {
            display: flex;

            .env-name {
                background-color: #2b2b2b;
                padding-left: 2px;
                padding-right: 2px;
            }

            .env-params {
                display: flex;
                font-size: 8pt;
                margin-left: 3px;

                .env-param.selected {
                    color: black;
                    background-color: white;
                }

                .env-param {
                    cursor: pointer;
                    position: relative;
                    color: white;
                    width: unset;
                    line-height: 20px;
                    margin-right: 5px;

                    .param-val {
                        color: #b8b8b8;
                    }
                }
            }
        }
    }

    .graphical-envelope {
        margin-top: 5px;
        svg {
            background-color: #402ed24f;

            polygon {
                //fill: #ff0076;
            }

            circle.selected.point {
                fill: red !important;
            }

            .point:hover {
                fill: red;
                r: 7px;
            }

            line, circle {
            }
        }
    }

    .instrument-subsection {
        background-color: $synthColor;
        color: white;
    }


    .slider-handle {
    }


    .edge-properties {
        color: black;
        padding: 5px;

        .parameter-mod {
            flex-direction: column;
        }
    }

    .synth-mods-name {
        font-size: 11pt;
    }

    .parameter-mod {
        opacity: 1;
        display: flex;
        width: 400px;

        .parameter-mod-pieces {
            flex: 1;
        }
        
        .parameter-mod-min, .parameter-mod-max {
            margin-right: 5px;
            color: black;
        }
        
        .parameter-mod-modulator {
            color: white;
        }
        
        .parameter-mod-text {
            color: black;
        }
        
        .parameter-mod-parameter {
            color: white;
        }
    }

    .osc-type-icon {
        width: 14px;
    }
    .osc-type-icon.sawtooth, .osc-type-icon.sine, .osc-type-icon.triangle {
        width: 18px;
    }
    .fm-parameters {
        background-color: $synthColor;
        max-width: 262px;;
        width: 262px;;
        overflow: hidden;
        
        .osc-name {
            width: 20px;
        }

        .fm-parameters-container {
            flex: 1;
            display: flex;
        }

        .lfo-boxes {
            flex-wrap: nowrap;
        }

        .parameter-box {
            .parameter-label-container {
                margin-right: auto;
                flex: 1;
            }

            .slider.slider-horizontal {
                width: 90px;
            }
        }
    }

    .fm-parameter {
        position: relative;
        
        .values {
            display: flex;
            padding: 3px;

            .whole-value, .decimal-value {
                padding-left: 3px;
                background-color: black;
                width: 30px;
                line-height: 15px;
                height: 15px;
            }

            .whole-value:active, .decimal-value:active {
                cursor: none;
            }

            .dot-value {
                line-height: 1px;
                padding: 3px;
                font-size: 20px;
            }
        }
    }

    .lfo-component {
        background-color: white;
        color: black;
        min-width: 250px;

        .instrument-subsection-name {
            color: black !important;
        }

        .refined-checkbox-preferences {
            color: black;
        }

        .mods-button {
            display: none;
        }
    }

    .slider-handle {
        // background-image: linear-gradient(#ff0177, #c382ff) !important;
    }

    .partials {
        .parameter-box {
            display: flex;
            margin-left: 0px;
            margin-right: 0px;

            .parameter-label {
                margin-top: 0px;
            }
            
            .parameter-label-value {
                width: 20px;
            }

            .slider {
                margin-left: 10px;
            }
        }
    }
}

.envelope-options {
    clear: both;
    display: flex;

    .envelope-option {
        border-bottom: 1px solid white;
        margin-right: 2px;
        padding: 3px;
        cursor: pointer;
    }
    
    .envelope-option.selected {
        background-color: white;
        color: black;
    }
}


.instrument.rack-synth, .track-mini-control, .pulled-effect {
    .operators-mixer.expanded {
        width: 480px;
    }

    .operators-mixer {
        width: 210px;
        .mixers {
            display: flex;

        }

        .slider.slider-horizontal {
            width: 80px;
        }

        padding: 5px;
        padding-bottom: 0px;
        background-color: $synthColor;
        margin: 5px;

        .mixer-parameter {
            writing-mode: vertical-rl;
            text-orientation: sideways-right;
        }

        .waveform-selector ::-webkit-scrollbar-thumb {
            background: white !important;
        }

        .waveform-selector {
            padding: 5px;
            margin-top: 5px;
            position: absolute;
            left: 0px;
            top: 7px;
            z-index: 1001;
            width: 200px;
            background-color: #000000d4;
            color: white;

            .parameter-box {
                display: flex;
                margin-left: 0px;

                .parameter-label {
                    margin-right: 15px;
                }

                .parameter-label-value {
                    width: 30px;
                    overflow: hidden;
                }
                .slider.slider-horizontal {
                    margin-top: 5px;
                }
            }

            .inner {
                display: flex;
            }

            .types {
                .type-option {
                    background-color: #1c1c1c33;
                    margin-top: 1px;
                    margin-bottom: 1px;
                    padding: 2px;
                }
                .type-option.selected {
                    background-color: white;
                    color: black;
                }
            }

            .waveforms {
                padding: 3px;
                flex: 1;
                max-height: 100px;
                overflow: scroll;

                .osc-type.selected {
                    color: black;
                    background-color: white;
                }
                .osc-type {
                    padding-left: 5px;
                    margin: 1px;
                    background-color: #0000ff12;
                    text-align: left;
                }
            }
        }
        .operator-mixer {
            margin-right: 4px;
            width: 45px;

            .operator-name.disabled {
                opacity: 0.2;
            }

            .operator-name.selected {
                background-color: white;
                color: black;
                box-shadow: 0px 0px 1px 1px #ffffffad;
            }

            .operator-name {
                border-top-left-radius: 8px;
                border-top-right-radius: 8px;
                background-color: $fmFill;
                color: white;
                width: 25px;
                text-align: center;
                padding-top: 3px;
                padding-bottom: 3px;
                margin-left: auto;
                margin-right: auto;
                cursor: pointer;
            }

            .oscillator-waveform.showing {
                color: black;
                background-color: white;
                box-shadow: 0px 0px 1px 1px #ffffffad;

                img {
                    filter: invert(1);
                }
            }

            .oscillator-waveform {
                border-radius: 5px;
                position: relative;
                text-align: center;
                width: 45px;
                cursor: pointer;
                font-size: 8pt;

                background-color: $fmFill;
                color: white;

                .waveform-type {
                    overflow: hidden;
                }
            }

            .volume-box:active {
                .value {
                    opacity: 1;
                }
            }
 
            .volume-box {
                border-radius: 3px;
                border-color: black;
                margin-left: auto;
                margin-right: auto;
                width: 25px;
                display: flex;
                flex-direction: column;
                background-color: #402ed24f;
                
                .rest {
                    flex: 1;
                }
                
                .volume-indicator {
                    border-radius: 3px;
                    opacity: 1;
                    background-image: linear-gradient(0deg, #262dff4a, transparent);
                    background-color: #ff0177;
                    cursor: unset;
                    position: relative;
                    margin-top: auto;
                    width: 23px;
                }
            }

            .value {
                opacity: 0;
                position: absolute;
                top: 20px;
                color: white;
                left: 5px;
            }
        }

        .operator-details {
            .formants-header {
                font-size: 12pt;
                color: #dfdfffa1;
                letter-spacing: 1px;
            }
            
            .volume-and-detune {
                display: flex;
                flex-wrap: wrap;
                margin-top: 5px;

                .parameter-box {
                    width: 90px;
                }
            }

            .parameter-label-value {
                max-width: 38px;
                overflow: hidden;
            }

            .dragging-formant-info {
                font-size: 16pt;
                height: 100px;
                text-align: center;
                width: 100%;
                width: 220px;
            }

        }
    }
}

.effect {
    .formant-parameters {
        .slider.slider-horizontal {
            width: 80px !important;
        }

    }
}
.instrument, .effect { 
    .formant-parameters {
        .inner {
            display: flex;
            .parameter-box {
                width: 90px;
            }
        }

        .osc-type-dropdown {
            width: 60px;
            .Dropdown-control {
                border-color: black !important;
                line-height: 15px;
            }
            .Dropdown-arrow {
                right: 0px;
                border-color: black transparent transparent;
            }
        }

        .fx-button.show {
            background-color: white;
            color: black;
            box-shadow: 0px 0px 3px 3px white;
        }

        .fx-button {
            background-color: #151414;
            color: white;
            text-align: center;
            cursor: pointer;

            .fx-name {
                margin-left: 15px;
                background-color: #5d5c5c38;
                padding-left: 5px;
                padding-right: 5px;
            }
        }
    }

    .formants-wrapper {
        background-color: #0e0e0e94;
        position: relative;
        overflow: hidden;

        .formant-presets-icon {
            width: 18px;
            height: 18px;
            position: absolute;
            top: 5px;
            left: 5px;
            z-index: 10;
        }



        .formant-analyser {
            position: absolute;
            top: 0px;
            left: 0px;
            pointer-events: none;
        }

        .expand-formants-icon {
            position: absolute;
            right: 5px;
            top: 5px;
            width: 15px;
            height: 15px;
            cursor: pointer;
        }

        .formants-helper {
            position: absolute;
            z-index: 1;
            pointer-events: none;

            line.gain-helper {
                opacity: 0.3;
            }
            line {
                stroke: #2a2727;
            }
        }


        div.circle-wrap {
            z-index: 999;
        }
        div.circle-wrap.selected {
            z-index: 1000;
        }
        
        .band-wrap.selected .circle-wrap.selected {
            will-change: tranform;
        }

        div.band-wrap.selected {
            z-index: 2;
        }
        div {
            z-index: 1;
            overflow: hidden;
            position: absolute;

            svg.circle-svg {
                z-index: 1000;
            }
        }
    }

    .circle-svg.selected {
        text {
            fill: black;
        }
    }

    .band-wrap.disabled {
        svg {
            path {
                fill: #6075af61;
                stroke: #777373;
            }
        }
    }
    svg {
        g.frmt {
            will-change: transform;
        }
        circle {
            z-index: 1000;
        }

        .bandwidth-visualizer {
            fill: #ff53259e;
            
        }

        .bandwidth.selected {
        }

        .bandwidth.disabled {
            fill: #6075af61;
            stroke: black;
        }

        .bandwidth {
            fill: #aee73661;
            stroke: #feee00;
            stroke-width: 2px;
            // stroke-dasharray: 5;
        }
        
        text {
            fill: #ffffff;
            font-size: 8pt;
            transform: translate(1px,0px);
        }

        .formant.enabled.selected {
            fill: #befe00;
        }

        .formant.enabled {
            stroke: #befe00;
            stroke-width: 2px;
        }

        .formant {
            stroke-width: 1px;
            fill: black;
            transform: scale(0.9) translate(1px, 1px);
            stroke: gray;
        }
    }
}
 

.filter-section {
    .parameter-box {
        .slider.slider-horizontal {
            width: 180px;
        }
    }
    .filter-types {
        display: flex;
        
        .option {
            background-color: $fmBtnColor;
            padding: 3px;
            color: white;
            margin-right: 5px;
        }
        
        .option.selected {
            background-color: black;
            background-image: radial-gradient(#14243b, transparent);
            color: white;
            border-bottom: 1px solid white;
        }
    }
}
 

