.piano-roll-container {
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .whole-wrapper {
        overflow: hidden;
    }

    .piano-roll-control {
        display: flex;
        border-top: 4px solid #191919;
        height: 90px;
        
        .control-choice {
            width: 52px;
            background-color: white !important;
            // background-image: linear-gradient(45deg, #c0fbfdb0, transparent 40%);

            .Dropdown-menu {
                top: -190px;
                width: 150px;

                .Dropdown-option:hover {
                    background-color: white !important;
                }

                .Dropdown-option {
                    background-color: #e3e3e3 !important;
                    font-size: 9pt;
                    color: black !important;
                }
            }

            .Dropdown-placeholder {
                background-color: transparent !important;
                height: 20px;
                width: 40px;
            }

            .Dropdown-control {
                background-color: transparent !important;
                color: #000000c2 !important;
                border: none;
                height: 40px;
                width: 1px;
                font-size: 9pt;
                padding-left: 2px;

                .Dropdown-arrow {
                    right: 3px !important;
                    border-color: black transparent transparent !important;
                }
            }
        }

        .controller-container::-webkit-scrollbar {
            display: none;
        }

        .controller-container {
            overflow-x: scroll;
            overflow-y: hidden;
            flex: 1;
            height: 90px;
            padding-left: 6px;
            
            
            .controller {
                padding-left: 6px;
                background-color: #0a0a0ae3;
                height: 90px;
                position: relative;

                .step-control-container:hover {
                    opacity: 1;
                }

                .step-control-container:active {
                    .param-tooltip {
                        display: flex;
                    }
                }

                .step-control-container {
                    
                    position: absolute;
                    width: 4px;
                    top: 0px;
                    height: 90px;
                    opacity: 0.6;
                    z-index: 2;

                    .param-tooltip {
                        display: none;
                        position: absolute;
                        left: 20px;
                        top: 5px;
                        font-size: 6pt;
                        width: 40px;
                        overflow: hidden;
                        background-color: #ffffff2b;
                        color: white;
                        padding: 3px;
                    }
                }

                .step-control {
                    width: 2px;
                    position: absolute;
                    bottom: 10px;
                    .step-control-circle.selected, .step-control-circle:hover {
                        filter: hue-rotate(140deg);
                    }
                    
                    .step-control-circle {
                        z-index: 1000;
                        width: 8px;
                        height: 8px;
                        border-radius: 4px;
                        position: absolute;
                        left: -3px;
                        top: -1px;
                    }
                }
            }
        }
    }
}

.notes-side-panel {
    position: relative;
    .piano-roll-side-control {
        position: absolute;
        bottom: 0px;
        height: 88px;
        width: 59px;
        background-color: black;
        z-index: 1000;
        border-top: 4px solid #191919;
    }
}
