.sequence-name {
    float: left;
}

.sequence-date {
    float: right;
}

.sequence {
    width: 300px;
    cursor: pointer;
    clear: both;
    height: 20px;
}

.folder {
    cursor: pointer;
}

.sequences-table {
    float: left;
}

.folders-table {
    float: left;
    margin-left: 100px;
}

.new-folder {
    border-style: solid;
    padding: 10px;
    cursor: pointer;
    float: left;
}

.move-to-folder {
    border-style: solid;
    padding: 10px;
    cursor: pointer;
    float: left;
}

.filter-to-folder {
    border-style: solid;
    padding: 10px;
    cursor: pointer;
    float: left;
}

.folder.selected {
    border-left-style: solid;
}

.sequence.selected {
    border-left-style: solid;
}


.combine-view {
    clear: both;
}

.combine-header {
    font-size: 18px;
    clear: both;
}

.private-sequence {
    padding: 3px;
    float: right;
    background-color: #f4225e;
    font-size: 8pt;
    margin-left: 10px;
}

.share-sequence {
    cursor: pointer;
    background-color: #5802d9;
}

input[type=radio] {
    width: 15px;
    padding: 5px;
}

.sequences.radio, .sequences.filter-radio {
    font-size: 8pt !important;
}

.privacy-note {
    margin-top: 5px;
    font-size: 10pt;
}

input[type=checkbox] {
    width: 20px;
}

label {
    margin-right: 10px;
}
