.zshader-material {
    padding: 25px;

    display: flex;
    flex-direction: column;
    position: relative;

    .shaders {
        display: flex;

        .shader.enabled {
            opacity: 1;
            .dot {
                opacity: 1;
                background-color: #2bffb0;
            }
        }
        
        .shader {
            padding: 2px;
            padding-left: 5px;
            padding-right: 5px;
            font-size: 10pt;
            background-color: black;
            color: white;
            opacity: 0.4;
            margin-right: 5px;

            .dot {
                background-color: gray;
            }
        }
    }

    .creating-uniform {
        position: absolute;
        background-color: #0a0a0ab5;
        width: 100%;
        backdrop-filter: blur(4px);
        width: 100%;
        height: 100%;
        top: 0px;
        left: 0px;
        padding-top: 10px;

        .create-row {
            display: flex;

            .label {
                line-height: 30px;
                margin-right: 5px;
            }

            input {
                flex: 1;
                height: 30px;
                font-size: 12pt;
                background-color: black;
            }
        }

        .done-btn {
            background-color: white;
            color: black;
            padding: 2px;
            width: 50px;
            text-align: center;
            cursor: pointer;
        }
    }
    
    .material-title {
        text-align: center;
        margin-bottom: 10px;
    }

    .new-uniform-button {
        padding: 5px;
        width: 110px;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        background-color: white;
        color: black;
        cursor: pointer;
        
    }

    .uniforms {
        margin-top: 10px;
        margin-bottom: 10px;
        font-size: 9pt;
        .uniform {
            max-width: 150px;
            max-height: 30px;
            overflow: hidden;
            white-space: nowrap;
        }
    }
}
