.rack-synth {
    .mods-container {
        display: flex;
        background-color: transparent;

        .mod-type-btn.selected {
            background-color: white;
            color: black;
        }
        
        .mod-type-btn {
            background-color: black;
            margin: 1px;
            width: 32%;
            text-align: center;
            cursor: pointer;
        }
    }

    .showing-mods.inner {
        flex-direction: column;
        .envelope-component {
            .pre-post-option {
                display: none;
            }
            .Dropdown-control {
                display: none;
            }
        }
        .lfo-component {
            label, .lfo-subsection-name {
                color: white !important;
            }
            background-color: black;
            color: white;
        }
    }
    .mods-inner {
        max-width: 700px;
        flex-wrap: wrap;
        display: flex;
    }

    
    .mods-header {
        display: flex;

        .mod-type {
            font-size: 14pt;
            margin-left: 15px;
        }

        .arrow-container {
            background-color: white;
            width: 45px;
            cursor: pointer;
        }
        .arrow-left {
            margin-left: 10px;
            height: 0px;
            margin-top: 12px;
            width: 0px;
            border-bottom: 6px solid transparent;
            border-top: 6px solid transparent;
            border-right: 8px solid black;
            position: relative;
            
            &:after {
                width: 14px;
                height: 4px;
                background-color: black;
                content: "";
                position: absolute;
                left: 10px;
                top: -2px;
            }
        }
    }
}
