.rack-synth {

    .fm-operator-component {
        position: relative;
        height: 170px;
        .z-object.custom-subpatch-container {
            top: 24px !important; 
            z-index: 0;
        }

        .z-object {
            overflow: hidden;
            left: 0px;
            border: none !important;
            background-color: transparent !important;
        }

        svg {
            position:absolute;
            top: 24px !important;
            left: 0px;
            z-index: 1;

            background-color: #0808086e;
            background-image: repeating-linear-gradient(90deg, rgb(201 201 201 / 8%) 0px, rgb(135 135 135 / 0%) 1px, transparent 1px, transparent 96px), repeating-linear-gradient(0deg, rgb(160 144 144 / 6%) 0px, rgb(227 221 221 / 0%) 1px, #24232342 1px, transparent 96px), repeating-linear-gradient(0deg, rgb(53 53 53 / 0%) 0px, rgb(121 116 116 / 9%) 1px, transparent 1px, transparent 12px), repeating-linear-gradient(90deg, rgb(201 201 201 / 3%) 0px, rgb(201 201 201 / 9%) 1px, transparent 1px, transparent 12px), linear-gradient(90deg, rgba(255, 255, 255, 0), rgb(255 255 255 / 0%));

            line {
                //stroke: transparent !important;
            }

            circle {
                //opacity: 0 !important;
            }

            rect {
                fill: transparent !important;
            }
        }

    }
    .z-object {
        box-shadow: unset;
        border: unset;
        .inlets, .outlets {
            display: none;
        }

    }
    .sample-explorer {
        .presets-list {
            width: 200px;
            background-color: black;
            background-image: radial-gradient(#152033, transparent);
            max-height: 250px;
            
            .preset-option.selected {
                background-color: white;
                color: black;
            }
        }
    }

    .save-formants-flow {
        display: flex;
        input {
            flex: 1;
            background-color: black;
            color: white;
        }

        .done-formants-btn {
            margin-left: 10px;
        }
        .confirm-formants-btn {
            padding: 5px;
            background-color: #3320c1;
            background-image: linear-gradient(45deg, transparent, #29f359ab);
            border-top-right-radius: 8px;
            border-bottom-right-radius: 8px;
            color: white;
            text-align: center;
        }
    }
    .sample-explorer {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        .save-formants-btn {
            padding: 5px;
            border-radius: 5px;
            background-color: black; 
            background-image: radial-gradient(#14243b, transparent);
            color: white;
            align-items: flex-start;
        }
    }

    .explorer-row {
        display: none;
    }
     .presets-search-container {
        display: none;
    }
}
