.effect-header {
    .macro-map-button {
        margin-left: auto;
        border: 1px solid #5c5b5b;
        background-color: white;
        cursor: pointer;
        color: black;
        padding-left: 3px;
        padding-right: 3px;
        margin-right: 5px;
        font-size: 8pt;
    }

    .macro-map-button.mapping {
        background-color: #31d76b;
        color: black;
        border-color: #31d76b;
        box-shadow: 0px 0px 4px 4px #9affbe;
    }
}

.effect.Macros {
    .delete-button {
        margin-left: 5px;
    }
}

.macros-container {
    display: flex;
    flex-wrap: wrap;
    width: 320px;
    padding-left: 10px;

    .macro-knob {
        font-size: 8pt;
        margin-right: 12px;
        margin-bottom: 3px;

        .unmap.map-button {
            background-color: #a7a7ff47;
            box-shadow: 0px 0px 2px 2px #eaf8ff87;
        }

        .map-button {
            cursor: pointer;
            margin-top: 2px;
            background-color: white;
            color: black;
            border: 1px solid #6c757a;
            border-radius: 6px;
            padding-left: 3px;
            padding-right: 3px;
            width: 34px;
            text-align: center;
            font-size: 7pt;
        }
    }
}

.macro-details {
    padding: 5px;

    .unmap-button {
        cursor: pointer;
        border-radius: 10px;
        background-color: white;
    }

    .name-editor {
        width: 100%;
        background-color: white;
        color: black;
    }
    
    .sub-macro {
        display: flex;

        .sub-field.effect, .sub-field.parameter {
            max-width: 60px;
            margin-left: 0px;
            min-height: 0px;
        }

        .sub-field {
            margin-right: 5px;
        }

        .min-field, .max-field {
            width: 35px;
            background-color: #a8dbff7d;
            color: black;
        }
    }
}


.lfo-rate-dropdown {
    .macro-overlay {
        height: 20px !important;
    }
}
.parameter-button, .lfo-rate-dropdown {
    position: relative;
    
    .macro-overlay {
        left: 0px;
        top: 0px;
        height: 40px;

        flex-direction: column;

        .overlay-detail {
            display: none;
            height: 20px;
            opacity: 0.3;
        }
    }

    .macro-overlay.mapped {
        .overlay-detail {
            display: block;
        }
    }

    .macro-overlay.parameter-to-map {
        opacity: 1;

        .overlay-main {
            opacity: 0.7;
        }

        .overlay-detail {
            opacity: 1;
        }
    }

}

.macro-overlay.parameter-to-map {
    border: 1px dashed white;
    opacity: 0.6;
    box-shadow: 0px 0px 2px 2px #0e921f;
}

.macro-overlay {
    position: absolute;
    display: flex;
    z-index: 300;
    width: 100%;
    height: 100%;
    cursor: pointer;
    .overlay-main {
        opacity: 0.4;
        background-color: #31d76b;
        flex: 1;
    }

    .overlay-detail {
        opacity: 0.4;
        width: 30%;
        min-width: 28px;
        height: 100%;
        background-color: #31d76b;
        font-size: 8pt;
        color: black;
        text-align: center;
    }
}

.macro-overlay.mapped {
    .overlay-detail {
        opacity: 1;
    }
}


.effect-details {
    max-height: 200px;
    overflow-y: scroll;

    .macros-details-title {
        background-color: #007eff38;
        padding: 5px;
    }

    .no-macros-text {
        text-align: center;
        padding: 20px;
    }
    
    .macro-details {
        padding: 0px;
        background-color: #007eff38;
        margin: 5px;


        .min-field, .max-field {
            width: 50px;
        }
        .macro-name {
            background-color: #005cfd47;
            /* opacity: 0.5; */
            padding-left: 5px;
            color: #fbfbfb;
        }
    
        .sub-field.parameter, .sub-field.effect {
            border: none;
            width: 100px;
            max-width: 100px;
            overflow: hidden;
        }

        .sub-macro {
            display: flex;
            padding: 3px;
            margin: 5px;
            background-color: #fbfbfb52;
        }
    }
}

.macros-effect {
    
    .auto-map-settings {
        padding: 10px;
    }

    .auto-map-toolbar {
        display: flex;

        .generate-button {
            flex: 1;
        }

        .auto-map-settings-button {
            height: 25px;
            width: 25px;
            cursor: pointer;
            filter: hue-rotate(220deg);
        }
    }
    
    .generate-button {
        cursor: pointer;
        background-color: white;
        text-align: center;
        color: black;
        padding: 3px;
    }

    .generate-button:active {
        box-shadow: 0px 0px 2px 2px #b8b8b9;
    }
}
