.packs-component {
    padding: 10px;

    .new-pack-btn:active {
        box-shadow: 0px 0px 1px 1px white;
    }

    .new-pack-btn {
        background-color: black;
        width: 100px;
        color: white;
        padding: 5px;
        cursor: pointer;
    }

    .new-pack-flow {
        .name-container {
            background-color: blue;

            input {
                background-color: black;
                color: white;
            }
        }

        .drop-content-here {
            height: 200px;
            text-align: center;
            line-height: 200px;
            background-color: blue;
        }
    }


    .packs-selector {
        position: relative;
        .pack-search-container {
            width: 100%;
            position: absolute;
            top: 0px;
            left: 0px;
            z-index: 4;
            .clear-btn {
                position: absolute;
                right: 5px;
                top: 0px;
                font-size: 12pt;
                cursor: pointer;
            }

            .pack-search {
                width: 100%;
                backdrop-filter: blur(4px);
                background-color: #000000c9;
                color: white;
                outline: none !important;
            }
        }
    }

    .packs-grid {
        padding-top: 30px;
        display: flex;
        flex-wrap: wrap;
        max-height: 100%;
        overflow-y: scroll;

        .pack-option.selected {
            box-shadow: 0px 0px 5px 5px #ffffff99;
        }

        .pack-option.editing {
            animation: fadeparam 1s 0.1s infinite;
            animation-direction: alternate;

            .floppy-title {
                animation: fadeparam 1s 0.1s infinite;
                animation-direction: alternate;
            }
        }

        .pack-option.showing {
            width: 180px;
            position: relative;

            .pack-edit-btn {
                position: absolute;
                top: -15px;
                cursor: pointer;
                color: black;
                right: 5px;
                font-size: 20pt;
            }

            .pack-author {
                margin-top: auto;
                text-align: center;
                margin-bottom: 5px;
                font-size: 7pt;
                color: black;
            }

            .floppy-top {
                margin-left: auto;
                width: unset;
                height: unset;
                min-width: 100px;
                margin-right: auto;

                .floppy-title {
                    font-size: 9pt;
                    margin-top: 5px;
                    margin-bottom: 5px;
                    padding: 3px;
                    color: black;
                    margin-left: auto;
                    margin-right: auto;
                }
            }

            .floppy-sticker {
                display: flex;
                padding: 5px;
                flex-direction: column;
                max-height: 200px;
                overflow-y: scroll;
                font-size: 8pt;
                color: black;

                .load-pack:active {
                    box-shadow: 0px 0px 2px 2px white;
                }

                .load-pack {
                    cursor: pointer;
                    background-color: white;
                    color: black;
                    text-align: center;
                    padding: 5px;
                }

                .pack-content {
                    font-size: 8pt;
                    color: black;

                    .delete-btn {
                        display: none;
                    }
                }
            }
        }

        .pack-option {
            display: flex;
            z-index: 1;
            flex-direction: column;
            width: 120px;
            cursor: pointer;
            margin: 3px;
            color: white;

            .floppy-top {
                display: flex;
                background-color: #0000009e;
                margin-left: auto;
                margin-right: auto;

                .pack-title {
                    font-size: 6pt;
                    padding: 3px;
                    color: black;
                    margin-left: 10px;
                    margin-right: 10px;
                    margin-top: 5px;
                    margin-bottom: 5px;
                }
            }

            .floppy-sticker {
                background-color: #ffffff26;
                margin: 5px;
                display: flex;

                .pack-description {
                    margin-left: 5px;
                    color: black;
                    font-size: 9pt;
                }
            }
        }
    }
}

.pack-window {
    z-index: 2000;
    position: absolute;
    width: 250px;
    min-height: 250px;
    background-color: #ffffffcc;
    backdrop-filter: blur(4px);
}

.pack-option {
}

.pack-window,  .pack-option {
    .floppy-top {
        border-bottom-left-radius: 3px;
        border-bottom-right-radius: 3px;
    }
}

.pack-window,  .pack-option.showing {
    .delete-btn {
        color: black;
        position: absolute;
        font-size: 14pt;
        right: 5px;
        top: -5px;
        cursor: pointer;
    }

    .floppy-top {
        margin-left: auto;
        margin-right: auto;
        width: 180px;
        height: 50px;
        background-color: black;
        
        .pack-name-input {
            background-color: #ffffffcc;
            margin-top: 10px;
            margin-left: 50px;
            font-size: 9pt;
            outline: none !important;
            border: none;
            width: 100px;
            text-align: center;
        }
    }
    
    .drop-here {
        margin-left: auto;
        color: black;
        margin-top: 20px;
        margin-right: auto;
        width: 200px;
        height: 50px;
    }
    
    .save-button {
        background-color: white;
        padding: 5px;
        color: black;
        cursor: pointer;
        position: absolute;
        bottom: 5px;
        right: 5px;
    }
    
    .contents {
        margin: 5px;
        background-color: #ffffff2e;
        font-size: 9pt;
        padding: 5px;
        margin-bottom: 70px;
    }
    
    .pack-content:hover {
        .delete-btn {
            opacity: 1 !important;
        }
    }
    
    .pack-content:active, .pack-content.selected {
        background-color: #ffffff30;
    }
    
    .pack-content.current {
        background-color: white;
        color: black;
    }

    .pack-content.preset {
        .content-thumb {
            border-radius: 16px;
        }
    }

    .pack-content {
        position: relative;
        white-space: nowrap;
        overflow: hidden;
        display: flex;

        .content-thumb {
            width: 16px;
            height: 16px;
            min-width: 16px;
            margin-right: 5px;

            background-size: cover;
        }

        .content-tags {
            display: flex;
            flex: 1;
            .content-tag {
                padding-left: 2px;
                padding-right: 2px;
                background-color: #ffffff6e;
                color: black;
                margin-left: auto;
            }
        }

        .content-name {
            overflow: hidden;
            color: black;
        }

        .delete-btn {
            z-index: 100000;
            opacity: 0.1;
            position: absolute;
            right: 0px;
            top: -8px;
            color: black;
            cursor: pointer;
        }
    }

    .pack-description {
        background-color: #ffffff26;
        color: black;
        outline: none !important;
        font-size: 9pt;
        width: 200px;
        height: 60px;
        border: none;
        padding: 3px;
        position: absolute;
        bottom: 0px;
    }
}


.pad-packs {
    margin-top: 15px;
    width: 100%;
    height: 90%;
    margin-left: 8px;
    position: relative;
    border-radius: 5px;

    .packs-header {
        img {
            height: 20px;
            width: 20px;
            margin-right: 5px;
        }
        width: 100%;
        backdrop-filter: blur(15px);
        font-size: 11pt;
        position: absolute;
        padding: 5px;
        top: 0px;
        left: 0px;
        color: white !important;
        background-color: #80808036 !important;
        border-top-right-radius: 5px;
        border-top-left-radius: 5px;
    }

    .pack-list {
        border-radius: 5px;
        height: 100%;
        overflow-y: scroll;
        background-color: #0000004d;
        padding-top: 40px;
    }

    .pad-pack.selected {
        background-color: white;
        color: black;
    }
    
    .pad-pack {
        font-size: 10pt;
        padding: 5px;
        display: flex;
        cursor: pointer;

        .pack-thumb {
            width: 8px;
            height: 8px;
            margin-right: 8px;
            margin-top: 5px;
        }
    }
}
