.frozen-drag-area {
    display: none;
    width: 100px;
    margin: 10px;
    font-size: 8pt;
    text-align: center;
    background-color: white;
    color: black;

    .text-drag {
        margin: auto;
    }
}

.frozen-containers {
    margin-right: 5px;

    .p-box {
        .p-name {
            padding-left: 5px;
            color: black;
            font-size: 8pt;
            text-align: center;
        }

        .volume-box {
            border-color: #dbdbdb !important;
            color: black;
            background-color: white;
            margin-left: auto;
            height: 20px;
        }
        .volume-indicator {
            height: 18px;
            opacity: 0.8;
        }
        
        display: flex;
    }
}

.frozen-container {
    position: relative;

    .freeze-name:active {
        background-color: white;
    }

    .freeze-name {
        white-space: nowrap;
        overflow: hidden;
        left: 36px;
        top: 0px;
        position: absolute;
        z-index: 4;
        color: black;
        line-height: 20px;
        font-size: 8pt;
        width: 110px;
        height: 18px;

        .freeze-name-edit {
            width: 120px;
            height: 17px;
            border-radius: 0px;
            font-size: 8pt;
        }
    }
}
