.track.selected {
    .showing-waveform {
        box-shadow: 0px 0px 4px 4px #7b7992;
    }
}

.bottom-details {
    .contextual-help {
        position: absolute;
        bottom: 0px;
        right: 0px;
        border-style: solid;
        border-width: 1px;
    }

    .sound-waveform.details-view {
        border: none;
    }

    .waveform-buttons {
        display: flex;
        color: #bdbdbd;
        letter-spacing: 1px;

        .warp-button {
            display: none;
        }

        .onset-threshold-container {
            font-size: 10pt;
            /* float: left; */
            background-color: #101010;
            padding-top: 20px;
            padding-left: 10px;
            width: 150px;
            margin: 10px;

            .slider.slider-horizontal {
                width: 100px;
            }
        }

        .radio {
            margin: 10px;
            padding: 3px;
            background-color: #101010;
            border: solid 8px #121212;
        }

        .radio.reverse {
        }

        .inner.crop-button {
            padding: 8px;
        }

        .add-slice-button.details-button {
            line-height: 28px;
            padding-top: 0px;

            .inner {
                padding: 0px;
            }
        }

        .add-slice-button.disabled.details-button {
            color: white;
        }

        .sample-tempo {
            margin: 10px;
            font-size: 15px;
            padding: 2px;
            line-height: 30px;
            border: 8px solid #121212;
            width: 110px;
            height: 60px;
            background-color: #101010;
        }

        .details-button.disabled {
            opacity: 0.3;
            pointer-events: none;
        }

        .details-button {
            display: flex;
            flex-direction: column;
            background-color: #1d1d4a;

            .inner {
                flex: 1 1;
                padding-top: 5px;
            }

            .inner:active{
                box-shadow: 0px 0px 6px 6px #34343b;;
             }

            .dot {
                display: flex;
                margin-left: auto;
                margin-right: auto;
                height: 8px;
                width: 8px;
                margin-top: 2px;
            }

            flex-direction: column;
            font-size: 11pt;
            line-height: 20px;
            display: flex;
            /* padding-top: 5px; */
            margin: 10px;
            text-align: center;
            word-wrap: wrap;
            height: 60px;
            width: 60px;
            cursor: pointer;
       }
    }
}

.details-heading {
    font-size: 20px;
    float: left;
}

.details-name-container {
    float: left;
    margin-left: 15px;
}

.details-back-button {
    cursor: pointer;
    clear: both;
}

.name-input {
    background-color: black;
    color: white;
    width: 300px;
    height: 35px;
    padding: 5px;
}

.amp-envelopes {
    float: left;
    width: 220px;
}

.pitch-envelopes {
    float: left;
    width: 220px;
}

.recordings-dropdown-container {
    height: 60px;
}

.recordings-dropdown-placeholder {
    font-size: 8pt !important;
}

.details-banner {
    display: flex;
    flex-direction: row;
}

.banner-back-button {
    font-size: 18pt;
    background-color: #101010;
    padding: 10px;
    cursor: pointer;
}

.banner-heading {
    font-size: 26pt;
    margin-left: 20px;
}

/* Sound Tag Editor */
.keyboard-tag-pane {
    margin-top: 10px;
    float:left;
    margin-left: 20px;
}

.sound-parameters {
    margin-top: 10px;
    margin-left: 20px;
    clear: both;
}

.sound-parameters-full {
    float: left;
}

.sound-parameters-heading {
    font-size: 20px;
    margin-bottom: 20px;
}

.sound-parameter-name {
    margin-top: 10px;
    font-size: 14px;
}

.extracting-stems {
    color: red;
}

.onset-threshold-container {
    font-size: 10pt;
    float: left;
    background-color: #03151f;
    padding: 3px;
    width: 200px;
}
