.context-menu {
    backdrop-filter: blur(4px);
    background-color: #0c0c0cbf;
    font-size: 10pt;
    z-index: 2000;
    position: absolute;
    padding: 3px;
    border-radius: 3px;
    min-width: 200px;

    .menu-item:hover {
        cursor: pointer;
        background-color: white;
        color: black;
    }
    
    .menu-item {
        padding: 5px;
    }

    .sub-menu {
        width: 100%;
        display: flex;
        .sub-menu-name {
            flex: 1;
        }
        .arrow-right {
            margin-top: 6px;
        }

        .sub-menu-children {
            max-height: 400px;
            overflow: scroll;
            padding: 3px;
            border-radius: 3px;
            backdrop-filter: blur(4px);
            background-color: #0c0c0cdf;
            color: white;
            position: absolute;
            left: 190px;
            top: 0px;
            display: none;
            width: 200px;
        }
    }

    .sub-menu:hover {
        .sub-menu-children {
            display: block;
        }
    }
}
