
.freeze-history-container {
    max-height: 400px;
    overflow-y: scroll;
}

.freeze-history.hovered {
    background-color: #ff8a8ab5;
}

.freeze-history {
    position: relative;
    min-width: 300px;
    padding-left: 5px;
    padding-top: 5px;
    background-color: #ffffff85;
    margin-left: 30px;
    color: black;
    font-size: 10pt;

    .unfreeze-button:active {
        box-shadow: 0px 0px 2px 2px white;
    }

    .unfreeze-button {
        position: absolute;
        right: 0px;
        top: 0px;
        padding: 3px;
        background-color: #00000026;
        cursor: pointer;
        color: black;
    }
    
    .frozen-track {
        .frozen-info-container {
            display: flex;

            .frozen-track-thumbnail.dot {
                cursor: unset;
            }
            
            .frozen-track-thumbnail:active {
                box-shadow: 0px 0px 2px 2px white;
            }

            .frozen-track-thumbnail {
                width: 15px;
                height: 15px;
                margin-right: 5px;
                cursor: pointer;
                background-size: cover;
            }
        }

        .freeze-history {
            padding-left: 5px;
            margin-left: 10px;
        }
    }
}
