.new-selector.sequences {

    .search-options {
        .view-modes {
            display: flex;
            margin-left: auto;

            .view-mode.selected {
                background-color: #466efe !important;
            }

            .view-mode {
                cursor: pointer;
                padding-left: 10px;
                padding-right: 10px;
                padding-top: 0px;
                padding-bottom: 5px;

                img {
                    width: 15px;
                }
            }
        }
    }

    .sequences-list.grid-mode {
        display: flex;
        flex-wrap: wrap;
        height: 120px;

        .project-option-wrapper, .project-option-wrapper.has-metadata {
            height: 120px;
            .project-metadata {
                display: flex;
            }
        }

        .project-option-wrapper, .project-option-wrapper.has-metadata {
            width: 130px;
            margin: 5px;

            .project-name {
                max-width: 100px;
                overflow: hidden;
                white-space: nowrap;
            }
            .project-date, .local-only, .edits-btn, .project-bpm {
                display: none;
            }

            .project-metadata {
                .samples, .presets {
                    display: none;
                }
                .project-thumbnail {
                    margin: auto;
                    max-width: 130px;
                    max-height: 80px;
                    height: unset;
                }
            }
        }
    }

    .sequences-list {
        .project-option-wrapper {
            background-color: #0f0f116e;
            height: 40px;
            margin-bottom: 2px;
            .project-option {
                background-color: unset;
                .project-date {
                    margin-left: 5px;
                }
                .project-bpm {
                    position: relative;
                    margin-left: auto;

                    .bpm-slider {
                        position: absolute;
                        left: 0px;
                        opacity: 0.1;
                        height: 16px;
                        background-color: #077e92;
                        background-image: linear-gradient(45deg, #7568d6, transparent);
                        z-index: 1;
                        border-top-left-radius: 4px;
                        border-bottom-left-radius: 4px;
                    }
                    .bpm-text {
                        z-index: 2;
                        position: absolute;
                        left: 5px;
                        top: 0px;
                    }
                    width: 60px;
                    margin-right: 5px;
                    font-size: 7pt;
                    height: 16px;
                    line-height: 18px;
                    padding-left: 5px;
                    padding-right: 5px;
                    background-color: #332e2e45;;
                    border-radius: 4px;
                    color: white;
                    margin-top: 3px;
                }
            }
        }
        .project-option-wrapper.has-metadata {
            margin-bottom: 2px;

            .project-metadata {
                display: none;

                .samples {
                    display: flex;
                    flex-wrap: wrap;
                    max-width: 150px;
                    padding: 5px;
                    background-color: #141626;
                    border-top-left-radius: 15px;
                    margin-left: 10px;
                    max-height: 60px;
                    overflow: hidden;
                    .sample-name {
                        margin-right: 5px;
                        white-space: nowrap;
                        font-size: 6pt;
                    }
                }
                .presets {
                    background-color: #141626;
                    margin-left: auto;
                    max-width: 150px;
                    //display: flex;
                    //flex-wrap: wrap;
                    padding: 5px;
                    .preset-name {
                        display: flex;
                        font-size: 6pt;
                        
                        .dot {
                            background-image: radial-gradient(#4fd683, transparent 6px 4px);
                            width: 6px;
                            height: 6px;
                            border-radius: 6px;
                            margin-top: 3px;
                            margin-right: 2px;
                        }
                        padding: 2px;
                    }
                }
                .project-thumbnail {
                    height: 80px;
                }
            }
        }
    }
}
