.v3.synthz {
    .synthz-effects {
        width: 570px;
        margin-left: auto;
        margin-right: auto;
        left: 0px;
        right: 0px;
        position: absolute;
        bottom: 0px;

        display: flex;

        .synthz-effect {
            .effect-name {
                font-size: 12pt;
            }
            box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px, rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px, rgba(240, 46, 170, 0.05) 25px 25px;
            padding: 10px;
            border-radius: 22px;
            text-align: center;
            width: 230px;
            height: 230px;
            margin: 30px;
            background-color: #2a28282b;
            backdrop-filter: blur(16px);
            border: 3px solid #ffffff36;


            .parameter-label-value {
                width: 25px;
            }

            .slider-and-labels {
                margin-top: 15px;
                display: flex;
            }

            .slider-handle {
                background-image: radial-gradient(#f77bff, #ff00a1) !important;
                border: none;
            }

            .slider.slider-horizontal {
                margin-top: 6px;
                margin-left: 8px;
                width: 100px;
            }

            .slider-horizontal > .slider-track > .slider-selection {
                background-image: linear-gradient(to left, #ff6ece, #b1aeff 100%);
            }

            .slider-track-high {
                background-color: #6e5a95;
            }

            .effect-name:hover {
                cursor: pointer;

                .arrow-down {
                    border-top-color: white;
                }
            }

            .effect-name {
                position: relative;
                overflow: visible;

                .effect-dropdown {
                    padding: 5px;
                    right: 0px;
                    position: absolute;
                    z-index: 10000;
                    max-height: 200px;
                    overflow: scroll;
                    background-color: #0f0e0ec7;
                    color: white;
                    width: 150px;
                    text-align: left;
                    border-radius: 8px;

                    .effect-option:hover {
                        background-color: white;
                        color: black;
                    }

                    .effect-option {
                        cursor: pointer;
                        padding: 3px;

                    }
                }

                .arrow-down {
                    position: absolute;
                    right: 0px;
                    top: 0px;
                    width: 0; 
                    height: 0; 
                    border-left: 10px solid transparent;
                    border-right: 10px solid transparent;
                    border-top: 15px solid #6e5a95;
                }
            }

            .parameter-knob {
                margin-left: 45px;
                margin-top: 20px;
                height: 100px;
            }

            svg {

                g.knob-group {
                    transform: scale(0.8) translate(5px, 5px);
                }

                path:first-of-type {
                    stroke-width: 4px;
                    stroke: #6e5a95;
                }

                path {
                    stroke-width: 4px;
                    stroke: #ff00c1;
                }
            }
        }
    }
}
