
.quick-actions.no-tracks {
    height: unset;
    max-height: unset;

    .quick-sample-chooser {
        height: unset;
        max-height: unset;
    }
}

.quick-actions {

    .effect-choices {
        border-radius: 4px;
        background-color: #0f14ae6b;
        width: 240px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-top: 10px;
        max-height: 250px;
        overflow: scroll;
        margin-left: -10px;
        
        .effect-option.selected {
            background-color: white;
            color: black;
        }

        .effect-option {
            /* padding: 1px; */
            width: 100%;
            font-size: 10pt;
            display: flex;
            flex-direction: column;
            cursor: pointer;
            
            .effect-presets {
                background-color: #1b1c25;
                width: 100%;
                .preset-option {
                    white-space: nowrap;
                    margin: 1px;
                    background-color: ffffff0a;
                    padding-left: 18pt;
                    font-size: 9pt;
                }

                .preset-option:active {
                    box-shadow: 0px 0px 1px 1px white;
                }
            }

            .inner {
                display: flex;
                padding: 4px;

                .collapse-toggle {
                    margin-top: 3px;
                    border-color: white;
                    border-style: none;
                    background: none;
                    
                    .arrow-down {
                        border-top-color: white;
                    }
                    
                    .arrow-right {
                        border-left-color: white;
                    }
                }

                .effect-name {
                    margin-left: 5px;
                }
            }
        }

        .effect-option:active {
            box-shadow: 0px 0px 1px 1px white;
        }
    }

    .search-container {
        position: relative;
        margin-top: 5px;

        .clear-btn {
            position: absolute;
            top: 0px;
            right: -25px;
            cursor: pointer;
        }

        .quick-search {
            width: 365px;
            background-color: #0000007d;
            // border: none;
            margin-left: -20px; 
            color: white;
            padding-left: 10px;
        }

        input:focus {
            outline: none;
        }
    }

    .quick-sample-chooser, .quick-instrument-chooser {
        // animation: showing 0.25s;
    }

    .quick-sample-chooser.hide, .quick-instrument-chooser.hide {
        animation: hiding 0.5s;
        overflow: hidden;
        height: 0px;
    }

    @keyframes showing{ 
        from { height : 0px } 
        to { height : 50px } 
    } 

    @keyframes hiding{ 
        from { height : 50px } 
        to { height : 0px } 
    } 

    .quick-sample-chooser {
        margin-top: 20px;
        max-height: 100px;
        overflow-y: scroll;

        .sample-chooser.selected {
            background-color: #1e5ab5c4;
        }
    }
}

.hide.sounds-pane {
    .option-tabs {
        .option-tab {
            font-size: 11pt;
            margin-left: 5px;
            color: #dadadabd;
        }
    }
}


.modal {
    .modal-row {
        margin-top: 20px;
        align-items: flex-start;
        display: flex;
        margin-bottom: 20px;

        .track-quick-view, .sample-explorer {
            margin-top: 0px !important;
            align-items: flex-start;
        }
    }

    padding-bottom: 20px;
    
    
    .quick-actions {
        width: 100%;
    }

    .effect-choices {
        width: 80%;
    }

    .search-container {
        .clear-btn {
            right: 25px;
        }

        .quick-search {
            width: 100%;
        }
    }

    .quick-sample-chooser {
        margin-right: 20px;
    }
}


.quick-sampler-chooser {
    .explorer-header {
        position: absolute;
        top: 0px;
        left: 0px;
    }
}
