
.sub-patch.top-window {
    border-color: transparent;
    filter: unset;
    box-shadow: #333333b5 0px 54px 55px, #3b3b3b1f 0px -12px 30px, #4040401f 0px 4px 6px, #4646462b 0px 12px 13px, #49494917 0px -3px 5px;
    .sub-patch-header {
        background-color: #484848;
        color: white;
    }
}

.v3.full-screen, .nft-mode {
    .sub-patch {
        background-color: #c0c0c061 !important;
    }

    .edges-drawer {
        .pad-effect-edge {
            stroke: #f2f2f2;
        }
    }

}

.sub-patch.custom-mode, .custom-subpatch {
    .z-object {
        box-shadow: unset !important;
    }
    .annotation-text {
        background-color: black !important;
        color: white !important;
    }
    .z-message.bang {
        pointer-events: unset;
    }
    .z-message {
        pointer-events: none;
        font-family: stem;
        color: white !important;
        font-size: 10pt !important;
        text-shadow:1px 1px 10px #fff, 1px 1px 10px #ccc;


        .message-content {
            //line-height: 17px;
        }
    }
    .z-message.number {
        pointer-events: unset;
    }
}


.v3.light-theme {
    .sub-patch {
        .zobject-toolbar {
            img {
                filter: invert(1);
            }
        }
    }
}

.v3.dark-theme {
    .sub-patch {
        background-color: #7e7e7e !important;
        background-image: repeating-linear-gradient(90deg, rgb(201 201 201 / 8%) 0px, rgb(201 201 201 / 9%) 1px, transparent 1px, transparent 96px), repeating-linear-gradient(0deg, rgb(201 201 201 / 9%) 0px, rgb(201 201 201 / 9%) 1px, transparent 1px, transparent 96px), repeating-linear-gradient(0deg, rgb(201 201 201 / 12%) 0px, rgba(201, 201, 201, 0.09) 1px, transparent 1px, transparent 12px), repeating-linear-gradient(90deg, rgb(201 201 201 / 11%) 0px, rgb(201 201 201 / 13%) 1px, transparent 1px, transparent 12px), linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0));


        .edges-drawer {
            .pad-effect-edge {
                stroke: #f2f2f2;
            }
        }
    }
}

.sub-patch {
    filter: brightness(0.7);
    width: 600px;
    height: 600px;
    position: absolute;
    border: 1px solid #656363;
    background-color: #121212f5;
    z-index: 1000000000000000000000;
    overflow: visible;
    display: flex;
    flex-direction: column;
    border-radius: 5px;

    .zobject-toolbar {
        margin-left: 0px;
        background-color: var(--sidebarColor2);
        border-bottom: 1px solid #827f7f;
        width: 100%;
        display: flex;

        .subpatch-menu {
            margin-left: auto;

            .btn.btn-primary {
                background-color:  #ffffff38 !important;
                color: white !important;
                border: none;

            }
            .dropdown-menu {
                font-size: 10pt;
                padding: 5px;
            }

            .small_checkmark {
                margin-bottom: 6px;
                margin-left: 10px;
            }
        }
    }
    

    .z-object.comment {
        box-shadow: unset !important;
        border-color: #00000017;
        .annotation {
            textarea {
                //background-color: #fcff2aab;
                color: black;
            }
        }
    }
    .z-object.z-message.io {
        height: 35px;
        min-width: 20px;
        background-color: white;
        display: flex;
        flex-direction: column;

        input {
            color: black;
            backgorund-color: gray;
        }

        .io-inner.instance-inlet {
            background-color: #3b3b3b;
            
            .io-number {
                color: white;
            }
            .inlet-icon {
                border-top: 8px solid white;
            }

            input {
                color: white;
            }
        }

        .io-inner {
            padding-left: 5px;
            padding-right: 5px;
            display: flex;
            flex-direction: column;
            flex: 1;
        }
                     

        .inlet-icon, .outlet-icon {
            margin-left: auto;
            margin-right: auto;
            margin-top: 2px;
        }

        .io-number {
            white-space: nowrap;
            width: 100%;
            text-align: center;
            color: black;
        }

        .inlet-icon {
            width: 0; 
            height: 0; 
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
            border-top: 8px solid black;
        }
        
        .outlets, .inlets {
            .outlet, .inlet {
                background-color: gray;
            }
        }
        .outlet-icon {
            width: 0; 
            height: 0; 
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
            border-bottom: 8px solid black;
        }
    }

    .sub-patch-content {
        flex: 1;
        //background-image: radial-gradient(circle at center center, #09090959 0%, #abababc4 12%, transparent 12%, transparent 100%), linear-gradient(90deg, #fbfdfe00, #2c2c2c00);
        background-size: 10px 10px;
        display: flex;
        flex-direction: column;

        img:hover {
            opacity: 1;
        }
        img {
            opacity: 0.7;
        }

        .sub-patch-size {
            position: absolute;
            background-color: transparent !important;
            border: 1px solid white;
            background-image: radial-gradient(circle at center center, rgb(84, 176, 218) 0%, rgb(84, 176, 218) 12%,transparent 12%, transparent 100%),linear-gradient(90deg, hsl(207deg 71% 99% / 2%),hsl(0deg 0% 0% / 1%));
            background-size: 37px 37px;
            pointer-events: none;

            .right-bottom-corner {
                pointer-events: all;
                position: absolute;
                width: 20px;
                z-index: 100000000000;
                height: 20px;
                background-color: white;
                right: 0px;
                bottom: 0px;
                cursor: nwse-resize;
            }
        }

        .content-inner {
            flex: 1;
            width: 100%;
            height: 100%;
            position: relative;
            overflow: scroll;
        }

        .pad-effect-control, .pulled-effect {
            z-index: 100000;
        }

        .edges-drawer {
            .pad-effect-edge.message-edge {
                // stroke: white;
            }
        }
 
    }


    .sub-patch-header {
        height: 25px;
        line-height: 15px;
        font-size: 10pt;
        border-top-right-radius: 5px;
        border-top-left-radius: 5px;
        display: flex;
        padding: 5px;
        color: white;
        background-color: #u91313;

        .subpatch-breadcrumb {
            display: flex;

            .parent {
                display: flex;
                .parent-name {
                    margin-left: 5px;
                    background-color: #f9f9f9;
                    color: #0e37ff;
                    padding: 2px;
                    padding-left: 5px;
                    padding-right: 5px;
                    border-radius: 4px;
                    line-height: 10px;
                    cursor: pointer;
                }
                
                .arrow-right {
                    position:relative;
                    margin-left: 5px;
                    width: 0; 
                    height: 0; 
                    border-top: 5px solid transparent;
                    border-bottom: 5px solid transparent;
                    border-left: 10px solid #9a9a9a;
                    margin-left: 15px;

                    &:after {
                        width: 8PX;
                        height: 4px;
                        background-color: #9a9a9a;
                        content: "";
                        position: absolute;
                        left: -20PX;
                        top: -2px;
                    }
                       
                }
            }
        }

        .delete-btn {
            cursor: pointer;
            margin-left: auto;
            margin-right: 8px;
            color: black;
            width: 15px;
            height: 15px;
            text-align: center;
            line-height: 15px;
            font-size: 8pt;
            border-radius: 15px;
            background-color: white;
        }
    }


    .bottom-right-edge, .bottom-left-edge, .top-right-edge, .top-left-edge { 
        z-index: 10000000000;
    }
}

.custom-subpatch {
    width: 100%;
}
.sub-patch.custom-mode, .custom-subpatch {
    .z-pad {
        .output-node {
            display: none;
        }
    }
    .z-object.z-message.io, .inlets, .outlets {
        display: none;
    }

    .z-object.number {
        // background-color: #c8c7c7eb !important;
    }
    
    .z-object {
        border-color: transparent !important;
        background-color: transparent !important;
    }

    .zmacro {
        .more-info-btn {
            display: none;
        }
    }
}

.z-object.custom {
    border-radius: 5px;
}

.subpatch-zobject.z-object {
    background-color: #fff6f6;
    border-color: black;
    border-radius: 3px;

    .outlets, .inlets {
        .oulet:hover, .inlet:hover,  .oulet.connected:hover, .inlet.connected:hover {
        
            background-color: red;
        }
        .outlet, .inlet {
            background-color: gray;
        }
        .outlet.connected, .inlet.connected {
            background-color: black;
        }

    }
    .closed-subpatch.viewing {
        // background-color: #1ffa88;
    }

    .closed-subpatch {
        padding: 1px;
        padding-left: 5px;
        padding-right: 5px;
        background-color:  unset;
        font-size: 10pt;
        display: flex;
        color: #000000;
        
        .subpatch-name {
            margin-top: 3px;
            white-space: nowrap;
            margin-left: 1ch;
            height: 15px;
            line-height: 15px;
            background-color: black;
            color: white;
            padding-left: 1ch;
            padding-right: 1ch;
        }

        .subpatch-name-input {
            white-space: nowrap;
            margin-left: 1ch;
            margin-top: 3px;
            width: 60px !important;
            height: 15px;
            line-height: 15px;
            min-width: unset;
            margin-bottom: 0px;
            background-color: black;
            color: white;
            padding-left: 1ch;
            padding-right: 1ch;
        }
    }
}

.zobject-window {
    position: absolute;
//    background-color: black;
    background-color: #000000c2;
    z-index: 1000000000000000000;
    top: 200px;
    left: 200px;
    border-radius: 5px;
    border: 1px solid #656363;

    .inlets, .outlets {
        display: none;
    }

    .zobject-header {
        height: 25px;
        display: flex;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        width: 100%;
        .delete-btn {
            margin-left: auto;
            margin-right: 10px;
            background-color: white;
            border-radius: 15px;
            font-size: 8pt;
            text-align: center;
            line-height: 15px;
            cursor: pointer;
            width: 15px;
            height: 15px;
            color: black;
            margin-top: 4px;

        }
    }

    .z-object {
        margin: 1px;
        border: none;
        position: unset;
        border-radius: unset;
    }

    .z-object.custom {
        .z-object {
            position: absolute;
        }
    }

}

.z-object {
    .instance-sub-patch {
        font-size: 9pt;
        padding-left: 5px;
        padding-right: 5px;
        padding-top: 3px;
        padding-bottom: 3px;
        white-space: nowrap;

        .implement-name {
            background-color: white;
            color: black;
            padding-left: 3px;
            padding-right: 3px;
            border-radius: 5px;
        }
    }
}


.z-object.custom.custom-subpatch-container {

    // overflow: hidden;
    .inner-custom {
