.varz.base {
    padding: 10px;
    max-height: 500px;
    overflow-y: scroll;
    width: 450px;
}

.varz {
    font-size: 10pt;
    .varz-options.base-level {
        padding-left: 0px;
    }
    .varz-options {
        padding-left: 15px;
    }
    .varz-option:hover {
        .hide {
            opacity: 1;
            pointer-events: unset;
        }
    }

    .varz-level:hover {
        .create-parameters {
            opacity: 1;
            pointer-events: unset;
        }
    }
    .varz-level.plane-mapping  {
        animation: fadeparam 1s 0.1s infinite;
        animation-direction: alternate;
    }
    .varz-level {
        display: flex;

        .create-parameters {
            opacity: 0;
            pointer-events: none;
            cursor: pointer;
            margin-left: 15px;
            background-color: #030303;
            color: #a48585;
            border-radius: 3px;
            padding-left: 4px;
            padding-right: 4px;
        }
    }

    .varz-option {
        display: flex;
        padding: 2px;

        .hide {
            background-color: white;
            cursor: pointer;
            color: black;
            opacity: 0;
            pointer-events: none;
            margin-left: auto;

        }

        .variable-context {
            background-color: white;
            color: black;
            padding-left: 5px;
            padding-right: 5px;
            margin-left: 5px;
            margin-right: 5px;
        }
        .variable-value {
            margin-right: 20px;
        }

        .variable-name {
            display: flex;
            margin-right: 5px;
            width: 100%;

                    img {
                width: 15px;
                height: 15px;
            }

            .v-color {
                width: 15px;
                height: 15px;
            }
        }
    }
    
}
