.pitch-clock {
    margin-left: 20px;
    
    svg {
        background-color: #64636373;
        height: 250px;
        width: 250px;
        transform: rotate(-90deg);

        circle {
            transform: translate(20px, 20px);
            stroke-width: 2;
        }

        .marks {
            transform: translate(20px, 20px);
            stroke-width: 1;
        }

        .mark {
            stroke: #ffffff17;
        }

        .mark.played {
            stroke: white;
            stroke-width: 2px;
            z-index: 200;
        }

        .marks > g:nth-child(1) {
            /* There are 12 marks, so we rotate by 30deg (12 * 30 = 360) */
            transform: translate(100px, 100px) //rotate(30deg); 
        }
        .marks > g:nth-child(2) {
            transform: translate(100px, 100px) rotate(calc(1 * 30deg));
        }
        .marks > g:nth-child(3) {
            transform: translate(100px, 100px) rotate(calc(2 * 30deg));
        }
        .marks > g:nth-child(4) {
            transform: translate(100px, 100px) rotate(calc(3 * 30deg));
        }
        .marks > g:nth-child(5) {
            transform: translate(100px, 100px) rotate(calc(4 * 30deg));
        }
        .marks > g:nth-child(6) {
            transform: translate(100px, 100px) rotate(calc(5 * 30deg));
        }
        .marks > g:nth-child(7) {
            transform: translate(100px, 100px) rotate(calc(6 * 30deg));
        }
        .marks > g:nth-child(8) {
            transform: translate(100px, 100px) rotate(calc(7 * 30deg));
        }
        .marks > g:nth-child(9) {
            transform: translate(100px, 100px) rotate(calc(8 * 30deg));
        }
        .marks > g:nth-child(10) {
            transform: translate(100px, 100px) rotate(calc(9 * 30deg));
        }
        .marks > g:nth-child(11) {
            transform: translate(100px, 100px) rotate(calc(10 * 30deg));
        }
        .marks > g:nth-child(12) {
            transform: translate(100px, 100px) rotate(calc(11 * 30deg));
        }
    }
}
