.v3.synthz {
    background-color: #1e1d21;
    background-image: radial-gradient(#7631fff2, #18171840 64%);
    height: 100%;


    -webkit-user-drag: none; 
    -khtml-user-drag: none; 
    -moz-user-drag: none; 
    -o-user-drag: none; 
    user-drag: none; 

    .guilds-menu {
        z-index: 1;
    }

    .canvas-wrapper {
        position: absolute;
        top: 15px;
        z-index: 0;
        .z-object {
            box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px, rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px, rgba(240, 46, 170, 0.05) 25px 25px !important;
        }

        .inlets, .outlets {
            display: none;
        }
    }

    .oscilloscope-wrapper {
        opacity: 0.9;
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        top: 15px;
        width: 300px;
        left: 0;
        z-index: 1;
        right: 0;
        display: flex;
        pointer-events: none;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px, rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px, rgba(240, 46, 170, 0.05) 25px 25px;
        overflow: hidden;

        .inlets, .outlets {
            display: none;
        }

        .z-object {
            background-color: transparent;
            position: unset;
        }
    }
    img.save-preset-icon  {
        width: 160px;
        height: 200px;
        position: absolute;
        bottom: 0px;
        left: 10px;
        z-index: 4;
    }
    .pack-window {
        left: 0px !important;
        top: 80px !important;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
            left: 0px !important;
    top: 60px !important;
    box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
    border-radius: 15px;
    overflow: hidden;
    background-color: #47415112;

    .floppy-top {
        display: none;
        .pack-name-input {
            background-color: #382e38;
            color: white;
        }
    }

    .delete-btn {
        display: none;
    }
   

    textarea {
        display: none;
    }

    .save-button.disabled  {
        display: none;
        opacity: 0.1;
        pointer-events: none;
    }
    .save-button {
        width: 100%;
        text-align: center;
        font-size: 16pt;
    }

    .contents {
        background-color: black;
        max-height: 400px;
        overflow-y: scroll;
        font-size: 12pt;

        .delete-btn {
            display: block;
            color: white;
        }
        .preset.selected {
            background-color: white;
            .content-name {
                color: black;
            }
        }
        .preset {
            .content-thumb {
                width: 13px;
                background-image: radial-gradient(#16f29273, #f7efef00);
                height: 13px;
                min-width: 13px;
                margin-top: 4px;
            }
            
            .content-name, .content-tags {
                color: white;
            }
        }
    }
    }

     .presets-explorer-container {
        height: 100%;
    }

    .editor-and-sidebars {
        .sidebar {
            background-color: #1c1c1c;
            backdrop-filter: blur(16px);
            width: 250px;
            min-width: 250px;

            .presets-list {
                //background-color: #cbcbcd12;
                background-color: #0000009e;

                .preset-option.selected {
                    .preset-name {
                        color: black !important;
                    }
                }
                .preset-name, .pack-name {
                    color: white !important;

                }
            }
        }
        height: 100%;
        min-height: 100%;
        position: absolute !important;
        right: 0px;
    }

    .instrument.rack-synth, .pulled-instrument {
        border-radius: 22px;
        overflow: hidden;
       // box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

        box-shadow: #333333b5 0px 54px 55px, #3b3b3b1f 0px -12px 30px, #4040401f 0px 4px 6px, #4646462b 0px 12px 13px, #49494917 0px -3px 5px;
        box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px, rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px, rgba(240, 46, 170, 0.05) 25px 25px;
        background-color: #2a28282b;
        backdrop-filter: blur(16px);
        z-index: 2;
        width: 720px;
        position: absolute;
        top: 190px;
        margin-left: auto !Important;
        margin-right: auto !important;
        left: 0;
        right: 0;

        .lfo-component, .envelope-component {
            background-color: #0000003b;
        }
/*
        margin-top: auto;
        margin-bottom: auto;
        margin-left: auto !important;
        margin-right: auto !important;
*/
    }

    .sidebar.collapsed {
        width: 26px;
        min-width: 26px;
        max-width: 30px;
        .collapse-header {
            .arrow-container {
                padding-top: 5px;
            }
        }
    }
    
    .sidebar {
        .collapse-header {
            .arrow-container {
                padding-top: 0px;
            }
        }
    }

    .sidebar {
        .collapse-header {
            display: flex;
            width: 100%;
            padding: 3px;
            .arrow-container {
                margin-left: auto;
                width: 20px;
                height: 20px;
                border-radius: 20px;
                background-color: white;

                .collapsed-arrow {
                    width: 0; 
                    height: 0; 
                    border-left: 10px solid transparent;
                    border-right: 10px solid transparent;
                    border-top: 15px solid black;
                }

                .uncollapsed-arrow {
                    width: 0; 
                    height: 0; 
                    border-top: 10px solid transparent;
                    border-bottom: 10px solid transparent; 
                    
                    border-right:15px solid black; 
                }
            }
        }
    }

    .bpm-component {
        position: absolute;
        font-size: 64px;
        right: 260px;
        bottom: 100px;
        background-color: #2a28282b; 
        padding: 15px;
        height: 120px;
        line-height: 80px;
        width: 120px;
        text-align: center;
        
        border-radius: 25px;
    }
}
