.sends.track {
    border-color:#19202bba;
    background-color: #2222228f;

    .track-header {
        background-color: #19202bba;

        .track-name-view {
            background-color: #040404;
        }
    }

    .sends-container {
        display: flex;
        padding: 5px;

        .send {
            margin-right: 10px;
            
            .send-name {
                padding: 0px;
                font-size: 10pt;
                margin-bottom: 10px;
            }

            .send-effect {
                border-radius: 3px;
                padding: 3px;
                font-size: 8pt;
                margin-bottom: 3px;
                cursor: pointer;

                background-color: #19202b;
                color: white;
            }

            .send-effect.selected {
                box-shadow: 0px 0px 2px 2px #818598;
                background-color: white;
                color: black;
            }

            .send-effects-dropdown {
                font-size: 8pt;
                
                .Dropdown-control {
                    height: 20px;
                    line-height: 5px;
                    border-color: #1e1e22 !important;
                }

                .Dropdown-arrow {
                    top: 7px;
                }
            }
        }
    }

    .new-send-button {
        padding: 5px;
        font-size: 10px;
        margin-top: auto;
        background-color: blue;
        margin-bottom: 5px;
        margin-left: auto;
        cursor: pointer;
    }
}

