.mint-modal {
    
    Input {
        width: 100%;
        background-color: black;
        color: white;
    }
    .editions, .price {
        display: flex;
        .value {
            flex: 1;
        }

        .slider.slider-horizontal {
            width: 300px;
        }
    }
}

.mints-toolbar-view {
    cursor: pointer;
    margin-right: 10px;
    margin-top: 3px;
    position: relative;

    .minted {
        background-color: #0000004d;
        padding: 5px;
    }

    .token-icon.showing {
        filter: blur(5px);
    }

    .minted-menu {
        position: absolute;
        top: 33px;
        width: 250px;
        min-height: 200px;
        max-height: 350px;
        overflow-y: scroll;
        background-color: #2b2b2b;
        left: -25px;
        background-color: #2b2b2b;
        opacity: 0.95;
        padding: 10px;
        font-size: 10pt;

        .project-info {
            display: flex;

            .project-id {
                background-color: black;
                padding-left: 5px;
                padding-right: 5px;
                margin-left: auto;
                margin-right: 5px;
            }
            margin-bottom: 5px;
        }

        .token-option.selected {
            background-color: #eae6e6ab;
            background-image: linear-gradient(45deg, #0f0f0f40, #77b1f4cc);
        }

        .token-option {
            display: flex;
            padding: 4px;

            .token-id {
                margin-left: auto;
            }
            .token-icon {
                margin-top: 2px;
                width: 15px;
                height: 15px;
                background-size: 15px 15px;
            }
        }

        .mint-button:active {
            box-shadow: rgba(240, 46, 170, 0.4) -5px 5px, rgba(240, 46, 170, 0.3) -10px 10px, rgba(240, 46, 170, 0.2) -15px 15px, rgba(240, 46, 170, 0.1) -20px 20px, rgba(240, 46, 170, 0.05) -25px 25px;
        }
        
        .mint-button.minting {
            opacity: 0.3;
            pointer-events: none;
            animation: fadeparam 1s 0.1s infinite;
        }

        .mint-button {
            text-align: center;
            background-color: white;
            color: black;
            padding: 5px;
            font-size: 12pt;
            cursor: pointer;
        }
    }
}

.token-icon {
    margin-top: 3px;
    background: radial-gradient(92% 75.29% at 2.96% 24.36%, #5670e396 0.52%, #2121f7 18.75%, #3b1bef 34.38%, #009bf5d9 49.48%, #194bff 74.48%, #087ae7 100%);
    width: 20px;
    height: 20px;
    background-size: 20px;
    border-radius: 20px;
}

