.z-object.custom.z-track {
    .inner-custom {
        overflow: visible;
    }

    .ztrack-component.playing {
        .trigger-indicator {
            display: block;
        }
    }
    .ztrack-component {
        display: flex;
        width: 100%;
        height: 100%;
        position: relative;

        .output-node {
            position: absolute;
            right: -10px;
            top: 4px;
        }

        .trigger-indicator {
            display: none;
            width: 100%;
            height: 100%;
            background-color: orange;
            opacity: 0.8;
            border-radius: 10px;
            position: absolute;
            bottom: 0px;
            left: 0px;
        }
        
        .documented {
            width: 100%;
            height: 100%;
        }
        .youtube-info {
            width: 100%;
            height: 100%;
            margin-left: 0px;
            padding-left: 0px;
            .sound-thumbnail, .youtube-thumbnail {
                min-width: 100%;
                margin-left: 0px;
                padding-left: 0px;
                line-height: 65px;
                text-align: center;
                width: 100%;
                height: 100%;
            }
        }
    }
}
