            backdrop-filter: unset;
        }
    }

    .track {
        .track-inner {
            background-color: #00000026;
        }
    }
//}

.track.selected {
    .track-inner {
        background-color: #0000009c;
        backdrop-filter: blur(4px);
    }
}

.track.showing-tab {
    backdrop-filter: unset;
    .track-inner {
        backdrop-filter: unset;
    }
}

.track {

    .track-inner::-webkit-scrollbar {
        height: 0px !important;
    }
    .track-inner.showing-cirklon {
        overflow-y: auto;
        // background-color: #272a34 !important;
    }
    .track-inner {
        background-color: #000000c7;
        overflow-x: scroll;
        overflow-y: hidden;


        .effects-view::-webkit-scrollbar {
            height: 8px !important;
        }
    } 
}

.track.selected.showing-tab {
    background-image: linear-gradient(0deg, #5db6e07a, #ffffff08 40%);
    background-color: unset !important;

    .track-inner {
    }
}

.track.selected {
   /*
    background-image: linear-gradient(#00000017, #ffffff82);
   */
    // background-image: linear-gradient(#52568c, #ffffff82 90%);

    .envelope-option, .warp-button {
        opacity: 1;
    }
}

.track {
    display: flex;

    .track-container {
        flex: 1;
        display: flex;
        flex-direction: column;
        width: 90%;
    }
    
    .effect-lock {
        margin-left: auto;
        margin-right: 5px;
        margin-bottom: 3px;
        margin-top: 0px;
    }

    .envelope-option.connected {
        opacity: 1;
    }
    .envelope-option {
        opacity: 0.4;
    }
}

.track.odd {
    //border-color: #0f0f107a;
}

.track {
    border-radius: 0px;
    clear: both;
    border-color: var(--sidebarColor);
    margin-bottom: 2px;
    border-bottom-style: solid;
    border-right-style: solid;
    border-width: 4px;
    margin-right: 10px;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; 

    .youtube-info {
        .volume-box {
            .volume-indicator {
                height: 18px;
            }

            .value {
                position: absolute;
                left: 13px;
            }
        }
    }

    .track-footer-container {
        display: flex;

        .freeze-button {
            opacity: 0.04;
            margin-left: 10px;
            margin-top: 8px;
        }

        .freeze-button:active {
            box-shadow: 0px 0px 2px 2px white;
        }
    }

    .volume-box {
        .volume-indicator {
            background-color: #237bff;
            cursor: col-resize;
            opacity: 1 !important;
        }
    }

    .youtube-container {
        padding-right: 5px;
        padding-top: 3px;
        .youtube-thumbnail {
            margin-left: 0px;
        }
    }

    .track-right-buttons {
        float: none;
        flex: 1;

        .effects-section {
            width: 25px;
            border-radius: 0px;
            padding-left: 5px;
        }
    }
    
    .track-inner {
        display: flex;
   }

    .track-header.odd {
        //background-color: #0f0f108c;
    }

    .track-header {
        display: flex;
        background-color: var(--sidebarColor);
        padding-top: 5px;

        .planar-btn {
            width: 20px;
            height: 20px;
            cursor: pointer;
            background-color: #0a28ff63;
            padding: 2px;
        }
        
        .effects-tab {
            margin-left: 5px;
            border-top-right-radius: 3px;
            padding-left: 4px;
            padding-right: 4px;
            font-size: 9pt;
            background-color: #18181a;
            cursor: pointer;
        }

        .effects-tab.selected {
            border-bottom: 2px dotted white;
            color: black;
            background-color: white;
            box-shadow: 0px 0px 4px 4px #cdcdcd;
        }

        .track-name-view.selected {
            border-bottom: 2px solid white; 
        }

        .sample-tag-input {
            font-size: 8pt;
            margin-right: 5px;
        }

        .add-sample-tag-button {
            margin-right: 5px;
            background-color: black;
            line-height: 14px;
            color: red;
            padding-left: 3px;
            padding-right: 3px;
            cursor: pointer;
        }

        .track-name-view {
            width: 122px;
            overflow: hidden;
            white-space: nowrap;
            border-top-right-radius: 3px;
            padding-left: 4px;
            padding-right: 4px;
            font-size: 9pt;
            background-color: #18181a;
            cursor: pointer;
        }

        .hide-track-button {
            font-size: 16pt;
            line-height: 6px;
            margin-right: 1px;
        }

        .delete-button:active {
            box-shadow: 0px 0px 2px 2px white;
        }

        .delete-button {
            margin-right: 5px;
            margin-left: 1px;
            background-color: #ffffff3d !important;
            color: black;
            border-color: black;
        }

        .track-palettes {
            display: flex;
            margin-left: 5px;
            .palette-option {
                font-size: 9pt;
                padding-left: 6px;
                padding-right: 6px;
                margin-left: 5px;
                cursor: pointer;
                color: black;
            }

            .palette-option.selected {
                box-shadow: 0px 0px 4px 4px #ffa0d4;
            }

        }

        .track-tags {
            margin-left: 11px;
            display: flex;

            .track-tag.highlighted {
                background-color: red;
            }

            .track-tag {
                font-size: 8pt;
                padding-left: 3px;
                padding-right: 3px;
                background-color: #18181a;
                border-radius: 2px;
                margin-right: 2px;
            }
        }
    }
}

.track.flex.compact {
    border-width: 6px;
}

.midi-channel-container, .midi-input-dropdown-container {
    display: flex;
    .midi-input-dropdown {
        .midi-input-control-dropdown {
            padding-left: 5px;
            display: flex;
            flex: 1;
            height: 12px;
            .Dropdown-placeholder {
                line-height: 4px;
            }

            .Dropdown-arrow-wrapper {
                .Dropdown-arrow {
                    top: 8px !important;
                }
            }
            
        
        }
    }
    margin-top: 5px;
}

.midi-input-dropdown {
    width: 60px;
}

.audio-input-dropdown-container {
    height: 20px;

    .Dropdown-control {
        border: 1px solid #484848;
    }
}

.midi-option {
    font-size: 6pt;
    cursor: pointer;
    border-style: solid;
    border-radius: 2px;
    border-width: 1px;
    margin: 5px;
    padding: 3px;
}

.midi-input {
    clear: both;
    margin-bottom: 10px;
}

.midi-output {
    clear: both;
    margin-bottom: 10px;
}

.inputs-heading, .outputs-heading {
    clear: both;
    font-size: 20px;
    color: #f4f6ff;
}

.midi-option.enabled {
    background-color: red;
    color: white;
}

.midi-channel-container {
    display: flex;

    .midi-channel {
        margin-right: 5px;
    }
}

.midi-outputs-container {
    margin-top: 5px;
    width: 400px;
    display: flex;
    flex-wrap: wrap;
}

.react-contextmenu {
    background-color: #1d2122;
    border-style: solid;
    border-width: 1px;
    border-color: #3e3232;

    .react-contextmenu-item:hover {
        background-color: black;
    }

    .react-contextmenu-item {
        cursor: pointer;
        padding: 5px;
        
    }

}

.frozen-columns-container {
    display: flex;
}

.frozen-container.selected {
    border: 1px solid white;
    box-shadow: 0px 0px 1px 1px #fafafa;
    filter: saturate(1.5);
}

.frozen-container {
    margin-bottom: 1px;
    display: flex;
    height: 20px;
    width: 150px;

    .freezing-label {
        font-size: 10pt;
        color: black;
        padding: 5px;
        line-height: 10px;
    }

    .playpause-user label {
        height: 10px;
    }

    .playpause-user {
        float: none;
        margin-top: 0px;
        margin-right: 5px;
        height: 10px !important;
    }

    .playpause .playpause-user label {
        height: 10px !important;
    }

    .playpause label {
        border-width: 0px 0 0px 8px;
    }

    .playpause-user input[type="checkbox"]:checked + label {
        border-style: solid;
        height: 10px !important;
        border-width: 5px 0 5px 8px !important;
    }

    .frozen-progress {
        background-color: #1d1d1d;;
        display: flex;
        flex: 1;

        .progress-part {
            width: 6.25%;
            background-color: #505d69;
        }
    }
}

.waveform-container {
    .warp-button {
        float: left;
        padding: 5px;
        margin-left: 5px;
        font-size: 10pt;
        margin-top: 0px;
        margin-right: 10px;
    }
}

.warp-and-envelopes {
    display: flex;
    margin-left: auto;

    .envelope-options {
        margin-left: 5px;
    }
}

.warp-button {
    background-color: black;
    line-height: 20px;
    text-align: center;
    width: 40px;
    FONT-SIZE: 8PT;
    border-radius: 5px;
    cursor: pointer;
    opacity: 0.4;
    margin-bottom: 1px;
    margin-left: 4px;
}

.warp-button.selected {
    background-color: white;
    color: black;
    opacity: 1;
}


.track-controls {
    display: flex;
    flex-direction: column;
    margin-right: 5px;
    padding-top: 3px;

    .effect-lock {
        margin-top: auto;
    }

    .row {
        flex-wrap: unset;
        margin-left: 0px;
        margin-right: unset;
        display: flex;
        flex: 1;
    }

    .volume-container {
        max-width:150px;
        margin-left: 5px;
    }
    
    .volume-container > .slider {
        height: 60px;
        margin-bottom: 5px;
    }
}
    

.ghost-meter {
    height: 45px;
    border: dashed 1px #2d2d2d;
    margin-left: 18px;
}

.track-meter {
    border: solid 1px #7000ff;
    width: 15px;
    height: 45px;
    margin-left: 5px;
    position: relative;
    
    .track-meter-reference {
        position: absolute;
        bottom: 0px;
        border-top: solid 1px #7000ff82;
        width: 14px;
    }
    
    .track-meter-value.is-red {
        background-color: red;
    }
    
    .track-meter-value {
        position: absolute;
        bottom: 0px;
        background-color: #41f4ad;
        width: 13px;
    }
}

.expand-sample {
    font-size: 8pt;
    padding: 3px;
    background-color: #1f385e;
    color: black;
    width: 50px;
    margin-left: auto;
    padding: 5px;
    cursor: pointer;
}

.sequencer-container.focused {
    .tracks-container {
        max-width: none !important;
    }
}

.steps-container {
    max-width: 4000px;
    padding-top: 3px;
}

.steps-container {
    float: left;
    padding-left: 5px;
}

.lfos-track.track {
    display: flex;
    flex-direction: row;
    align-content: flex-start;
    border: none;

    .lock-button {
        opacity: 1;
        filter: brightness(5.5) grayscale(1);
    }
    .lfo-thumbnail {
        margin-top: 5px;
    }
   
    .lfo-effects-view {
        padding: 5px;
    }

    .youtube-container {
        color: var(--baseColor);
        background-color: #0c0c0c24;
    }
}


.steps-map {
    .play-tick {
        position: absolute;
        height: 25px;
        width: 1px;
        background-color: red;
    }

    background-color: #2b2837;
    height: 25px;
    width: 100%;
    margin-bottom: 10px;

    display: flex;
    position: relative;

    .part {
       flex-grow: 1;
       flex-basis: 0; 
       height: 100%;
       border-right: solid 1px #2a3d65;
       cursor: pointer;
    }

    .part:hover {
        background-color: black;
    }

    .part.current-part {
        background-color: #55545b;
    }

    .on, .off {
        height: 2px;
        position: absolute;
    }

    .on {
    }
}

.resample-name-input {
    background-color: black;
    color: white;
}

.track-loading-spinner {
    position: absolute;
    top: 4px;
    left: 9px;
}


.sends-and-options-container {
    .sends-container {
        display: flex;
        flex-direction: column;

        .effect {
            width: 150px;
            display: flex;

            .effect-name {
                flex: 1;
            }
            
            .parameter-box {
                margin-left: auto;
                margin-right: 10px;
                flex: 1;

                .slider.slider-horizontal {
                    width: 100px !important;
                }
            }
        }
    }

    .track-output-type {
        border: solid 1px black;
        border-radius: 3px;
        font-size: 8pt;
        margin-left: 10px;
        padding: 3px;
        height: 45px;
        background-color: #292c37ad;

        .Dropdown-root.is-open {
            margin-bottom: 50px;
        }

        .Dropdown-option {
            padding: 3px;
        }

        .Dropdown-control {
            border: 1px solid #4e5058;
            width: 80px;
        }
    }
}

.track {
    .add-sample-tag-button {
        display: none;
    }

    .add-steps-button, .remove-steps-button {
        border-radius: 8px;
    }
    .add-steps-button, .remove-steps-button, .lock-button, .playhead-direction-btn {
        opacity: 0.3;
    }
}

.track.selected {

    .track-tags {
        .track-tag:active {
            box-shadow: 0px 0px 1px 1px white;
        }

        .track-tag {
            background-color: #ffffff52;
            color: black;
        }
    }
 
    .freeze-button {
        opacity: 1;
    }
    .track-name-view {
        background-color: #191407;
    }

    .add-steps-button, .remove-steps-button, .lock-button, .effects-section, .playhead-direction-btn {
        opacity: 1;
    }
}

.progress-bar {
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    margin: auto;
    width: 400px;
    height : 50px;
    background-color: #3c3a3a !important;
    //background-color: #0c71d6 !important;
    padding: 5px;
    z-index: 5000000000;
    border-radius: 0px;
    opacity: 0.8;
    
    .inner-progress-bar {
        position: relative;
        .progress {
            border-radius: 0px;
            height: 40px;
            background-color: red;
            //background-image: linear-gradient(to right, #5e5ec5, #21e3f1);
            background: linear-gradient(to right, #333336, #8e4c4c78);
        }
        
        .loading-text {
            z-index: 6000;
            position: absolute;
            left: 20px;
            color: #d7d1d1;
            font-size: 14pt;
            top: 6px;
        }
    }
}

.menu-divider {
    height: 1px;
    border-bottom: 1px solid #4e4e4e;
    width: 100%;
}

.react-contextmenu-item {
    font-size: 10pt;
}


.steps-container.frozen {
    .steps-row {
        pointer-events: none;
        opacity: 0.2 !important;
        display: none;
    }
}

.track-length-button.selected {
    box-shadow: 0px 0px 4px 4px #5a5a5a;
}

.track.selected {
    .track-length-button {
        opacity: 1;
        display: block;
    }
}

.track-length-button {
    display: none;
    opacity: 0;
    width: 35px;
    cursor: pointer;
    height: 20px;
    font-size: 9pt;
    line-height: 20px;
    float: left;
    margin-left: 5px;
    padding-left: 6px;
    margin-top: 5px;
}

.steps-container.track-length-mode {
    .steps-row-container {
        position: relative;
    }
    
    .step, .fill, .step.on {
        opacity: 0.8 !important;
        pointer-events: none;
    }
}

.steps-container {
    padding-right: 20px;

    .steps-row-container {
        min-width: 450px;
        
        .track-length-bar {
            top: 0px;
            position: absolute;
            //opacity: 0.8;
            min-width: 400px;
            height: 18px;
            display: flex;

            .length-option.selected {
                background-color: white;
            }

            .length-option.disabled {
                background: none;
            }

            .length-option {
                cursor: pointer;
                background-color: #9fd3ff;
                color: black;
                width: 23px;
                min-width: 23px;
                height: 13px;
                margin-left: 4px;
                margin-right: 5px;
                margin-bottom: 4px;
                margin-top: 2px;
                border: 1px solid #8f8e8e;
                text-align: center;
                font-size: 6pt;
            }
            
            .right-edge {
                margin-left: auto;
                width: 10px;
                background-color: white !important;
                opacity: 0.5;
                height: 18px;
                cursor: col-resize;
            }
        }
    }
}

.bpm-container {
    .slider-handle {
        background-image: linear-gradient(#d0d0d0, #76b5d4) !important;
    }
}

.sequencer-buttons-container {
    display: flex;

    .hidden-tracks {
        margin-left: 10px;
        align-items: flex-start;
        overflow: scroll;
        
        display: flex;
        flex-wrap: wrap;
        
        .hidden-track {
            cursor: pointer;
            border-radius: 2px;
            border-style: solid;
            border-width: 1px;
            background-color: #00000091;
            color: black;
            padding: 3px;
            font-size: 8pt;
            margin: 1px;
        }
    }
}

.show-piano-roll {
    .tracks-container {
        overflow-y: scroll;
    }
}

.track {
    .youtube-info {
        .sound-thumbnail, .youtube-thumbnail {
            border-radius: 8px;
            color: var(--baseColor);
        }

        .youtube-thumbnail {
            height: 20px !important;
            max-width: 60px !important;
            width: 60px !important;
            object-fit: cover !important;
        }
    }
}
.sound-thumbnail.is-multi {
    padding-left: 0px;
}

.multi-freeze-thumbnails {
    display: flex;
    height: 100%;
    width: 100%;

    .multi-freeze-part {
        height: 100%;
        flex: 1;
    }
}

.envelope-options {
    display: flex;
    margin-left: auto;
    
    .envelope-option {
        margin-right: 5px;
        cursor: pointer;
        padding-right: 4px;
        padding-left: 4px;
        background-color: black;
        color: white;
        font-size: 8pt;
    }
    
    .envelope-option.connected {
        background-color: white;
        color: black;
    }
} 

.track.sends {
    border-style: solid;
    .track-header {
        padding-top: 0px;
    }

    .sends-container {
        .send, .audio-effects {
            width: 110px;
            .send-effect {
                display: flex;

                .effect-name {
                    font-size: 8pt;
                    white-space: unset;
                }

                .parameter-visual {
                    margin-left: 1px;
                    margin-right: 1px;
                    margin-top: auto;
                    width: 2px;
                    max-height: 15px;
                }
            }
        }
    }

    .Dropdown-root.is-open {
        margin-bottom: 50px;
    }
}

.playhead-direction-btn:active, .add-steps-button:active, .track-length-button:active, .remove-steps-button:active {
    box-shadow: 0px 0px 1px 1px white;
}

.playhead-direction-btn {
    margin-left: auto;
    height: 20px;
    width: 25px;
    margin-top: 5px;
    cursor: pointer;
    border-radius: 5px;
    background-color: #212121;

    .playhead-direction-icon {
        width: 18px;
        height: 8px;
        margin-left: 3px;
        margin-top: -8px;
    }

}

.track.selected {
    .playhead-direction-icon {
        // filter: brightness(0);
    }
}


.steps-row {
    min-width: 480px;
}

.track.selected {
    .track-side-container {
        .trap-rest,  .track-side-trap {
            // background-image: linear-gradient(180deg, transparent, #ffffffa1);
            opacity: 1;
        }
    }
}
.track {
    .track-side-container {
        display: flex;
        flex-direction: column;

        .trap-rest {
            flex: 1;
            width: 20px;
        }

        .track-side-trap {
            border-bottom: 28px solid transparent;
            border-left: 20px solid transparent;
            border-right: var(--sidebarColor);
            height: 28px;
            width: 10px;
        }
    }
}

.trigger-meter {
    width: 55px;
    height: 5px;
    background-color: #141414;
    margin-left: 1px;
    margin-top: 5px;

    .trigger-meter-indicator {
        height: 5px;
        background-color: red;
    }
}

.track.hidden {
    margin-bottom: 0px;
    margin-top: 0px;

    .envelope-option {
        opacity: 0.05;
    }

    .envelope-option.connected {
        opacity: 1;
    }

    .track-side-container {
        .track-side-trap {
            border-bottom: 25px solid transparent;
            border-left: 20px solid transparent;
            height: 25px;
        }
    }

    .track-header {
        .delete-button.hide-track-button {
            font-size: 10pt;
        }

        .track-tags {
            .track-tag {
                opacity: 0.35;
            }
        }
    }
}

.sequencer-container, .documentation {
    .coke-machine-glow {
        width: 80%;
        height: 100%;
        background: radial-gradient(92% 75.29% at 2.96% 24.36%, #42424296 0.52%, #eeeeef 18.75%, #3b1bef 34.38%, #05a3ff 49.48%, #194bff 74.48%, #2896ff 100%);
        left: 40%;
        filter: unset;
        top: 40%;
        opacity: .15;
        z-index: -1;
    }

    .blur-logo {
        position: fixed;
        left: 30%;
        top: 25%;
        filter: blur(400);
        opacity: 0.01;
        z-index: 0;
    }
}

.sub-sequence-control {
    display: flex;

    .midi.track {
        flex: 1;

        .send-effect.pulled {
            opacity: 0.4;
            pointer-events: none;
        }
        
        .send-effect.selected.pulled {
            pointer-events: unset;
        }

        .sends-container {
            display: flex;
            flex-direction: column;
            .audio-effects {
                display: flex;
            }

            .effects-channel-strip {
                margin-top: 30px;
            }
            
            .effects-view {
                overflow: unset;
            }
            
            .send-effects-dropdown {
                .Dropdown-control {
                    height: 20px;
                    line-height: 5px;
                    width: 100px;
                    border-color: #1e1e22 !important;
                    font-size: 8pt;
                }

                .Dropdown-arrow {
                    top: 8px !important;
                }

                .Dropdown-option {
                    font-size: 9pt;
                    padding: 2px;
                }
                order: -1;
            }

            .send-effect {
                border-radius: 3px;
                padding: 3px;
                font-size: 8pt;
                margin-bottom: 3px;
                cursor: pointer;
                background-color: #19202b;
                color: white;
            }

            .send-effect.selected {
                background-color: white;
                color: black;
                box-shadow: 0px 0px 2px 2px white;
            }
        }
    }
}


