.pulled-background {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;

    .selection {
        z-index: 10000000;
        position: absolute;
        border: 2px solid red;
        border-radius: 6px;
        background-color: #ff141463;;
