.pulled-pad, .z-object {
    .pads {
        background-color: #222222bf;
        backdrop-filter: unset;
        // box-shadow: 0px 0px 4px 4px #21212154;
        padding: 2px;

        .pad-inner {
            padding: unset;
            margin: 0px;
            .collapse-container {
                left: 100px;
                top: 30px;
            }
            .pad-toolbar {
                padding: 5px;

                .waveform-btn, .pad-length  {
                    background-color: black;

                    .bars {
                        background-color: #1a1a1a;
                    }
                }
                .pad-toolbar-btn {
                    background-color: black;
                    color: white;
                    padding: 3px;
                }
            }
            .pad-toolbar, .pad-slots {
                .pad-length {
                    margin-top: 5px;
                    margin-left: unset;
                }
                width: 150px;
                flex-wrap: wrap;
            }

            .pad-slot {
                .volume-indicator {
                    width: 8px;
                    right: 0px;
                }
                width: 48px;
                height: 48px;

                
            }

            .pad-slot.empty {
                background-color: 0e0e0eb0;
            }

            .track.showing-tab {
                background-image: unset !important;
                .track-inner {
                    background: none !important;
                    
                    .effects-view {
                        background: none !important;
                        background-image: none !important;
                        
                    }
                }
