}
.pulled-sequencer, .pulled-pad {
    position: relative;
}

.pulled-sequencer, .pulled-pad, .sub-patch, .z-object.custom, .zobject-window {

    .bottom-right-edge, .bottom-left-edge, .top-left-edge, .top-right-edge {
        position: absolute;
        width: 20px;
        height: 20px;
    }

    .bottom-left-edge, .top-left-edge {
        left: -2px;
    }

    .bottom-right-edge, .top-left-edge {
        cursor: nwse-resize;
    }

    .top-right-edge, .bottom-left-edge {
        cursor: nesw-resize;
    }

     .top-right-edge {
        right: -2px;
    }

    .bottom-right-edge { 
        right: 0px;
        bottom: -2px;
    }

    .bottom-left-edge {
        bottom: 0px;
    }
    .top-right-edge, .top-left-edge {
        top: 0px;
    }
}

.pulled-sequencer, .pulled-effect, .pulled-pad {
    z-index: 200000;
}

.pulled-sequencer {
    background-color: #f3f3f3c7;
    position: absolute;
    display: flex;
    flex-direction: column;
    // overflow: hidden;


    .pulled-header {
        .delete-button {
            margin-right: 16px !important;
        }
    }

    .output-node {
        animation: anti-trigger-finger .4s !important;
    }

    .output-node {
        right: -8px !important;
    }

    .inner-sequencer {
        flex: 1;
        overflow-y: scroll;
        transform-origin: top left;
        overflow-x: hidden;
    }

    .drop-here {
        padding: 50px;
    }

    .track.hiding {
        min-width: 200px;
    }

    .track.muted {
        opacity: 0.15;
    }

    .track {
        .delete-track-trash {
            height: 14px;
            margin-top: 3px;
            border: 1px solid #191919;
            background-color: black;
            border-radius: 14px;
            width: 14px;
            margin-left: 5px;
            line-height: 9px;
            text-align: center;
            font-size: 8pt;

        }
    }

    .track:hover {
        .delete-track-trash {
            cursor: pointer;
            opacity: 1;
            pointer-events: unset;
        }
    }

    .track.selected {
        .sound-thumbnail {
            color: black;
            box-shadow: 0px 0px 2px 2px #ffffff4d;
        }
        .youtube-thumbnail {
            box-shadow: 0px 0px 2px 2px #ffffff4d;
        }
    }
    .track.not-selected {
        .sound-thumbnail {
            background-color: #151515 !important;
        }
    }

    .track {
        overflow-x: unset;
        overflow-y: unset;
        border: none;
        margin-right: 0px;
        // margin-bottom: 1px;

        .cirklon-editor {
            .track-footer-container {
                display: block;
            }

            color: black;

            .cirklon-steps {
                .cirklon-step {
                    .step-toggle {
                        background-color: #c9c9c9;
                        color: black;
                    }
                }

            }

            .param-btn {
                background-color: #d7d7d7;
            }
        }
        .track-inner {
            overflow-x: unset;
            overflow-y: unset;
            background-color: unset;
        }
        .sound-thumbnail {
            border-right: 5px solid white;
            color: white;
            letter-spacing: unset;
            font-size: 7pt;
        }
        .youtube-thumbnail {
            margin-left: 0px;
            height: 15px;
        }
        .trigger-meter {
            display: none;
        }
        .steps-container {
            .steps-row-bottom {
                min-width: unset;
            }
            .steps-row-container {
                .steps-row {
                    min-width: unset;
                }
                min-width: unset;
            }
        }

        .step-container.selection {
            background-color: white !important;

            .fill {
                background-color: unset !important;
            }
        }

        .steps-row-bottom {
            .current-step-box {
                .right-box {
                    width: 20px;
                }
            }
        }
        .step-container {
            width: 20px;
            .step {
                margin-top: 6px;
                margin-left: 6px;
                width: 5px;
                height: 5px;
                margin-right: 2px;
                border-radius: 5px;
            }
            .step.on {
                margin-top: 2px;
                margin-left: 3px;
                width: 12px;
                height: 12px;
                border-radius: 12px;
            }
        }
        .youtube-info {
            .volume-box {
                display: none;
            }
        }
        .track-controls, .track-header, .track-side-container, .track-mini-control, .track-footer-container {
            display: none;
        }
    }
}

