.pad-effect-control {
    width: 80px;
    height: 80px;
    background-color: #252cffb5;
    background-image: linear-gradient(45deg, #ffffff57, transparent);
    border-radius: 80px;
    position: absolute;
    z-index: 2;
    position: absolute;
    .control-name {
        font-size: 14pt;
        text-align: center;
        color: #0000007a;
    }

    .volume-box {
        border: 1px solid white;
        background-color: #2d2c2f29;
        opacity: 0.8;
        position: relative;

        .volume-indicator {
            opacity: unset;
        }
        .volume-value {
            pointer-events: none;
            color: black;
            position: absolute;
            top: 4px;
            left: 0px;
            width: 50px;
        }
    }

    .context-menu {
    }
}

