.edges-drawer, .grids-container {
    .all-edges {
        animation: fade-in-edges 1s;
    }
    .pad-effect-edge:hover, .pad-effect-edge.selected {
        stroke: red;
    }

    .pad-effect-edge:hover {
        stroke-width: 6px;
    }

    polygon {
        //fill: white;
    }


    .pad-effect-edge.drawing-edge {
        stroke: white !important;
        stroke-dasharray: none;
    }

    .pad-effect-edge.received, .pad-effect-edge.selected.received {
        stroke: #55ffc0;
    }
    
    .pad-effect-edge.message-edge {
        stroke-dasharray: none; 
    }

    .pad-effect-edge {
        z-index: 1000000000;
        stroke-dasharray: 12 2;
        stroke: #8c8c8c;
        stroke-width: 3;
        fill: none !important;
    }
}

.edges-drawer {
    z-index: 2;
    transform-origin: top left;
    position: absolute;
    top: 0px;
    left: 0px;
    //width: 100%;
    //height: 100%;
}

@keyframes fade-in-edges {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}


