
.midi.effect.Jaki.collapsed {
    .effect-header {
        width: unset;
    }
}

.grids-container {
    .midi.effect.Jaki.collapsed {
        display: flex;
        flex-direction: row !important;
        border-radius: 3px;
        border: 1px solid black;
        .effect-header {
            width: unset;
        }
    }
}

.midi.effect.Jaki {
    background-color: #232323de;
    // backdrop-filter: blur(4px);
    border-radius: 15px;
    border: none;
    position:relative;
    .bottom-effect-toolbar {
        display: none;
    }
    
    .effect-header {
        width: 180px;
    }
}

.jaki {
    display: flex;

    .track-screen {
        height: 236px;

        .tidal-art.structure {
            margin-left: auto;
            position: relative;
        }
        
        .scale-rules {

            background-color: black;
            padding: 5px;

            .rule-part.selected {
                box-shadow: 0px 0px 8px 8px white;
            }

            .rule-part {
                margin: 1px !important;
            }
        }

        .puzzle-options, .scale-rules {
            display: flex;
            flex-wrap: wrap;

            .puzzle-option, .rule-part {
                padding: 5px;
                cursor: pointer;
                background-color: white;
                color: black;
                margin: 5px;
            }
        }
    }

    .rules-screen {
        position: relative;

        .rules-chooser {
            position: absolute;
            bottom: 0px;
            left: 90px;
            display: flex;

            .rule {
                width: 20px;
                text-align: center;
                background-color: #2B2C2B;
                margin-left: 5px;
                cursor: pointer;
            }

            .rule.selected {
                background-color: white;
                color: black;
            }
        }
    }
    .color-btn, .color-pickers {
        //display: none;
    }
    
    .color-btn {
        left: 0px;
        top: 0px;
    }

    .color-pickers {
        left: -330px;
        top: 20px;
    }

    .color-pickers {
        height: 200px;
        width: 400px;
    }
    
    .script-tidals {
        position: relative;
        width: unset;
        height: 300px;

        .leaf-details {
            z-index: 100;
            background-color: black;
            padding: 5px;
            position: absolute;
            top: 0px;
            left: 0px;
            width: 150px;
            height: 75px;

            .delete-btn {
                position: absolute;
                top: 2px;
                right: 2px;
                text-align: center;
                background-color: white;
                color: black;
                cursor: pointer;
            }
        }
    }

    .tidal-art.structure {
        z-index: 1;
    }

    .tidal-art {
        z-index: 2;
        left: 0px;
        top: 0px;
        position: absolute;
        margin-left: 50px;
        margin-top: 0px;
        width: unset;
        height: unset;
    }

    .screen {
        margin: 20px;
        margin-top: 21px;
        width: 401px;
        height: 236px;
        background-color: #000000bd;

        padding: 20px;

        .figure-screen {
            position: relative;

            .delete-btn {
                cursor: pointer;
                position: absolute;
                background-color: white;
                right: 5px;
                bottom: 5px;
                color: black;
                text-align: center;
                line-height: 15px;
                height: 15px;
                width: 15px;
                border-radius: 15px;
            }
            .figure-header {
                font-size: 16pt;
                display: flex;

                .Dropdown-root {
                    margin-top: -7px;
                    flex: 1;
                }
            }
            
            .cycle-string {
                font-size: 48px;
            }

            .figure-options {
                display: flex;
                flex-wrap: wwrap;

                .cycle-option.selected {
                    color: black;
                    background-color: white;
                }

                .cycle-option:hover {
                    border-color: white;
                }

                .cycle-option {
                    border: 1px dashed transparent;
                    cursor: pointer;
                    line-height: 32px;
                    font-size: 32pt;
                    margin: 5px;
                    background-color: black;
                    padding: 5px;
                }
            }
        }
    }

}

.z-object {
    .effect-rack {
        margin: 10px !important;
    }

    .rules-chooser {
        display: none;
    }
}
.rules-screen, .z-object {
    .effect-rack {
        margin: 20px;
        position: relative;
        .add-macro-button, .show-macros-button, .new-routing-button, .routing-chooser {
            display: none;
        }
        
        svg.rack-canvas {
            width: 400px;
            height: 230px;
            background-color: #000000bd;


            polygon, #arrowhead {
                fill: transparent;
            }

            rect.node.selected, rect.node.aux.selected {
                fill: red !important;
            }

            rect.node.aux {
                fill: #b9b9b9 !important;
            }

            path.edge {
                stroke: #ffffff38;
                stroke-dasharray: unset;
            }
            rect.node {
                fill: #ffffff !important;
            }
            text {
                fill: black;
            }
        }
        .Dropdown-root {
            position: absolute;
            bottom: 0px;

            .Dropdown-control {
                background-color: #171717 !important;
            }
        }
    }
}


.pulled-effect, .z-object {
    .drum-router {
        .Dropdown-control {
            border-color: black;
            background-color: black !important;
            color: white !important;
            font-size: 7pt;
        }

        label {
            margin: 0px !important;
            input {
                margin: 0px !important;
            }
        }
    }
}

.drum-router {
    display: flex;

    .track-col.labeler {
        .track-btn {
            opacity: 0;
        }
    }

    .track-col {
        margin: 5px;

        .empty-thumb {
            width: 30px;
            height: 15px;
            margin-bottom: 15px;
        }

        .youtube-info {
            float: none;
            margin-left: unset;
            margin-bottom: 15px;

            .youtube-thumbnail {
                width: 30px;
                height: 15px;
                border: unset;
                margin-left: unset;
            }

            .sound-thumbnail {
                color: black;
                width: 30px;
                font-size: 6pt;
            }
        }

        .track-btn.selected {
            background-color: white;
            color: black;
        }

        .synth-icon{
            display: none;
        }
        .track-btn {
            display: none;
            cursor: pointer;
            width: 30px;
            height: 30px;
            background-color: black;
            line-height: 30px;
            text-align: center;
            margin-bottom: 15px;
        }

        .track-pattern {
            text-align: center;
            margin-bottom: 15px;
            opacity: 0;
            width: 30px;
            height: 30px;
        }

        .drum-circle {
            margin-bottom: 15px;
            width: 30px;
            height: 30px;
            background-color: black;
            border-radius: 20px;
            position: relative;

            display: flex;
            position: relative;
            .hand-indicator {
                position: absolute;
                bottom: 10px;
                background-color: #befc01;
                width: 5px;
                height: 5px;
                border-radius: 5px;
                z-index: 8;
                pointer-events: none;
            }

            .hand-indicator.left {
                left: 5px;
            }
            .hand-indicator.right {
                right: 5px;
            }
 
            .left-circle {
                width: 15px;
                opacity: 0;
                height: 30px;
                z-index: 2;
                cursor: pointer;
                background-color: #403f3f;
                border-radius: 30px 0px 0px 30px;
            }

            .right-circle.toggled, .left-circle.toggled {
                opacity: 1;
            }

            .right-circle {
                opacity: 0;
                width: 15px;
                height: 30px;
                background-color: #403f3f;
                z-index: 2;
                cursor: pointer;
                border-radius: 0px 30px 30px 0px;
            }

            .drum-divider {
                z-index: 4;
                position: absolute;
                left: 14px;
                height: 30px;
                width: 2px;
                background-color: #ffffff47;
                
            }
        }

        .volume-box {
            background-color: black;
            width: 40px;
            height: 20px;

            .volume-value {
                pointer-events: none;
                position: absolute;
            }

            .volume-indicator {
                height: 18px;
                background-color: #272525;
                opacity: 1;
            }
        }
        
        .track-shift.track-rules {
            width: 40px;

            .Dropdown-arrow {
                top: 6px;
                right: 3px;
            }

            .Dropdown-control {
                width: 40px;
                height: 20px;
                padding: 5px;
                text-align: left;
                line-height: 10px;
            }
        }

        .track-shift {
            width: 50px;
            text-align: center;
            font-size: 8pt;
            height: 20px;
            background-color: black;
        }
    }
}


.figures {
    width: 400px;
    display: flex;
    margin-bottom: 40px;
    margin-left: 15px;
    flex-wrap: wrap;
    margin-top: -15px;
    
    .figure.selected {
        .figure-button {
            box-shadow: 0px 0px 6px 6px #ffffff5e;
            background-color: white;
            color: black;
        }
    }
    
    .figure {
        cursor: pointer;
        width: 90px;
        display: flex;
        flex-direction: column;
        white-space: nowrap;
        margin-top:  20px;
        
        .figure-button {
            z-index: 100;
            margin-left: auto;
            margin-right: auto;
            width: 40px;
            height: 40px;
            text-align: center;
            font-size: 18pt;
            background-color: black;
        }
        
        .cycle-string {
            white-space: nowrap;
            line-height: 50px;
            font-size: 48pt;
            text-align: center;
            color: white;
            background-color: #00000038;
        }
    }
}


.z-object.custom {
    .core-jaki {
        .screen {
            z-index: 900000;
            padding: 5px !important;
            top: 20px;
            left: -20px;
            width: 220px !important;
        }

        .fig {
            cursor: pointer;
        }
        
        .selected.fig {
            background-color: white;
            color: black;
        }
        
    }
    .jaki.jfigures {
        .screen {
            top: 40px;
        }
    }
    .jaki.jfigures, .core-jaki {
        position: relative;
        
        .screen {
            position: absolute;
            margin: 0px;
            background-color: white !important;
            color: black;
            z-index: 100;
            padding: 10px;

            .cycle-option {
                color: white; 
            }

            .cycle-option.selected {
                color: black;
                background-color: #ededed;
            }

            .Dropdown-control {
                font-size: 10pt;
                padding: 5px;
                border-color: black;
                background-color: white !important;
                color: black !important;
                width: 100px;
                margin-top: 5px;
                margin-left: 15px;
            }

            .Dropdown-menu {
                background-color: white !important;
                padding: 5px;
                font-size: 10pt;

                .Dropdown-option {
                    z-index: 1000000000000;
                    color: white !important;
                    padding: 5px;
                }
            }
        }
    }
}
