
.group-edges {
    position: absolute;
    pointer-events: none;
    z-index: 1000000000000;
    width: 100%;
    height: 100%;

    polygon {
        fill: #000000c7;
    }

    .markov-edge {
        stroke-dasharray: 18, 2; 
        stroke: #c4faffdb;
    }
}

.step {
    .group-indicator {
        position: absolute;
        left: 0px;
        width: 4px;
        height: 4px;
        border-radius: 4px;
        background-color: white;
    }
}

.steps-container {
    position: relative;
    .track-step-groups {
        position: absolute;
        left: 0px;
        top: 0px;
        z-index: 100;

        .steps-group.selected {
            box-shadow: 0px 0px 5px 5px #ffffffc7;
        }

        .steps-group.hide {
            display: none;
        }

        .steps-group {
            border: 1px solid #ffffff85;
            cursor: pointer;
            position: absolute;
            font-size: 7pt;
            white-space: nowrap;
            display: flex;
            color: black;

            .transformations {
                position: absolute;
                bottom: -8px;
                left: 5px;
                display: flex;

                .transformation.transforming {
                    box-shadow: 0px 0px 8px 8px #ffd9d9;
	            animation: transforming linear 0.5s infinite;
                }

                .transformation-wrapper {
                    position: relative;
                    .transformation-tooltip {
                        margin-top: 0px;
                        z-index: 10000;
                        padding: 3px;
                        position: absolute;
                        top: 5px;
                        left: 30px;
                        background: #ffffff99;
                        color: black;
                        display: flex;
                        font-size: 8pt;
                        
                        .arrow-right {
                            margin-top: 6px;
                            border-left-color: black;
                        }

                        .when-type, .when-value, .then-type, .then-value, .arrow-right {
                            margin-right: 5px;
                        }
                    }
                }

                .transformation {
                    position: relative;
                    width: 8px;
                    height: 8px;
                    margin: 2px;
                    margin-right: 7px;
                    background-color: white;
                    cursor: pointer;
                    border-radius: 8px;
                }

                .transformation:hover {
                    background-color: orange;
                }
            }

            .group-name {
                position: absolute;
                right: 5px;
                top: 2px;
                font-size: 9pt;
                height: 14px;
                line-height: 11px;
                background-color: #ffffff61;
                padding: 2px;
            }
        }
    }
}

@keyframes transforming {
	0% { opacity: 1; } 
	50% { opacity: .4; } 
	100% { opacity: 1; }
}
