.multi-filter {
    .multi-inner {
        display: flex;
    }
    min-width: 460px;


    .explorer-top {
        display: none !important;
    }

    .presets-list {
        padding-top: unset !important;
        max-height: 280px;
        overflow: scroll;
    }

    .Dropdown-control {
        background-color: black !important;
        .Dropdown-arrow {
            border-color: white transparent transparent !important;
        }
    }

    .fx-button {
        margin-top: auto;
    }
    .formant-parameters {
        .inner {
            .Dropdown-control {
                margin-left: -15px;
                margin-right: 15px;
            }
            .parameter-box {
                width: 130px !important;
            }
        }

    }
    .formants-editor {
        width: 310px;
        min-height: 300px;

        .dragging-formant-info {
            text-align: center;
            font-size: 16pt;
        }
    }

    .expand-formants-icon {
        display: none;
    }

    .formant-effect {
        min-width: 120px;

        .audio-effect-container {
            position: unset !important;
            left: unset !important;
            top: unset !important;
            right: unset !important;
            background-color: unset !important;
            padding: unset !important;
        }

        .Dropdown-control {
            font-size: 8pt;
            padding: 3px;
            border-color: black;

            .Dropdown-arrow {
                top: 8px;
            }
        }
        .sub-header {
            display: none;
        }
    }
}
