.settings {
    display: flex;

    .clock-type-container {
        .clock-user {
            background-color: #7de86e;
            padding-left: 10px;
            padding-right: 10px;
            background-color: #ffffff;
            border-radius: 10px;
            color: #000000;
        }

        .ask-buttons {
            display: flex;
            margin-left: auto;

            .ok-btn, .cancel-btn {
                margin: 5px;
                padding: 5px;
                border-radius: 5px;
            }
            .ok-btn {
                background-color: #51fa51;
                background-image: linear-gradient(45deg, #ed9dd0, transparent);
                color: black;
                cursor: pointer;
            }

            .cancel-btn {
                padding: 5px;
                background-color: red;
                color: black;
                cursor: pointer;
            }
        }
    }

    .settings-sidebar {
        .settings-option {
            padding: 5px;
            cursor: pointer;
            background-color: #00000040;
            margin: 1px;
        }

        .settings-option.selected {
            background-color: white;
            color: black;
        }
    }

    .settings-content {
        flex: 1;
        padding: 5px;
    }

    .appearance {

        .color-btn {
            width: 20px;
            height: 20px;
            border-radius: 20px;
            
        }
        .wallpaper-toolbar {
            flex-wrap: wrap;
            display: flex;
            .animate-btn.wall {
                width: 90px;
                margin-left: 10px;
            }

            .animate-btn {
                border: 1px solid var(--dividerColor);
                background-color: black;
                color: white;
                cursor: pointer;
                width: 60px;
                padding: 5px;
            }
            
            .animate-btn.selected {
                background-color: #fff7f752;
                border-color: white;
            }
        }

        .gallery {
            display: flex;
            flex-wrap: wrap;
            max-height: 160px;
            overflow-y: scroll;

            .wallpaper-choice {
                cursor: pointer;
                height: 80px;
                width: 100px;
                margin: 3px;
            }
        }
        .image-handler {
            .inner {
                position: relative;
                img {
                    width: 180px;
                    height: 120px;
                }
                .drag-placeholder {
                    position: absolute;
                    z-index: 2;
                    left: 15%;
                    top: 30%;
                }
            }
            .drop-here {
                display: flex;
                width: 180px;
                height: 120px;
                background-color: #2b2a2a;
                color: white;
                margin-top: 10px; 

                span {
                    margin: auto;
                }
            }
        }
    }

    .audio {
        .sample-rate-chooser, .preferences-audio-chooser {
            display: flex;
            .dropdown-title {
                flex: 1;
            }
            .Dropdown-control {
                margin-left: auto;
            }
        }

        .audio-outputs-option {
            cursor: pointer;
            color: #000000;
            background-color: #d7d7d7;
            padding: 5px;
        }
    }

    .midi-settings {
        max-height: 600px;
        overflow: scroll;
        
        .midi-input {
            display: flex;
            margin-bottom: 0px;

            .midi-name {
                flex: 1;
            }

            .midi-options {
                display: flex;

                .midi-option {
                    padding: 5px;
                    background-color: black;
                    color: white;
                }

                .midi-option.enabled {
                    background-color: white;
                    color: black;
                    box-shadow: 0px 0px 2px 2px white;
                }
            }
        }
    }
}
