.track-mini-control {
    margin-left: auto;
    display: flex;
    font-size: 9pt;

    .track-effects {
        display: flex;
        overflow-x: unset !important;
        overflow-y: unset !important;
        max-height: unset;
        height: unset;

        .Dropdown-root.is-open {
            margin-bottom: 200px;
        }
        
    }
    .filter-section {
        background-color: #ffffff7d;
    }
    
    .parameter-lfo-toggle, .parameter-envelope-toggle {
        margin-right: 2px;
    }

    .operators-mixer {
        margin: 0px !important;

        svg {
            text {
                fill: black;
            }
        }
    }
    
    .graphical-envelope {
        .env-toolbar {
            .env-name {
                font-size: 9pt;
            }
            
            .env-param.sustain {
                width: 30px;
            }
            .env-param {
                color: white;
                width: 40px;

                .param-val {
                    color: white;
                }
            }
        }
    }
    .mini-options {
        background-color: #00000045;
        .option {
            padding-left: 10px;
            padding-right: 10px;
            font-size: 9pt;
            cursor: pointer;
        }
        .option.selected {
            color: black;
            background-color: white;
        }
    }
    svg {
        rect {
        }

        text {
            fill: white;
        }

        line.fm-guide-line {
            stroke: #ffffff38;
            
        }
    }
    
    .algo-container {
        display: none;
    }
}
