.midi-pads-container {
    display: flex;
    .midi-pads-overview {
        overflow: scroll;
        max-height: 105px;
        
        .octave-pads.selected {
            box-shadow: 0px 0px 2px 2px #334aba;
        }
        
        .octave-pads {
            margin: 3px;
            background-color: #0e185d;

            .pads-row {
                display: flex;
                .pad {
                    margin: 2px;
                    background-color: #1ba0ff21;
                    width: 35px;
                    height: 15px;
                    font-size: 6pt;
                    border-width: 1px;
                    line-height: 10px;

                }
            }
        }
    }
}

.pads.mini {
    padding: 0px;
    margin-right: 3px;
    .pad {
        width: 40px;
        color: #4abbfd;
        border: none !important;
        line-height: 20px;
        font-size: 6pt;
        height: 20px;
        background-color: #090d2c;
        margin: 1px;
    }

    .midi-pads-overview {

    }
}

.pads-row {
    clear: both;
}

