.every-visualizer {
    max-width: 170px;
    display: flex;
    flex-wrap: wrap;

    .step {
        margin-right: 5px;
        background-color: #b0b0b0;
        width: 12px;
        height: 12px;
        border-radius: 12px;
        border: none;
    }

    .step.offset {
        background-color: #109afc;
    }

    .step.current {
        border: 1px solid red;
    }

    .step.current.offset {
        box-shadow: 0px 0px 2px 2px #ff95d0;
    }
}
