.roll-buttons {
    display: flex;
    
    .explore-roll-button {
        border: 1px solid #151517;
        background-color: #27272c;
        padding: 5px;
        margin-left: 5px;
        cursor: pointer;
        width: 40px;
        text-align: center;
    }
    
    .rolling {
        background-color: #3a3a67;
        animation: flash-pattern 1s;
    }
}

@keyframes flash-pattern {
    from {
        box-shadow: 1px 8px 24px white;
    }
}
 
