
.effect {
    .effect-presets {
        display: flex;
        flex-direction: column;
        width: 350px;

        .presets-container {
            max-height: 200px;
            width: 100%;
            overflow-y: scroll;

            .effect-preset {
                cursor: pointer;
                background-color: #111821;
                width: 100%;
                border-bottom: 1px solid #cacaca6b;
                display: flex;
                color: white;

                .preset-title {
                    font-size: 9pt;
                    padding: 5px;
                }

                .preset-author {
                    line-height: 20px;
                    margin-left: auto;
                    font-size: 7pt;
                    background-color: #3a3a3a;
                    color: white;
                    padding: 5px;
                }
            }
        }
        
        .save-container {
            display: flex;
            padding: 0px;
            
            .save-input, .save-input:focus {
                outline: none !important;
                width: 100%;
                background-color: #131313 !important;
                color: white;
                border: none;
                outline: none !important;
            }
            
            .save-button.disabled {
                opacity: 0.1;
                pointer-events: none;
            }

            .save-button {
                cursor: pointer;
                background-color: red;
                color: white;
                padding: 5px;
            }
        }
    }

    .presets-toggle-button, .back-button {
        background-color: #0d2046;
        border: none;
        color: white;
        text-align: center;
        font-size: 10pt;
        padding: 2px;
        cursor: pointer;
    }
}

.effect.midi {
    .presets-toggle-button, .back-button {
        background-color: #28282d;
    }
}
