.sdf-builder {
    display: flex;
    flex-direction: column;
    padding: 5px;

    .Dropdown-control {
        padding: 2px;
        font-size: 10pt;
        border: 1px solid black;

        .Dropdown-arrow {
            top: 8px;
        }

    }
    .Dropdown-option {
        font-size: 10pt;
        padding: 2px;
    }

    .sdf-field {
        display: flex;
        font-size: 10pt;

        .field-name {
            width: 80px;
            margin-right: 5px;
        }

        .z-number {
            background-color: black;
            .arrow-right {
                margin-top: 6px;
                margin-right: 5px;
            }
            display: flex;
            width: 60px;
        }
    }
}
