.jaki-grid {
    display: flex;
    flex-direction: column;
    margin: 15px;
    
    .jaki-row.odd {
        .cell.odd {
            background-color: #6969692e;
        }
        .cell.on, .cell.odd.on {
            background-color: white;
        }
    }

    .jaki-row {
        display: flex;


        .cell:hover {
            border-color: red;
        }

        .cell.selection {
            background-color: red !important;
        }

        .cell.on.dash, .cell.on.dot, .cell.odd.on.dash, .cell.odd.on.dot {
            .dash-inner {
                background-color: black;
            }
        }

        .cell.on, .cell.odd.on {
            background-color: white;
        }

        .cell {
            border: 1px solid transparent;
            cursor: pointer;
            width: 20px;
            height: 20px;
        }
    }
}

.jaki-grid {
    .grid-toolbar {
        display: flex;
        width: 180px;
        z-index: 1000000000000;
        
        .option {
            position: relative;
            .option-tooltip {
                position: absolute;
                top: 30px;
                background-color: black;
                padding: 3px;
                font-size: 10pt;
                white-space: nowrap;
                left: -5px;
                z-index: 10000;

            }
        }
        .more-options.show {
            background-color: #ffffffb8;
            color: black;
        }

        .more-options {
            width: 25px;
            height: 25px;
            margin-right: 5px;
            font-size: 24px;
            color: white;
            background-color: black;
            position: relative;
            text-align: center;
            line-height: 12px;
            cursor: pointer;


            .options-overlay {
                display: flex;
                position: absolute;
                left: -100px;
                top: 25px;
                background-color: #ffffffb8;
                padding: 5px;
            }
        }
    }

    .option.selected {
        background-color: white;
    }
    .option.selected, .cell {
        .symbol {
            background-color: black;
        }
    }
    .cell.dot, .cell.dash {
        .symbol {
            background-color: white;
        }
    }
    .cell.dash {
        position: relative;

        .dash-inner {
            position: absolute;
            background-color: white;
            top: -1px;
            left: -1px;
            width: 40px;
            height: 20px;


            .dash {
                margin-top: 7px;
                width: 30px;
            }
        }
    }

    .option.selected {
        .rest::before,
        .rest::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 2px; /* cross thickness */
            background-color: black !important;
        }
    }
    
    .option {
        cursor: pointer;
        width: 25px;
        height: 25px;
        padding: 5px;
        margin-right: 5px;
        font-size: 15px;
        color: white;
        background-color: black;

        .symbol {
            background-color: white;

        }
        .rest::before,
        .rest::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 2px; /* cross thickness */
            background-color: white !important;
        }
    }

    .cell, .option.selected {
        .symbol.piano, .symbol.pianissimo, .symbol.regular, .symbol.double {
            background-color: transparent;
            border-bottom-color: black;
        }

        .symbol.double {
            border-color: black;
        }

        .symbol.flam:before {
            position: absolute;
            content: '';
            width: 2px;
            left: -3px;
            bottom: 0px;
            height: 10px; /* cross thickness */
            background-color: black;
        }
    }

    .cell.selected {
        .symbol {
            background-color: red;
        }
    }
    
    .cell {
        .symbol {
            background-color: black;
        }

    }

    .cell:active {
        .dash-inner {
            pointer-events: none;

            .dash {
                pointer-events: none;
            }
        }
    }
    .option, .cell {
        display: flex;
        .symbol {
            margin: auto;
        }
        .dot {
            width: 10px;
            height: 10px;
        }
        
        .dash {
            width: 15px;
            height: 8px;
            border-radius: 15px;
        }
        
        .accent {
            width: 4px;
            height: 10px;
            transform: rotate(25deg);
        }

        .pianissimo {
            width: 0px;
            height: 0px;
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            border-bottom: 4px solid white;
            background-color: transparent;
        }

        .piano {
            width: 0px;
            height: 0px;
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            border-bottom: 8px solid white;
            background-color: transparent;
        }

        .regular {
            width: 0px;
            height: 0px;
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            border-bottom: 15px solid white;
            background-color: transparent;
        }

        .staccato {
            width: 7px;
            height: 7px;
        }

        .legato {
            width: 18px;
            height: 5px;
        }

        .rest {
            margin-top: -2px;
             position: relative;
             height: 20px; /* this can be anything */
             width: 20px;  /* ...but maintain 1:1 aspect ratio */
             display: flex;
             flex-direction: column;
             justify-content: center;
        }

        .double {
            border-left: 2px dashed white;
            border-right: 2px dashed white;
            width: 9px;
            height: 15px;
            background-color: transparent;
        }


        .symbol.flam {
            height: 15px;
            width: 2px;
            position: relative;
        }

        .flam:before {
            position: absolute;
            content: '';
            width: 2px;
            left: -3px;
            bottom: 0px;
            height: 10px; /* cross thickness */
            background-color: white;
        }

        .symbol.rest {
            background-color: transparent;
        }
        .rest::before,
        .rest::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 2px; /* cross thickness */
            background-color: black;
        }
        
        .rest::before {
            transform: rotate(45deg);
        }
        
        .rest::after {
            transform: rotate(-45deg);
        }
    }
}

.jaki-grid {
    position: relative;
    svg {
        z-index: 10000000;
        pointer-events: none;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 25px;
        left: 0px;

        rect {
            fill: #1dffc0;
            opacity: 0.6;
        }
    }
}

