.v3.presentation-mode {
    .editor-bottom {
        display: none;
    }
}

.editor-bottom.piano-roll-mode {
    .collapse-container {
        position: absolute;
        top: 3px;
        left: 3px;
    }

    .piano-roll-dropdown {
        opacity: 0;
        pointer-events: none;
    }
}
.editor-bottom.piano-roll-mode {
    background-color: var(--editor-bottom);
}

.editor-bottom.show.effects-mode {
    min-height: 280px;
    max-height: unset;
    //animation-duration: 0.2s;
    //animation-name: grow-bottom;


    .effects-view {
        overflow: unset;
        overflow-x: auto;
    }
}
@keyframes grow-bottom{
    from{
        max-height: 40px;
        min-height: 40px;
    }

    to{
        max-height: 280px;
        min-height: 280px;
    }
}

@keyframes collapse-color {
    from{
        background-color: #3b4343; 
    }

    to{
        background-color: white;
    }
}




.v3 {
    .editor {
        .effect {
            background-color: #292c37ad;
            .delete-button {
                cursor: pointer;
                color: #ffffff;
                border-color: #666c7d;
                background-color: #3c4343;
                border-width: 2px;
            }
        }
    }
}

.editor-bottom.full-width {
    left: 0px;
    right: 0px;
}
.editor-bottom {
    position: absolute;
    bottom: 0px;
    left: 240px;
    right: 240px;
    z-index: 1000000000;
    background-color: var(--editor-bottom);
    min-height: 40px;
    max-height: 40px;
    //width: 100%;
    border-top: 1px solid var(--dividerColor);
    padding: 5px;
    display: flex;

    .editor-bottom-resizer {
        width: 100%;
        height: 10px;
        position: absolute;
        top: 0px;
        left: 0px;
        cursor: row-resize;
    }

    .bottom-inner {
        display: flex;
    }

    .collapse-container:hover {
        animation-duration: 0.7s;
        animation-name: collapse-color;
        background-color: white;
    }

    .effects-view {
        flex: 1;
        background-image: unset;
        background-color: #35353521;
        border-radius: 5px;

        .effect {
            background-color: #292c37ad;
            /*
            .parameter-box, .wider-sliderparameter-box {
