                        .slider.slider-horizontal {
                            margin-left: auto;
                            width: 80px !important;
                        }
                    }
                }
            }
            */
        }
    }
}


.plugins-pane, .editor-bottom {
    .collapse-container {
        width: 26px;
        height: 26px;
        border-radius: 30px;
        background-color: var(--sidebar-btn);
        cursor: pointer;
        margin-right: 5px;

        .arrow-right {
            width: 0; 
            height: 0; 
            margin-left: 6px;
            margin-top: 3px;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            border-left: 18px solid var(--antiColor);
        }

        .arrow-up {
            width: 0; 
            height: 0; 
            margin-left: 3px;
            margin-top: 3px;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-bottom: 18px solid var(--antiColor);
        }
    }
}
