.bottom-toolbar {
    display: flex;
    flex-direction: row;
}

.bottom-toolbar-left {
    flex: 1;
}

.contextual-help.text {
    color: white;
    animation-name: contextual;
    animation-duration: 0.85s;

    .doc-line.rest {
        color: #7d7d86;
    }
    overflow: hidden;
}


@keyframes contextual {
    from {color: red; opacity: 0.7;}
    to {color: white; opacity: 1}
}

.contextual-help {
    letter-spacing: 1px;
    margin-top: auto;
    background-color: black;
    opacity: 0.9;
    font-size: 11pt;
    color: #f5f5f5;
    width: 360px;
    height: 100px;
    border-style: solid;
    border-width: 1px;
    border-color: #05265d;
    padding: 5px;
}

.contextual-help.empty {
    border-color: #05265d;
}
