.jaki-grid.j-types {
    display: flex;
    flex-direction: row;
    .j-type {
        margin: 1px;
        .j-slider {
            width: 20px;
            height: 80px;
            background-color: black;
            position: relative;
            margin-bottom: 2px;

            .j-slider-inner {
                position: absolute;
                width: 20px;
                background-color: white;
                bottom: 0px;
                left: 0px;
            }
        }
        .cell {
            background-color: white;
            width: 20px;
            height: 20px;
        }
    }
}
