.external-instrument.instrument {
    color: black;
    background-color: #b5ffe1;
    padding: 0px !important;

    .inner-instrument {
        padding-left: 10px;
        padding-right: 10px;
    }
    
    .external-instrument-header {
        width: 100%;
        padding: 3px;
        padding-left: 10px;
        background-color: #7effb3;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
    }

    .slider.slider-horizontal .slider-selection, .slider.slider-horizontal .slider-track-low, .slider.slider-horizontal .slider-track-high {
        background-color: #71d4ae;
    }

    .Dropdown-control {
        background-color: white !important;
        color: black !important;
    }

    min-width: 250px !important;
    padding: 10px;

    .midi-outputs-container {
        width: unset;
    }

    .midi-option {
        margin-left: 0px;
        background-color: #3c3c3c;
        border-style: none;
        color: white;
    }

    .midi-option.enabled {
        background-color: #ffffff;
        color: black;
        box-shadow: 0px 0px 2px 2px #e0dbdb;
    }

    .resample-config {
        display: flex;

        .samples-per-note {
            display: flex;
            margin-left: 10px;
            margin-top: 10px;

            .dropdown-label {
                margin-right: 5px;
            }

            .Dropdown-option {
                padding: 2px;
            }
        }

        padding-bottom: 20px;
    }

    .sample-synth-button.sampling {
        background-color: red;
    }

    .sample-synth-button {
        background-color: #73ffc6;
    }
}

.midi-cc-effect {
    .Dropdown-control {
        padding: 2px;
    }

    .Dropdown-arrow {
        top: 8px;
    }
    
    .midi-cc-info {
        display: flex;
        cursor: pointer;
        background-color: #35ecff;
        padding: 3px;
        margin-top: 5px;
        border-radius: 3px;

        .midi-cc-number {
            margin-right: 5px;
        }
    }

    .midi-cc-name-container {
        display: flex;

        .midi-cc-name {
            flex: 1;
            border-radius: 0px;
            border-color: #7cffc9;
        }

        .label-button {
            background-color: #35ecff;
            cursor: pointer;
            text-align: center;
            padding: 3px;
        }
    }

}


