$altStepColor: #2b64e6;

.step.off.selection {
    background-color: #f00000af !important;
    opacity: 0.3 !important;
}

.step.on.masked {
    backgroun-color: black;
}

.step.on.selection {
    opacity: 0.3 !important;
}

.step.selected {
    border-width: 2px;
}

.step.off {
    step-parameter {
        color: white;
    }
}

.step.on {
    .step-parameter {
        min-width: 200px;
        color: black !important;
        padding-left: 6px;
        padding-right: 6px;
    }
}

.effects-parameters-button-container {
    .small-button {
        margin-left: 5px;
        background-color: black !important;
    }
}

.step-container {
    .fill {
        height: 18px;
    }
}

.fill.current-step {
    border-bottom: 2px solid red;
}

.fill.cursor {
}

.fill.cursor.on {
}

.step.current-step {
}

@keyframes blink { 50% { border-color: #c4ffec; }  }
@keyframes blink2 { 50% { border-color: #f2ffc9; }  }
@keyframes blink3 {
    from {
        background-color: transparent;
    }
    to {
        background-color: #c4ffec;
    }
};

.step.cursor, .step.alternate.cursor {
    border-width: 2px;
    border-color: #ff549e;
}

.step.vi-mode.cursor {
    animation: blink .3s step-end infinite alternate; 
}

.step.cursor {
    animation: blink2 .3s step-end infinite alternate; 
    opacity: 1 !important;
}

.step.on.vi-mode.cursor {
    border-top-color: #ff549e !important;
    border-right-color: #ff549e !important;
    border-left-color: #ff549e !important;
}

.step.on.cursor {
}

.step.off.cursor {
}

.step.off.vi-mode.cursor {
    border-color: #ff549e !important;
}

.steps-row.odd {
    .step.cursor {
        animation: blink .3s step-end infinite alternate; 
    }

    .step.alternate.cursor, .step.off.cursor {
        border-color: #ff549e;
    }

    .step {
        border-color: #2b64e600;
    }

    .step.alternate {
        border-color: #2b64e600;
    }
}

.step {
    border-color: #2b64e600;
}

.step.alternate {
    border-color: #2b64e600;
}

.step-container {
    position: relative;

    .fill {
        top: 0px;
        z-index: -1;
    }
}

.step-parameter {
    color: white;
    display: block;
    display: flex;

    .parameter-label {
        margin-right: auto;
        white-space: nowrap;
        max-width: 40px;
    }

    .slider.slider-horizontal {
        margin-top: 5px;
        width: 100px;
    }
}

.step-position {
    font-size: 4pt;
    color: black;
    text-align: center;
}

.steps-row-bottom {
    width: 100%;
    min-width: 480px;
    height: 2px;

    .current-step-box {
        background-color: #07414e;
        background-image: linear-gradient(45deg, #101011, transparent);
        width: 28px;
        height: 2px;
        display: flex;

        .right-box {
            background-color: #4de7ff;
            margin-left: auto;
        width: 28px;
        height: 2px;
        }
    }
}

.track.selected {
    .step.not-expanded {
        background-color: #383737;
    }

    .step.beat-division {
        background-color: #696868d4;
    }
}
.step.not-expanded {
    width: 23px;
    height: 13px;
    border-radius: 23px;
    display: flex;
    background-color: #282828b8;

    .triplet {
        margin-left: 12px;
        font-size: 9pt;
        margin-top: -2px;
        color: #515151;
    }
}

.step {

    position: relative;

    .right-edge {
        position: absolute;
        right: -6px;
        width: 8px;
    }

    border-width: 1px;
    float: left;
    Cursor: pointer;
    border-style: solid;
    margin-left: 4px;
    margin-right: 5px;
    margin-bottom: 4px;
    margin-top: 2px;
    font-size: 14px;
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */

}


.step {
    .palettes-container {
        display: flex;
        flex-wrap: wrap;

        .palette-option {
            padding: 3px;
            width: 20px;
            text-align: center;
            margin: 5px;
        }

        .palette-option.selected {
            box-shadow: 0px 0px 3px 3px white;
        }
    }
}

.slice-indicator {
    width: 10px;
    height: 10px;
    border-radius: 10px;
    margin-top: 10px;
    margin-right: 30px;
}

.step.p-locked {
    position: relative;
    .p-lock-indicator {
        position: absolute;
        background-color: white;
        bottom: 0px;
        right: 0px;
        width: 6px;
        height: 6px;
        filter: brightness(0.8);
    }
}


.step.beat-division {
    background-color: #696868d4;
}

.track.selected {
    .step.not-expanded {
        //background-color: #525252cf;
    }
}
