.samples-stream-explorer.selected {
    .inner-explorer {
        .sample-stream-option.selected {
            background-color: white;
            .s-name {
                color: black;
            }
        }
    }
}

.samples-stream-explorer {
    border: 1px solid transparent;
    background-color: unset !important;
    border-width: thin;
    border-radius: 5px;
    margin-left: 10px;
    width: 240px;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    position: relative;
    

    .explorer-header {
        overflow: hidden;
        padding-top: 5px;
        font-size: 10pt;
        padding-left: 5px;
        width: 100%;
        white-space: nowrap;
        padding-bottom : 5px;

        .tags-icon.selected {
            opacity: 1;
        }

        .heart {
            position: absolute;
            top: 11px;
            right: 35px;
        }

        .tags-icon {
            position: absolute;
            top: 8px;
            right: 3px;
            opacity: 0.5;
            width: 20px;
            height: 20px;
            cursor: pointer;
        }

        .header-text {
            width: 20em;
            animation: type 1s steps(60, end); 
            display: flex;

            .arrows {
                display: flex;
                width: 30px;
                height: 20px;
                margin-right: 5px;
            }

            .arrow-left, .arrow-right {
                cursor: pointer;
                border: solid white;
                border-width: 0 2px 2px 0;
                display: inline-block;
                padding: 5px;
                height: 10px;
            }

            .arrow-left:active, .arrow-right:active {
                border-width: 0 4px 4px 0;
            }

            .arrow-left {
                margin-left: 3px;
                transform: rotate(135deg);
                -webkit-transform: rotate(135deg);
                margin-top: 4px;
            }
            .no-left.arrow-right{
                margin-right: 2px;
            }
            .arrow-right{
                margin-top: 4px;
                transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
                margin-left: auto;
                margin-right: 5px;
            }
        }
        display: flex;
        position: absolute;
        border-top-right-radius: 4px;
        backdrop-filter: blur(4px);
        background-color: #16187dc7;
        border-top-left-radius: 5px;
        z-index: 10;

        .stream-type.first {
            margin-left: 5px;
        }
        .stream-type.last {
            margin-right: 5px;
        }

        .stream-type {
            color:  #ffffff73;
            max-width: 160px;
            overflow: hidden;
            margin-left: 1px;
            margin-right: 1px;
            cursor: pointer;
        }
    }

    .inner-explorer::-webkit-scrollbar-thumb {
        background-color: #ffffff33 !important;
    }
    
    .inner-explorer {
        margin-top: 2px;
        flex: 1;
        overflow-y: scroll;
        overflow-x: hidden;
        background-color: #00000960;
        padding: 3px;
        border-radius: 5px;
        padding-top: 28px;
        

        .samples-stream-tag {
            .tag-info.expanded {
                background-color: #a8d3ff33;;

                .tag-box {
                    border-left: none !important;
                    border-bottom: none;
                    border-top: 9px solid transparent;
                    border-right: 9px solid white;
                }
            }
            
            .tag-info {

                display: flex;
                font-size: 9pt;
                cursor: pointer;
                padding: 2px;

                .tag-box {
                    margin-top: 6px;
                    //border-left: 9px solid #6d6d6d !important;
                    border-bottom: 9px solid transparent;
                    width: 0px;
                    height: 0px;
                    margin-right: 5px;
                }
            }

            .tag-results {
                margin-left: 13px;
            }
        }
        
    }
}

.sample-stream-option.is-exploring {
    background-color: #9580e8d6;
}

.sample-stream-option.showing-dropdown {
    border: 1px dashed white;
}

.sample-stream-option {
    display: flex;
    cursor: pointer;
    font-size: 9pt;

    .sample-side-explorer {
        position: absolute;
        left: -400px;
        top: 0px;
        width: 400px;
        height: 500px;
        background-color: #1F0A72af;
        backdrop-filter: blur(4px);
    }

    .pack-thumb {
        margin-top: 5px;
        min-width: 8px;
        width: 8px;
        height: 8px;
    }
    
    .favorited.heart:before,  .favorited.heart:after {
        background: #fc2e5eba !important;
    }

    .heart {
        transform: scale(0.65);
        margin-top: 3px;
        cursor: pointer;
        width: 20px;
        min-width: 20px;
        pointer-events: none;
        margin-left: -4px;
    }

    .sample-dropdown {
        .heart {
            margin-left: 2px;
            margin-top: 0px;
        }
    }

    .s-thumb {
        margin-top: 3px;
        width: 14px;
        height: 14px;
        margin-right: 5px;
    }
    .s-name {
        color: #d2d2d2;
        margin-right: 5px;
        padding: 2px;
    }
    .s-tags {
        display: flex;
        margin-left: auto;
        padding: 2px;

        .s-tag:active {
            background-color: white;
        }
        .s-tag:hover {
            color: red;
        }
        .s-tag {
            color:  #ffffff73;
            margin-left: 5px;
        }
    }
    white-space: nowrap;
}

.sample-stream-option.selected {
    background-color: #ffffff63;;
    .s-name {
        color: black;
    }
}

