
.user-script-component.syntax-error {
    border: 1px dashed red !important;
}

.user-script-component {

    .script-variables {
        max-width: 340px;
        display: flex;
        flex-wrap: wrap;
    }
    .tidal-visualizer {
        width: 100%;
   }
    .load-script-flow, .save-script-flow {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        z-index: 10000000;
        height: 100%;
        background-color: #00000040;
        backdrop-filter: blur(4px);

        .script-option {
            display: flex;
            padding: 3px;
            margin-bottom: 1px;
            cursor: pointer;
            background-color: #2d2d2d52;
            color: white;
            .script-name {
            }
            .script-user {
                margin-left: auto;
                color: red;
            }
        }

        .load-toolbar {
            display: flex;
            .cancel-btn {
                cursor: pointer;
                margin-left: auto;
                padding: 5px;
                background-color: red;
                border-radius: 3px;
            }
        }
    }

    .save-script-flow {
        .save-toolbar {
            display: flex;
            .cancel-btn, .done-btn {
                cursor: pointer;
                border-radius: 3px;
                padding: 5px;
            }
            .done-btn {
                background-color: green;
            }
            .cancel-btn {
                margin-left: auto;
                background-color: red;
                margin-right: 5px;
            }
        }
        input {
            background-color: #292929;
            color: white;
            width: 100%;
        }
    }
    
    .script-toolbar {
        display: flex;

        .execute-button:active, .save-script-btn:active {
            box-shadow: 0px 0px 2px 2px white;
        }

        .execute-button, .save-script-btn {
            border-radius: 3px;
        }

        .execute-button {
            display: flex;
            margin-left: 5px;

            .arrow-right {
                border-left-width: 15px;
                border-bottom-width: 7px;
                border-top-width: 7px;
                border-left-color: white;
                margin-right: 5px;
            }
        }

        .save-script-btn, .load-script-btn {
            background-color: #000000;
            color: white;
            text-align: center;
            border: 1px solid #353535;
            padding: 5px;
            cursor: pointer;
        }
        
        .save-script-btn {
            margin-left: auto;
       }
    }

    .script-box {
    }
}

.script-box, .z-object {
    .possibilities {
        position: absolute;
        z-index: 20000;
        background-color: #2f2f2f24;;
        backdrop-filter: blur(4px);
        color: white;
        overflow-y: scroll;
        ::-webkit-scrollbar-thumb {
            background: black !important;
        }

        .clear-btn {
            position: absolute;
            top: 2px;
            right: 2px;
            cursor: pointer;
        }

        .library-view {
            padding: 5px;
            background-color: #48484885;
            .library {
                display: flex;
                
                .see-source-btn {
                    cursor: pointer;
                    font-size: 7pt;
                    background-color: white;
                    color: black;
                    padding: 3px;
                    margin-left: 10px;
                }
            }

            .see-source {
                width: 250px;
                border: none;
                outline: none;
                min-height: unset !important;
                height: 200px;
                background-color: black;
                color: white;
                
            }
        }

        .option:hover, .option.selected {
            background-color: #443d3d91;
        }

        .option {
            padding: 5px;
            cursor: pointer;
            background-color: #00000099;
            .args {
                display: flex;
                .param {
                    margin-left: 5px;
                    color: #ff8fb8;
                }
            }

            .description {
                font-size: 8pt;
                color: #fefeff;
            }
        }
    }
}


.user-script-component {
    .script-graphics {
        width: 500px;
        height: 500px;

        polygon {
            transform-origin: top left;
        }
    }
}

.script-tidals {
    position: relative;
    width: 600px;
    height: 600px;
    
    canvas {
        position: absolute;
    }

    .color-btn {
        width: 15px;
        height: 15px;
        border-radius: 15px;
        cursor: pointer;
        bottom: 0px;
        left: -550px;
        position: absolute;
        background-color: red;
    }

    .color-pickers {
        bottom: -120px;
        left: -500px;
        position: absolute;
        display: flex;

        .chrome-picker {
            position: unset;
        }
        
    }
}

.script-and-tidal {
    display: flex;
}

.script-and-tidal.expanded {
    position: fixed;
    top: 40px;
    z-index: 100000;
    height: 100%;
    width: 100%;
    left: 0px;
    background-color: #000000db;

    .user-script-component {
        background-color: #6ab3e60f;

        .script-box {
            width: 700px;
        }
    }
}

.script-tidals {
    position: relative;

    .display {
        position: absolute;
        font-family: '8bit';
        color: white;
        z-index: 100;

        span {
            margin: auto;
        }
        
    }
}

.z-object {
    .user-script-component {
        .script-toolbar {
            .save-script-btn, .load-script-btn {
                display: none;
            }
            .execute-button {
                margin-left: auto;
            }
        }
    }
}
