.synth-mods {
    max-height: 400px;
    overflow-y: scroll;

    .simplifier {
        padding: 5px;
        background-color: #402ed24f;
        color: white;

        .slider-and-btn {
            display: flex;

            .simplify-btn {
                background-color: #30ff83;
                color: black;
                width: 120px;
                text-align: center;
                padding: 5px;
                margin-left: auto;
                cursor: pointer;
            }

            .slider.slider-horizonal {
                width: 150px;
            }
        }
    }
}
