.piano-roll-wrapper {
    position: relative;
    
    .parameter-automation {
        position: absolute;
        top: 28px;
        width: 100%;
        height: 100%;
        background-color: black;
        opacity: 0.6;
        z-index: 50;
    }
}


.installation {
    .installation-knobs {
        padding: 5px;
        display: flex;
        flex-wrap: wrap;
        width: 200px;
        border: 3px solid white;
        margin-left: 15px;

        .parameter-knob {
            margin: 5px;
        }
    }
    .automation-toolbar {
        .dropdowns {
            display: none;
        }

        .parameter-button {
            padding: 5px;
            font-size: 13pt;
            white-space: nowrap;
        }

   }

    .delete-button {
        display: none;
    }

    .mix-buttons {
        margin-left: 10px;
    }
}

.automation-toolbar {
    background-color: #505050e0;
    background-image: linear-gradient(45deg, #000000d6, transparent 40%);

    .Dropdown-control {
    }

    .step-actions {
        margin-left: auto;
        display: flex;
        margin-right: 10px;

        .resolution-dropdown {
            float: none;
            width: unset;
            margin-right: 6px;

            .Dropdown-control {
                margin-top: 5px;
                width: 40px;
                min-width: unset;
            }
        }

        .quantize-button {
            border: 1px solid white;
            background-color: #2e2e2e;
            font-size: 8pt;
            color: white;
            line-height: 12px;
            padding: 5px;
            cursor: pointer;
            border-radius: 5px;
        }

        .quantize-button.quantized {
            border-radius: 0px;
        }

        .add-steps-btn:active, .remove-steps-btn:active {
            box-shadow: 0px 0px 2px 2px white;
        }

        .add-steps-btn, .remove-steps-btn {
            color: black;
            margin-left: 5px;
            width: 20px;
            text-align: center;
            border-radius: 5px;
            cursor: pointer;
            line-height: 20px;
        }
    }
    
    .toolbar-container {
        display: flex;
        height: 25px;
    }
    
    .dropdowns {
        display: flex;
    }
    
    .dropdowns.disabled {
        pointer-events:none;
        opacity: 0.3;
    }
    
    .automation-icon {
        height: 20px;
        width: 20px;
        cursor: pointer;
        margin-left: 10px;
    }
    
    .automation-parameters {
        overflow: scroll;
    }
    
    .toolbar-resizer {
        flex: 1;
        cursor: row-resize;
        display: flex;
        margin-left: 5px;
        
        .parameter-button {
            margin-left: 5px;
            font-size: 8pt;
            background-color: #1b1b1b;
            padding: 3px;
            border-radius: 3px;
            line-height: 15px;
            cursor: pointer;
            white-space: nowrap;
        }
        
    }
    
    .Dropdown-option {
        font-size: 10pt !important;
        padding-top: 2px !important;
        padding-bottom: 2px !important;
        padding-left: 10px !important;
    }
    
    .Dropdown-option.has-automations {
        color: #f3235e !important;
    }
    
    .Dropdown-control {
        border: none;
        height: 20px;
        line-height: 5px;
        font-size: 10pt;
        opacity: 0.9;
        border-radius: 0px;
        min-width: 100px;
        
        .Dropdown-arrow {
            top: 7px;
        }
    }
}


.installation, .show-piano-roll {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                          supported by Chrome and Opera */
 
    display: flex;
    flex-direction: column;
    height: 100%;
    .arrangement-resizing-edge {
        width: 100%;
        height: 10px;
        background-color: #5c1bc7;
        opacity: 0.5;
        cursor: row-resize;
    }

    .scroll-area {
        overflow-y: hidden;
        height: 100%;
        flex: 1;
        z-index: 4000;
    }

    .arrangement-bottom-container {
        z-index: 1001;
        overflow-y: hidden;
        background-color: #000000a1;

        .piano-roll-container {
            padding-top: 0px;
        }
    }
}


.installation {
   
    .guilds-menu, .midi-input-dropdown-container, .lock-button, .piano-roll-button, .patterns-component {
        display: none;
    }
    
    .sequencer-dashboard {
        display: flex;
        margin: 15px;

        .dashboard-module {
            margin-right: 15px;
            padding: 5px;
            width: 150px;
            height: 150px;
            background-color: #141415;
            color: #a5a5a5;

            .module-name {
                font-size: 12pt;
            }

            .inner-module {
                font-size: 10pt;
            }
        }
    }

    .main-area {
        background: none;
    }

    .tracks-container {
        flex: 0;
        margin-left: 15px;

        .track-right-buttons {
            margin-right: 20px;
        }
        .track-footer-container {
            display: none;
        }

        .delete-button {
        }
    }

    .bpm-component {
        opacity: 0.8;
    }


}

.youtube-info {
    .volume-box {
        margin-top: 3px;
        margin-left: 0px;
        height: 20px;
        
        .volume-indicator {
            height: 20px;
        }
    }
}


.midi-input-dropdown-container { 
    .Dropdown-control {
        width: 150px;
    }

    .is-open {
        margin-bottom: 150px;
    }
}
