.track.live-loop {
    overflow: visible;

    .track-inner {
        overflow: visible;
        overflow-x: visible;
        overflow-y: visible;
    }

    .z-object {
        pointer-events: none;
        border-radius: 15px;
        position: relative;
        box-shadow: unset !important;
        border: none;
        margin-top: -10px;
        margin-right: 15px;
        top: 0px !important;
        left: 0px !important;
        height: 100px !important;
        overflow: hidden;

        .inlets, .outlets {
            display: none;
        }
    }

    .track-controls {
        margin-left: -11px;
    }

    .expand-sample {
        display: none;
    }

    .frozen-container.selected {
        box-shadow: unset;

    }

    .btn-and-countdown {
        margin-left: 5px;

        .countdown {
            margin-top: 10px;
            font-size: 16pt;
            text-align: center;
        }
    }
    .frozen-columns-container {
        display: flex;
        flex-direction: column;
        margin-left: 15px;
        max-height: 100px;
        flex-wrap: wrap;
        
        .frozen-container {
            .playpause label {
                border-color: transparent transparent transparent black;
            }
            border-radius: 0px;
            .freeze-name {
                color: black !important;
            }
            .frozen-progress {
                background-color: #9f9f9f;
                overflow: hidden;
            }
        }
    }

    .youtube-info {
        .volume-and-inputs {
            display: flex;
            flex-direction: row-reverse;

            .Dropdown-control {
                margin-top: -2px;
            }
        }
    }
}

.live-loop-track {
    padding: 10px;
    display: flex;
    flex: 1;
    
    .record-btn.preparing {
	-webkit-animation: preparing-to-record linear 1s infinite;
	animation: preparing-to-record linear 0.3s infinite;
        margin-top: 5px;
    }

    .record-btn.recording {
        margin: unset;
        margin-top: 5px;
        background-color: red !important;
    }

    .record-btn {
        width: 40px;
        height: 40px;
        margin-top: 5px;
        border: 4px solid red;
        border-radius: 45px;
        border: 4px solid red;
        border-radius: 40px;
        background-color: #343434;
    }
}

@keyframes preparing-to-record {
	0% { opacity: 1; } 
	50% { background-color: red; } 
	100% { opacity: 1; }
}
