.texture-loader {
    img {
        max-width: 200px;
        max-height: 200px;
    }

    .drag-placeholder {
        padding: 15px;
    }
}

.z-matrix {
    padding: 5px;
    background: #16161b;
    .z-row {
        display: flex;

        .z-value {
            margin: 2px;
            width: 20px;
            height: 20px;
            overflow: hidden;
            background-color: #292929;
            border-radius: 20px;
            text-align: center;
            line-height: 20px;
            font-size: 8pt;
        }
    }
}

.z-tidal {
    padding: 5px;
    border-radius: 5px;
    position: relative;
    background: transparent;

    .dot, .cycle-length {
        display: none;
    }
    
    .tidal-string {
        font-size: 11pt;
        left: 8px;
        top: 13.5px;
        position: absolute;
        pointer-events: none;
        color: #c0ff00;

        .str-part.current {
            color: red;
        }
    }

    .tidal-component {
        width: 300px;

        .enter-button {
            display: none;
            font-size: 9pt;
            width: 30px;
            height: 30px;
        }

        .tidal-input {
            background-color: #1e1e1e;
            font-size: 11pt;
            padding: 3px;
            min-width: 250px;
            flex: 1;
        }
    }
}

.dpad {
    display: flex;
    flex-direction: column;
    padding: 5px;
    .arrows-row {
        display: flex;
        width: 122px;
        
        .arrow-up, .arrow-down {
            margin-left: auto;
        }

        .g-btn:active {
            background-color: red;
        }

        .g-btn {
            border-radius: 5px;
            cursor: pointer;
            background-color: black;
            padding: 8px;
            margin-left: auto;
            margin-right: auto;
        }
    }

    .arrow-right {
        position:relative;
        width: 0; 
        height: 0; 
        border-top: 8px solid transparent;
        border-bottom: 8px solid transparent;
        border-left: 18px solid white;
        margin-top: 0px;
    }

    .arrow-left {
        position:relative;
        width: 0; 
        height: 0; 
        border-top: 8px solid transparent;
        border-bottom: 8px solid transparent;
        border-right: 18px solid white;
    }

    .arrow-up {
        position: relative;
        width: 0;
        height: 0;
        border-left: 8px solid transparent;
        border-bottom: 18px solid white;
        border-right: 8px solid transparent;
    }

    .arrow-down  {
        position: relative;
        width: 0;
        height: 0;
        border-top: 18px solid white;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
    }
}



.jrepl {
    max-width: 200px;
    min-width: 200px;
    font-size: 8pt;
    // background-color: blue;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .tidal-string.immutable {
        border: 1px dashed red;
    }
    .tidal-string.triggering {
        background-color: #252525;
    }
    .tidal-string {
        min-height: 24px;
        font-size: 10pt;
        min-width: 10px;
        align-items: flex-start;
        background-color: #151515;
        color: #c0ff00;
        padding-left: 5px;
        padding-right: 15px;
        border: 1px solid transparent;
        
        .cycle-length {
            margin-right: 5px;
            font-size: 8pt;
            width: 15px;
        }

        .dot {
            width: 10px;
            height: 10px;
            margin-right: 10px;
            background-color: #befc01;
        }

        .dot.immutable {
            background-color: red;
        }
        .str-part-1 {
            color: red;
        }

        .current {
            color: red;
        }
    }
}


.zmacro {
    padding: 5px;
    .more-info-btn.show {
        .dot {
            background-color: white;
        }
    }
    .more-info-btn {
        .dot {
            width: 5px;
            height: 5px;
            margin: 1px;
            background-color: white;
            border-radius: 2px;
        }
    }
    .macro-slider {
        height: 120px;

        position: relative;
        width: 25px;
        background-color: #686565;

        .slider-indicator {
            position: absolute;
            background-color: #acd3f5;
            bottom: 0px;
            left: 0px;
            width: 25px;
        }
        
    }
}

.jsnapshot {
    padding: 5px;

    display: flex;

    .take-snapshot-btn:active {
        filter: contrast(.25);
    }
    .take-snapshot-btn {
        margin-top: 5px;
        padding: 5px;
        color: black;
        color: transparent;
        width: 28px;
        height: 21px;
        filter: contrast(.015);
        background-size: cover;
        cursor: pointer;
        background-image: url("/snapshot.svg");
        margin-right: 10px;
    }
    
    .snapshots {
        display: flex;
        min-width: 50px;
        background-color: #252525;
        padding: 5px;
        .snapshot-option:active {
            background-color: #352323;
        }

        .snapshot-option {
            width: 20px;
            height: 20px;
            /* border-radius: 25px; */
            line-height: 20px;
            font-size: 8pt;
            cursor: pointer;
            margin-left: 5px;
            text-align: center;
            background-color: #151515;
            color: #befc01;
        }
    }
}
