
.ztracker-object.z-object {
    padding: 10px;
    background-color: #181818 !important;
    border-radius: 15px !important;

    .z-tracker {
        background-color: #0d0d0d;
        padding: 10px;
        border-radius: 10px;
        overflow: hidden;
    }
}

.group-tracker {
    display: flex;

    .tracker {
        .youtube-info {
            float: none;
            padding-bottom: 2px;
            .sound-thumbnail, .youtube-thumbnail {
                width: 100%;
                border-radius: 0px;
                background-color: black !important;
                font-size: 8pt;
                color: white;
                margin: 0px !important;
                margin-top: 0px !important;
                letter-spacing: unset;
                height: 17px;
            }
        }
        width: 100px;
        margin-right: 1px;
        margin-left: 1px;

        .tracker-step.cursor {
            .inner-step {
                border: 1px solid red;

                .step-number {
                    background-color: red;
                    color: white;
                }
            }
        }

        .tracker-step.current {
            background-color: #3e3e3e;
        }

        .tracker-step {
            height: 17px;
            display: flex;

            .inner-step {
                flex: 1;
                border: 1px solid transparent;
                display: flex;
                width: 100%;
            }

            .bar.step-number {
                color: white;
            }

            .step-number {
                padding-left: 2px;
                text-align: left;
                font-size: 10pt;
                color: #8e8f91;
                width: 20px;
                min-width: 15px;
                // background-color: #1d1d1d;
                cursor: pointer;
            }

            .play-dot {
                width: 10px;
                height: 10px;
                /* line-height: 30px; */
                margin-top: 2px;
                border-radius: 8px;
                background-color: #7fe40e;
                margin-left: auto;
            }


            .step-data {
                display: flex;
                font-size: 10pt;
                .note-name {
                    color: red;
                    border: none;
                    text-align: left;
                    font-size: 10pt;
                    width: 22px;
                }

                .edit.tracker-parameter, .note-name.edit {
                    background-color: red;
                }

                .tracker-parameter {
                    border-right: 1px solid #252525;
                    text-align: left;
                    cursor: pointer;
                    width: 20px;
                    overflow: hidden;
                    margin-right: 4px;
                }
            }
        }
    }
}

