
.cirklon-editor.frozen {
    pointer-events: none;
}

.v3 {
    .editor {
        .cirklon-editor.light-mode {
            .cirklon-parameter-chooser {
                .param-btn {
                    color: #484545;

                    &:after {
                        background-color: #484545;
                    }
                }
                
                .param-btn.selected {
                    color: black;
                    border-bottom-color: black;
                }
            }
        }
    }
}

.cirklon-editor {
        display: flex;
        flex-direction: column;
        position: relative;
        align-items: flex-start;

        .track-footer-container {
            display: flex;
        }

        .cirklon-playhead {
            position: absolute;
            top: 0px;
            height: 20px;
            width: 10px;
            border-right: 2px solid white;
            background-image: linear-gradient(268deg, #ffffff7a, transparent);
            z-index: 10000000000;
        }

        .legato-btn:active {
            transform: scale(1.1);
        }
        .legato-btn {
            display: flex;
            position: absolute;
            right: 80px;
            top: 128px;
            font-size: 8pt;
            background-color: white;
            color: black;
            border-radius: 5px;
            padding: 1px;
            padding-left: 3px;
            padding-right: 3px;
            cursor: pointer;
        }

        .arrow-container {
            width: 20px;
            cursor: pointer;
            margin-left: 5px;
        }
        .arrow-right {
            margin-left: 10px;
            height: 0px;
            margin-top: 2px;
            width: 0px;
            border-bottom: 6px solid transparent;
            border-top: 6px solid transparent;
            border-left: 8px solid black;
            position: relative;
            
            &:after {
                width: 13px;
                height: 3px;
                background-color: black;
                content: "";
                position: absolute;
                left: -20px;
                top: -2px;
            }
        }


        .frozen-overlay {
            top: 0px;
            left: 0px;
            background-color: black;
            position: absolute;
            width: 100%;
            height: 100%;
            opacity: 0.7;
            z-index: 1000;
            pointer-events: none;
            display: flex;

            .frozen-text {
                margin: auto;
                font-size: 14pt;
            }


        }

        .chokes-and-matches {
            border-top: none;
        }

        .checkbox-parameter {
            font-size: 9pt;
        }

        min-width: 465px;

        .Dropdown-control {
            background-color: white !important;
            color: black !important;
            font-size: 9pt;
        }
        
        .Dropdown-root.is-open {
            margin-bottom: 85px;
        }
        
        .Dropdown-option {
            padding: 2px;
            font-size: 9pt;
        }
        
        .Dropdown-placeholder {
            line-height: 15px;
        }
        
        .bars {
            display: flex;
            margin-left: 10px;
            margin-top: 5px;
            max-width: 335px;
            flex-wrap: wrap;

            .follow-btn {
                background-color: #353535;
                margin-left: 5px;
                padding: 3px;
                font-size: 9pt;
                cursor: pointer;
                line-height: 15px;
                height: 20px;
            }

            .follow-btn.following {
                box-shadow: 0px 0px 2px 2px #cfcfcf;
                background-color: white;
                color: black;
            }

            .bar-label {
                font-size: 9pt;
                line-height: 15px;
                padding: 3px;
            }

            .bar {
                padding: 3px;
                background-color: #353535;
                color: white;
                margin-left: 5px;
                margin-right: 5px;
                cursor: pointer;
                width: 20px;
                height: 20px;
                text-align: center;
                line-height: 15px;
            }

            .bar.current {
                position: relative;
                .bar-text {
                    z-index: 100000;
                }
                &:before {
                    content: '';
                    position: absolute;
                    top: 5px;
                    left: 5px;
                    width: 10px;
                    height: 10px;
                    background-color: #ffffff50;
                    border-radius: 15px;
                    animation: barblink .8s ease-in-out infinite alternate; 
                }
                &:after {
                    content: '';
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    width: 20px;
                    height: 20px;
                    background-color: #ffffff70;
                    border-radius: 20px;
                    animation: barblink 1.3s ease-in-out infinite alternate; 
                }
            }

            .bar.selected {
                box-shadow: 0px 0px 2px 2px #cfcfcf;
                background-color: white;
                color: black;
            }
        }

        .track-length-button, .freeze-button.dot {
            display: none !important;
        }

        .aux-events {
            margin-top: 5px;
            background-color: #0000003b;
            padding: 5px;
            font-size: 9pt;

            .aux-event-type {
                display: flex;

                .accumulator-options {
                    display: flex;

                    .parameter-box {
                        display: flex;
                        height: 10px;
                        .slider.slider-horizontal {
                            margin-left: 10px;
                            margin-top: 3px;
                            width: 100px;
                        }
                    }
                }

                .Dropdown-control {
                    margin-left: 10px;
                    width: 100px;
                    height: 20px;
                    padding: 2px;
                    border-color: #fdfdfd33;
                    color: white !important;
                    background-color: #ffffff26 !important;

                    .Dropdown-arrow {
                        top: 3px;
                    }
                }
            }
        }

        .cirklon-parameter-chooser {
            display: flex;
            align-items: flex-start;
            width: 360px;
            flex-wrap: wrap;
            margin-bottom: 5px;

            .param-btn {
                padding: 1px;
                padding-bottom: 3px;
                margin: 2px;
                margin-top: 2px;
                align-items: flex-start;
                // background-color: #353535;
                cursor: pointer;
                font-size: 9pt;
                color: silver;
                margin-left: 5px;
                border-bottom: 1px solid transparent;
                position: relative;

                &:after {
                    content: '';
                    position: absolute;
                    width: 4px;
                    height: 1px;
                    bottom: 3px;
                    background-color: #e9e6e6;
                    left: 2px;
                }
            }

            .param-btn.selected {
                border-bottom-color: white;
                background-color: unset !important;
                // box-shadow: 0px 0px 1px 1px white;
                color: white;

                &:after {
                    display: none;
                }
            }

            .selection-btns {
                display: flex;
                margin-left: auto;
                
                .cirklon-mode-btn {
                    padding: 3px;
                    align-items: flex-start;
                    background-color: #353535;
                    cursor: pointer;
                    font-size: 9pt;
                    margin-left: 5px;
                }

                .cirklon-mode-btn.selected {
                    background-color: unset !important;
                    //box-shadow: 0px 0px 1px 1px white;
                    border-bottom-color: white;
                    color: black;
                }
            }

       }

        .cirklon-steps {
            display: flex;
            overflow-x: scroll;
            align-items: flex-start;

            .bar-divider {
                margin-top: 26px;
                height: 60px;
                width: 1px;
                background-color: #94979b;
            }
            
            .cirklon-step.cursor {
                .step-toggle {
                    animation: blink3 .3s ease-in-out infinite alternate; 
                    background-color: white;
                }
            }

            .cirklon-step.on {
                opacity: 1;

                .volume-box {
                    .helper-line {
                        display: block;
                    }
                }
            }
            
            .cirklon-step {
                margin-left: 2px;
                margin-right: 2px;
                font-size: 9pt;
                text-align: center;
                position: relative;
                overflow: visible;
                //padding-top: 23px;

                .duration-indicator.p-locked {
                }

                .duration-indicator {
                    position: absolute;
                    border-left: 3px solid transparent;
                    border-right: 3px solid transparent;
                    z-index: 0;
                    left: 3px;
                    top: 0px;
                    height: 4px;
                }

                .step-toggle.current {
                    .step-inner {
                        .step-bottom-tick {
                            background-color: white;
                        }
                    }
                }

                .step-toggle {
                    z-index: 1;
                    cursor: pointer;
                    height: 26px;
                    width: 26px;
                    line-height: 20px;
                    border-radius: 5px;
                    background-color: #1e1e1e;
                    margin-left: 3px;
                    margin-top: 2px;
                    margin-bottom: 2px;
                    padding-top: 3px;
                    padding-bottom: 3px;
                    padding-left: 1px;
                    padding-right: 1px;
                    .step-inner {
                        pointer-events: none;
                        line-height: 15px;
                        background-color: black;
                        border-radius: 3px;
                        width: 100%;
                        height: 100%;
                        font-size: 8pt;
                        position: relative;

                        .p-lock-indicator {
                            position: absolute;
                            right: 0px;
                            top: 5px;
                            width: 6px;
                            height: 6px;
                            background-color: black;
                            border-radius: 6px;
                            padding: 1px;

                            .inner-indicator {
                                width: 4px;
                                height: 4px;
                                border-radius: 4px;
                                filter: hue-rotate(180deg);
                            }
                        }

                        .step-bottom-tick {
                            height: 2px;
                            width: 14px;
                            margin-left: 5px;
                            background-color: #80808063;
                            border-radius: 2px;
                        }

                    }

                }

                .step-toggle.current, .step-toggle.current.on {
                    //filter: brightness(1.2);
                    //background-color: white !important;
                }

                .step-toggle.selected {
                    box-shadow: 0px 0px 2px 2px white;
                }

                .step-toggle.on {
                    background-color: white;
                    color: black;
                }
            }
            
            .volume-box {
                opacity: 0.1;
                width: 25px;
                display: flex;
                flex-direction: column;
                position: relative;
                margin-left: 3px;
                
                .rest {
                    flex: 1;
                }
                
                .helper-line {
                    position: absolute;
                    width: 23px;
                    height: 1px;
                    //border-top: 1px dashed #ffffff61;
                    display: none;
                }

                .volume-indicator {
                    opacity: 1;
                    cursor: unset;
                    position: relative;
                    margin-top: auto;
                    width: 23px;
                }
            }

            .value {
                opacity: 0;
                position: absolute;
                top: 20px;
                color: white;
                left: 5px;
            }
        }

        .cirklon-step:active {
            .value {
                opacity: 1;
            }
        }
    }

    .cirklon-step.on {
        .volume-box {
            opacity: 1;
        }
    }

