
.modal {
    transition: unset !important;
    animation: unset !important;
}

.modal.fade .modal-dialog {
    transition: none !important;
    transform: none !important;
}

.modal-content {
    z-index: 1000000000000000 !important;
    animation: unset;
}
.rack-modal {
    max-width: 400px !important;

    .convert-options {
        display: flex;

        .convert-option {
            background-color: #232323;
            color: white;
            padding: 5px;
            margin-right: 5px;
            cursor: pointer;
        }

        .convert-option.selected {
            background-color: white;
            color: black;
            box-shadow: 0px 0px 2px 1px #fbf1f1;
        }
    }
}

.sequence-action {
    display: flex;
    flex-direction: row;
}

.sequence-action-name {
    margin-right: 5px;
}

.clear-pattern-button {
    margin-left: 0px !important;
}

.resample-action {
    margin-top: 5px;
    display: flex;
    flex-direction: row;
}

.tag-input {
    background-color: black !important;
    color: white !important;
    border-style: solid;
    border-color: #383f46 !important;
}

.buy-action {
    cursor: pointer;
    color: black;
    font-size: 8pt;
    padding: 5px;
    background-color: #62ffa1;;
}
.balance {
    margin-right: 20px;
}

.price {
    margin-top: 5px;
}

.price-eth, .price-dollars {
    color: white;
    margin-right: 5px;
}

.price-eth-wrapper, .price-dollars-wrapper {
    padding: 5px;
    background-color: #127543;
    color: white;
    font-size: 8pt;
    margin-left: 5px;
}

.waiting-for-tokens {
    font-size: 10pt;
    color: #a5b3d3;
}

.go-to-buy {
    margin-left: 20px;
}

.balance-info {
    font-size: 10pt;
}

.ethereum-explanation {
    margin-top: 15px;
    font-size: 10pt;

    .basic-explanation {
        margin-bottom: 10px;
    }
}

.input-wrapper {
    display: flex;
}

.metamask-instructions {
    font-size: 10pt;
}

.link-creator {
    margin-top: 20px;
}

#linkInput {
    width: 400px;
    background-color: #232b4b;
    color: white;
}

.copied-info {
    clear: both;
    font-size: 10pt;
    color: #75fe75;
    background-color: black;
    padding: 3px;
    text-align: center;
}

.link-button {
    padding: 3px;
    color: white;
    background-color: #212d68;
    text-align: center;
    cursor: pointer;
}

.no-metamask {
    font-size: 10pt;
    margin-bottom: 10px;
}

.password-user-name {
    padding: 3px;
    background-color: #111523;
    color: #f4215e;
}

.guild-options-container {
    padding: 5px;
    border: 1px solid #191920;
    font-size: 11pt;
    
    .guild-options {
        display: flex;
        flex-wrap: wrap;
        overflow-y: scroll;
        max-height: 160px;
        
        .guild-option {
            padding: 3px;
            background-color: #080808;
            cursor: pointer;
            margin: 2px;
        }
        
        .guild-option.selected {
            background-color: #ffffff;
            color: black;
        }
    }
}

.login-btn {
    color: blue;
    cursor: pointer;
}

.modal-content {
    position: relative;

    .sign-up-text {
        margin-top: 10px;
        .sign-up-here {
            color: #00f496;
            text-decoration: underline;
            cursor: pointer;
        }
    }
    .progress-bar {
        transform: unset;
        background-color: #4c52fd !important;
        position: relative !important;
        left: 5px !important;
        top: 20px !important;
        margin-left: 10px;
        width: 380px !important;
        height: 30px !important;
        margin-top: 10px;
        
        .loading-text {
            font-size: 12pt !important;
            line-height: 15px !important;
        }
        
        .inner-progress-bar, .progress {
            height: 25px !important;
        }
    }
}

.save-recordings {
    font-size: 10pt;

    .save-recordings-button {
        margin-left: 3px;
        padding: 1px;
        background-color: red;
        color: white;
        cursor: pointer;
    }
}

.render-length {
    display: flex;
    margin-top: 10px;

    input {
        width: 50px;
        background-color: #16297c;
        border: none;
        color: white;
    }
}

.render-modal {
    .render-label {
        margin-right: 5px;
        width: 140px;
    }

    .render-options {
        display: flex;


        .render-option.selected {
            background-color: white;
            color: black;
            box-shadow: 0px 0px 1px 1px white;
        }

        .render-option {
            margin-right: 5px;
            padding-right: 3px;
            padding-left: 3px;
            cursor: pointer;
            background-color: #313131;
            color: white;
        }
    }
}

.share-with-people {
    margin-top: 10px;
    padding-top: 10px;
    border-top: solid 2px #071836;

    .auto-completion-users {
        background-color: #031b48;

        .auto-completed-user {
            padding: 3px;
            cursor: pointer;
        }

        .auto-completed-user:hover {
            background-color: #0f0877;
        }
    }
    .people-input-container {
        display: flex;
        .shared-user {
            margin-right: 5px;
            background-color: white;
            color: black;
            padding-left: 3px;
            padding-right: 20px;
            position: relative;

            .delete-user {
                position: absolute;
                font-size: 12pt;
                right: 4px;
                top: 0px;
                cursor: pointer;
            }
        }
                     
        .people-input {
            flex: 1;
            background-color: #0a082a;
            color: white;
        }
    }
}

.modal-dialog {
    z-index: 1000000000000000000000000 !important;
}

.hic-modal {
    .ipfs-url {
        max-width: 100%;
        overflow: hidden;
        background-color: black;
        padding: 3px;
    }
}



