.effect-rack {
    .edge-properties, .macro-details {
        .parameter-label-container {
        }
    }
    .horizontal {
        display: flex;
    }

    .effect-header.hide-buttons {
        opacity: 0.8;
    }

    .effect.selected {
        border:none;
        box-shadow: none;
    }

    .rack-toolbar {
        display: flex;
        margin-top: 1px;
        margin-bottom: 1px;
        background-color: #bfbfbf24;

        .routing-chooser {
            margin-top: 5px;
            margin-left: 10px;
        }

        .add-macro-button {
            margin-left: 5px;
            background-color: white !important;
            color: black;
        }
        
        .add-macro-button, .new-routing-button {
            border-radius: 2px;
            background-color: white !important;
            color: black;
            padding-left: 5px;
            padding-right: 5px;
            line-height: 25px;
            cursor: pointer;
        }

        .show-macros-button {
            margin-left: auto;
            border-radius: 2px;
            background-color: white;
            color: black;
            padding-left: 5px;
            padding-right: 5px;
            line-height: 25px;
            cursor: pointer;
        }
    }

    .effect-dropdown {
        .Dropdown-control {
            width: 70px;
            height: 25px;
            padding: 4px;
            border: none;
            margin-right: 2px;

        }
        .Dropdown-menu {
            width: 100px;
            max-height: 300px;

            .Dropdown-option {
                padding: 3px;
            }
        }
    }

    .expand-macros-button {
        padding: 3px;
        background-color: #6679ff;
        color: white;
        width: 85%;
        text-align: center;
        cursor: pointer;
    }
}

