jaki {
    svg.rack-canvas {
        background-color: black;
        rect {
            fill: #d0d0d0cf;
        }
    }
}

.effect-rack, .jaki {
    svg.rack-canvas {

        path {
            stroke-dasharray: 18, 2; 
            stroke: white;
            fill: transparent;
        }

        path.edge.hovered, path.edge.selected {
            stroke: red !important;
        }

        path.edge.env-edge {
            stroke: #3abcd6;
        }

        path.edge.lfo-edge {
            stroke: #3ad688;
        }

        path.edge.macro-edge {
            stroke: #224fdd;
        }

        .rack-tooltip {
            fill: black;
            color: white;
            z-index: 100;
        }

        text.tooltip-text {
            fill: white;
            z-index: 101;
        }

        .node-io.mapping {
            animation: fadeparam 0.5s 0.2s infinite;
            animation-direction: alternate;
        }


        rect.selected.node {
            fill: red;
        }

        rect.mouse-selection {
            fill: red;
            opacity: 0.3;
        }

        circle {
            fill: #a7b7bf;
        }

        text {
            fill: white;
            font-size: 11px;
        }

        text.macro-text {
            fill: white;
        }

        text.selected.node-text {
            fill: white;
        }

        rect.macro {
            fill: #2856ffdb;
        }
    }
}

