.midi-preferences, .audio-preferences {
    margin: 20px;
}

.midi-preferences {
    width: 20%;
}

.midi-options {
    display: flex;
}

.midi-preferences-subsection {
    display: flex;
    flex-direction: column;
}

.audio-outputs-option {
    cursor: pointer;
    color: #d6e6ff;
}

.midi-name {
    font-size: 14px;
    color: #d6e6ff;
}

.header {
    font-size: 24px;
}

.sub-header{
    font-size: 24px;
    clear: both;
    color: #f4f6ff;
    margin-bottom: 20px;
}

.audio-midi-setup {
    float: left;
}

.back-button {
    cursor: pointer;
}


.parameters {
    float: left;
    margin-left: 10px;
}

.refined-checkbox-preferences {
    color: white;
}

.recordings-view {
    margin-left: 10px;
    margin-top: 25px;
    clear: both;
}

.preferences-flex {
    display: flex;
    flex-direction: row;
}

.audio-preferences {
    .audio-input-chooser {
        float: none !important;
        margin-left: 0px !important;
    }
}

.latency-container {
    display: flex;

}

.available-users {
    margin-top: 15px;
}

.available-user {
    display: flex;
    cursor: pointer;
    border-width: thin;
    border-color: #5a5a5a;
    border-radius: 2px;
    background-color: #2e2d39;
    margin-left: 5px;
    margin-right: 5px;
    padding: 5px;
}

.available-user:hover, .available-user.selected {
    background-color: #6d6b87;
}

.available-user > .user-status {
    padding: 5px;
    font-size: 8pt;
    background-color: #303955;
    margin-left: auto;
}


.dropdown-title {
}

.needs-reload {
    color: red;
}
