
.plane-parameters.hide-ui {
    position: relative;
    .plane-sidebar, .user-script-component, .row-powa, .genetic-btns, .plane-group-config,
    .snapshots-container, .plane-group-buttons, .tab-option, .config-parameters, .look-config {
        display: none !important;
    }

    .plane-config {
        height: unset;
        width: unset;
    }

    .hide-ui-toggle {
        position: absolute;
        bottom: 5px;
        z-index: 16;
        right: 5px;
    }
}

.plane-parameters {
    .plane-config {
        height: 120px;
        width: 310px;
        flex-direction: column;
        position: relative;
        display: flex;
        .look-config {
            padding-top: 10px;
            padding-bottom: 10px;

            .gradient-color {
                position: relative;
                display: flex;
                .color-dot {
                    width: 15px;
                    height: 15px;
                    border-radius: 15px;
                    margin-left: 5px;
                    cursor: pointer;
                }
                
            }

            .slider-and-labels {
                display: flex;
            }
            .Dropdown-control {
                margin-top: 4px;
                height: 20px;
                line-height: 15px;
                padding: 2px;
                padding-left: 10px;
            }

            .Dropdown-arrow {
                top: 5px;
            }
        }

        .tab-options {
            width: 100%;
            position: absolute;
            bottom: 0px;
            left: 270px;
            margin-top: auto;
            display: flex;
            padding-left: 5px;

            .hide-ui-toggle.hiding {
                background-color: #101010;
                color: white;
                width: 70px;
                left: -35px;

                .circle-button {
                    background-color: white;
                    margin-left: unset;
                    margin-right: 5px;
                }
            }

            .hide-ui-toggle {
                display: flex;
                // margin-left: auto;
                background-color: #ffffffbf;
                color: black;
                border-radius: 15px;
                padding-left: 5px;
                padding-right: 5px;
                cursor: pointer;
                .circle-button {
                    margin-top: 2px;
                    margin-left: 5px;
                    width: 15px;
                    height: 15px;
                    background-color: black;
                    border-radius: 15px;
                }
            }

            .tab-option {
                margin-right: 5px;
                cursor: pointer;
                color: gray;
            }
            
            .tab-option.selected {
                border-bottom: 1px solid white;
                color: white;
            }
        }
        .config-parameters, .look-config {
            display: flex;
            flex-wrap: wrap;
            flex: 1;
            
        }
    }
    .vertical-pane {
        display: flex;
        flex-direction: column;
        .plane-toolbar {
            flex: 1;

            .plane-toolbar-inner {
                display: flex;
                flex-direction: column;
                height: 100%;
            }

            .control-toolbar {
                margin-top: auto;
            }
        }
    }
    .osc-type-dropdown {
        width: 90px;
        z-index: 200000;
    }

    .row-powa {
        display: flex;
        width: 100%;
        align-items: flex-start;
        .mappy-btn {
            align-items: flex-start;
        }
        .plane-play {
            margin-left: auto;
        }
        
    }
    .plane-play {
        cursor: pointer;
    }
    
    .plane-play.play-btn {
        box-sizing: border-box;
        width: 30px;
        height: 16px;
        /* background-color: white; */
        border-width: 16px 0px 16px 28px;
        border-style: solid;
        border-color: transparent #ffffff00 transparent #f9f9f9;
    }

    .snapshots-container {
        margin-top: 10px;
        padding-top: 5px;
        padding-left: 5px;
    }

    .plane-play.pause-btn {
        box-sizing: border-box;
        width: 32px;
        height: 32px;
        /* background-color: white; */
        border-width: 0px 10px 0px 10px;
        border-style: solid;
        border-color: white;
    }


    .control-toolbar {
        flex-direction: column;
        
        .parameter-box {
            display: flex;
            width: 240px;
            .parameter-label-value {
                width: 50px;
            }

            .slider.slider-horizontal {
                margin-top: 5px;
            }
        }
    }
    .plane-inner.expanded {

        .plane-sidebar {
            order: 1;
        }

        .control-toolbar {
            flex-direction: column;
        }

        .plane-group-buttons {
            margin-left: 10px;
            min-width: 315px;
        }

        .snapshots-container {
            width: 270px;
        }

        .plane-group-config {
            margin-left: 0px;
        }
        .plane-sidebar{
            display: flex;
            height: 100%;
            flex: 1;
            .plane-results.parameters-list {
                flex: 1;
            }
        }
        .plane-toolbar {
            flex-direction: column;
        }
    }
    .plane-inner {
        display: flex;

        .pencil-icon.enabled {
            background-color: #7c86ff;
        }

        .pencil-icon, .reset-icon {
            margin-top: 2px;
            cursor: pointer;
            width: 18px;
            margin-left: 10px;
        }
        .parameter-label .parameter-label-name {
            background-color: 4a4a4a47;
        }
            
        .plane-sidebar.hide {
            width: 35px;
        }

        .user-script-component {
            .script-box {
                background-color: #0000007a;
            }
        }
        
        .user-script-component.expanded {
            .script-box { 
                height: 340px;
                width: 420px;
                textarea, pre {
                    min-height: 330px;
                }
            }
        }

   }
}

.plane-container {
    svg.drawn-svg {
        background: transparent;
        position: absolute;
        left: 0px;
        top: 0px;
    }
   svg {

       path.macro.selected {
           opacity: 0.8;
           stroke: white;
       }

       path.macro {
           stroke: transparent;
       }

       .drawn-path.elapsed {
           stroke: red;
       }
       .drawn-path {
           stroke-width: 3px;
           stroke: white;
           stroke-dasharray: 5;
           fill: transparent;
       }
        .density-region {
            fill: blue;
        }


        .density-region.selected {
            stroke: #b3b9ff;
            stroke-dasharray: 5;
            stroke-width: 1px;
        }
    }
}

.user-script-component {
    position: relative;
    .expanded-icon {
        width: 15px;
        height: 20px;
        position: absolute;
        right: 10px;
        top: 0px;
        z-index: 40000;
        cursor: pointer;
    }
}

.user-script-component.expanded {

    .expanded-icon {
        background-color: #cecece47;
        box-shadow: 0px 0px 2px 2px #ffffff63;
    }
    
    .script-box { 
        height: 500px;
        width: 600px;
        textarea, pre {
            min-height: 490px;
        }
    }
}

.plane-view {
    position: relative;

    canvas.force-drawer {
        z-index: 12;
    }

    canvas.canvas-shader {
        position: absolute;
        z-index: 11;
        pointer-events: none;
    }

    canvas.canvas-drawer {
        position: absolute;
        z-index: 10;
        // filter: blur(20px);
    }

    canvas.canvas-regions {
        position: absolute;
        top: 0;
        left: 0;
    }
}

.plane-parameters {
    .script-forces {
        display: flex;
        flex-wrap: wrap;

        .forces-label {
            font-size: 10pt;
            margin-right: 5px;
            margin-left: 5px;
        }

        .script-force {
            background-color: white;
            color: black;
            padding: 2px;
            margin-right: 5px;

            .automation-icon {
                cursor: pointer;
            }
        }
    }
}

.plane-toolbar {
    .plane-toolbar-inner {
        .inner-part-1 {
        }
    }
}
