
.v3 {
    .click-to-start.loading {
        filter: blur(16px);
    }
    .click-to-start.already-started {
        background-color: transparent;
        pointer-events: none;

        .image-wrapper {
            width: 40px;
            height: 40px;

            img {
                width: 40px;
                height: 40px;
            }
        }
    }
    .click-to-start {
        .image-wrapper {
            position: absolute;
            width: 270px;
            height: 300px;
            margin-left: auto;
            margin-right: auto;
            margin-top: auto;
            margin-bottom: auto;
            right: 0;
            top: 0;
            left: 0;
            bottom: 0;
            z-index: 0;
            img.helper-image {
                position: absolute;
                top: 0px;
                left: 0px;
                width: 270px;
                height: 300px;
            }
            
            img.arrow-1, img.arrow-2, img.arrow-3, img.arrow-4 {
                position: absolute;
                animation: arrow-blink .72s ease infinite;
                transform-origin: 50% 50%;
                animation-direction: alternate;
            }

            img.arrow-1 {
                left: 60px;
                top: 52px;
            }
            img.arrow-2 {
                left: 186px;
                top: 68px;
            }
            img.arrow-3 {
                left: 60px;
                top: 162px;
            }
            img.arrow-4 {
                left: 186px;
                top: 190px;
            }
        }

        span {
            margin: auto;
            font-size: 24pt;
            z-index: 2;
            background-color: #00000000;
            padding: 15px;
            border-radius: 35px;
            backdrop-filter: blur(4px);
        }
    }
}
.sequencer-container.NFT {
    font-family: inter !important;
    -webkit-font-smoothing: antialiased;
}


@keyframes arrow-blink {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(1.3);
    }
}
