.upload-btn {
    cursor: pointer;
    margin-left: 10px;
    background-color: #37902d;
    color: white;
    padding: 10px;
}

.waveform {
    margin-bottom: 10px;
    float: left;
}

.sound-details {
    float: left;
    text-align: right;
    width: 65px;;
    height: 65px;
    background-color: #110f1f;
    cursor: pointer;
}

.delete-button {
    line-height: 8px;
    font-size: 9pt;
}

.selected.sound-waveform {
    border-style: solid;
    border-color: #a9a9ff;
}

.buttons-container {
    margin: 10px;
    clear: both;
}

.columns-container {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    height: 100%;
    flex-wrap: wrap;
}

.column {
    margin: 10px;
    border-style: solid;
    border-width: thin;
    border-color: #232358;
}

.new-column {
    height: 100%;
    align-self: stretch;
    border-style: dashed;
    border-width: thin;
    border-color: #232358;

    display: flex;
    width: 150px;
    height: 500px;
}

input::placeholder {
  color: #232358;
}

.inside {
    flex: 1;
    text-align: center;
    color: #232358;
}

.sounds-container {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.tags-container {
    margin-bottom: 5px;
}

.tags-input {
    width: 200px;
    background-color: black;
    color: white;
    margin-left: 8px;
}

.text-instructions {
    margin-top: 30px;
    margin-bottom: 30px;
}
