.modulator-controls {
    position: relative;
    .switch-modulators-btn {
        position: absolute;
        top: 0px;
        right: -30px;
        cursor: pointer;
        height: 15px;
        top: 30%;
        width: 25px;
    }

    .modulator-control-container {
        display: flex;
        .modulator-thumb {
            width: 9px;
            height: 9px;
            margin-left: 4px;
            font-size: 6pt;
            text-align: center;
            line-height: 9px;
        }

        .modulator-thumb.env {
            border-radius: 9px;
        }
    }

    .modulator-control:active {
        background-color: #00000061;
    }

    .modulator-control {
        width: 100%;
        height: 9px;
        background-color: #0000001a;
        position: relative;
        margin-bottom: 1px;
        
        .mod-number {
            position: absolute;
            top: -2px;
            left: 40%;
            color: white;
            font-size: 10px;
        }

        .modulator-indicator {
            height: 2px;
            position: relative;
            top: 4px;
            max-width: 100%;
            // background-color: #949494e3;
            opacity: 0.8;

            .left-edge, .right-edge {
                position: absolute;
                border: 1px solid white;
                cursor: unset;
            }

            .left-edge:active, .right-edge:active, .right-edge:hover, .left-edge:hover {
                background-color: white;
            }

            .right-edge {
                right: -4px;
            }

            .big {
                top: -4px;
                height: 10px;
                width: 10px;
                border-radius: 10px;
            }

            .small {
                height: 8px;
                width: 8px;
                border-radius: 8px;
                top: -3px;
            }

            .left-edge {
                left: -4px;
            }
        }
    }

    .modulator-control:active {
        .modulator-indicator {
            opacity: 0.9;
            .left-edge, .right-edge {
                cursor: unset;
            }
        }
    }
}

.key-scaling-toggle.toggled {
    border-left-width: 1px;
    border-bottom-width: 1px;
}

.key-scaling-toggle.toggled::before {
    border-left-width: 4px;
    border-bottom-width: 1px;
    border-top-width: 4px;
}

.key-scaling-toggled {
    cursor: pointer;
    width: 0; 
    height: 0; 
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    
    border-bottom: 10px solid #959798;
}

.key-scaling-toggle {
    position: relative;
    cursor: pointer;
    transform: scale(var(--ggs,1));
    width: 13px;
    height: 10px;
    border-left: 1px solid transparent;
    border-bottom: 1px solid #959798;
}


.modulator-indicator.key-scaling {
    background-color: #959798;
}

.key-scaling-toggle,
.key-scaling-toggle::before {
    display: block;
    box-sizing: border-box;
    border-right: 1px solid transparent
}
.key-scaling-toggle::before {
    content: "";
    position: absolute;
    width: 11px;
    height: 11px;
    border-left: 1px solid #959798;
    border-top: 1px solid #959798;
    border-bottom: 1px solid transparent;
    transform: rotate(45deg) skew(10deg,10deg);
    left: 0px;
    bottom: -6px
} 

