.effect.midi.Generative_Chords {
    background-color: #292929bd;
}

.generative-chords {
    display: flex;
    padding: 15px;
    
    
    .scale-chooser {
        display: flex;
        .Dropdown-control {
        }
    }

    svg.rack-canvas {
        width: 400px;
        height: 300px;
        background-color: #00000094;

        circle.active {
            fill: #1bffc0;
        }

        rect.node.chord-1 {
            fill: black !important;
        }

        rect.node.chord-2 {
            fill: #424256 !important;
        }

        rect.node.chord-3 {
            fill: #4a4a4a !important;
        }

        rect.node {
            fill: #e5e6d6 !important;
        }
        rect.node.selected {
            fill: red !important;
        }

        text.chord-1, text.chord-2, text.chord-3 {
            fill: white;
        }

        text {
            fill: black;
        }



        path.edge {
            stroke: #a2a2a2;
            stroke-dasharray: none;
        }
    }

    .rack-toolbar {
        display: none;
    }

    .chord-chooser {
        padding: 15px;
        width: 300px;

        .tidal-string {
            display: flex;
            .tidal-input {
                font-size: 16pt;
                height: 22px;
                background-color: black;
                color: white;
            }

            .Enter {
                height: 22px;
                background-color: white;
                padding-left: 5px;
                padding-right: 5px;
                color: black;
                cursor: pointer;
                line-height: 22px;
                text-align: center;
            }

            .Enter:active {
                box-shadow: 0px 0px 2px 2px white;
            }

        }
        .current-chord-viewer {
            display: flex;

            .chord-note {
                .note-name {
                    font-size: 9pt;
                    margin: unset;
                    color: white;
                    float: unset;
                    width: unset;
                    border: unset;
                }
                .degree {
                    font-size: 6pt;
                }
                width: 20px;
                text-align: center;
                background-color: black;
                margin-left: 5px;
            }
        }

        .progression {
            background-color: black;
        }

        .possible-next, .current-chord, .chord-part, .expression {
            margin-left: 5px;
            width: 30px;
            height: 30px;
            line-height: 30px;
            background-color: black;
            text-align: center;
            cursor: pointer;
        }

        .chord-part.current {
            background-color: white;
            color: black;
        }

        .chord-part.selected {
            border: 1px dashed white;
        }

        .possible-next.selected, .expression.selected {
            background-color: white;
            color: black;
        }

        .possibilities, .current-chord-viewer, .progression, .expression-series {
            padding: 5px;
            display: flex;
        }
    }
}
