.micro-tuning {
    .error.micro-tuning-function {
        input.micro-script-input {
            background-color: #a03030;
        }
    }

    .micro-tuning-function {
        display: flex;

        input.micro-script-input {
            width: 100%;
            background-color: #3432949c;
            color: white;
        }
        .execute-button {
            padding: 3px;
            font-size: 9pt;
            background-color: green;
        }

        .error.execute-button {
            pointer-events: none;
            opacity: 0.2;
        }
    }

    .notes-container {
        height: 200px;
        overflow-y: scroll;

        .micro-tuning-note {
            display: flex;
            background-color: white;
            margin: 1px;

            .micro-piano-note {
                width: 40px;
                height: 15px;
                background-color: white;
                color: #0000008f;
                text-align: right;
                font-size: 8pt;
            }

            .micro-piano-note.black {
                margin-left: 10px;
                width: 30px;
                background-color: black;
            }

            .offset-control {
                flex: 1;
                background-color: #000000bd;
                height: 15px;

                .hidden-text {
                    display: none;
                    font-size: 7pt;
                    text-align: center;
                }

                .volume-box:active {
                    cursor: unset;

                    .volume-indicator {
                        cursor: unset;
                    }
                }

                .volume-box {
                    height: 15px;

                    .volume-indicator {
                        height: 15px;
                    }
                }
            }

            .offset-control:hover {
                .hidden-text {
                    display: block;
                }
            }
        }
    }
}
