.parameter-link-visualizer {
    position: absolute;
    pointer-events: none;
    z-index: 10000000000;

    polygon {
        fill: #5da9ff;
        opacity: 0.7;
        z-index: 10000;
        
    }
}

.accumulator-corner {
    position: absolute;
    top: 0px;
    right: 0px;
    width: 8px;
    height: 8px;
    background-color: #0000006b;
    cursor: pointer;
}

.p-box {
    input.volume-box {
        width: 40px;
    }
}

.control-parameters.accumulator, .zmacro {
    display: flex;

    .acc-column {
        .value-list-item.current {
            .delete-btn {
                background-color: white;
                color: black;
            }
        }
        
        .value-list-item {
            width: 80px;
            max-width: 80px;
            overflow: hidden;
            display: flex;
            padding: 2px;
            margin: 2px;

            .delete-btn {
                margin-left: auto;
                border-radius: 15px;
                background-color: black;
                width: 15px;
                height: 15px;
                font-size: 10pt;
                cursor: pointer;
                line-height: 15px;
                text-align: center;
            }

            input {
                width: 80px;
                background-color: black;
                color: white;
                border: none;
            }
        }
    }

}

.plane-parameters, .plane-inner {

    .control-toolbar {
        display: flex;
        align-items: flex-start;

        .row-powa {
            background-color: #35353580;
            padding: 5px;
            .mappy-btn, .macro-map-button {
                margin-top: 3px;
            }
        }
    }

    .y-axis {
        text-orientation: sideways;
        writing-mode: vertical-rl;
        position: absolute;
        top: 70px;
        color: white;
        z-index:0;
    }

    .x-axis {
        position: absolute;
        color: white;
        top: 180px;
        left: 100px;
        z-index:0;
    }

    .x-axis, .y-axis {
        display: none;
    }
    .plane-toolbar {
        display: flex;
        background-color: #35353580;
        padding-bottom: 3px;
        align-items: flex-start;

        .parameter-box  {
            flex-wrap: wrap;
            width: unset !important;
        }

        .plane-btn.selected {
            background-color: white;
            color: black;
        }

        .plane-btn:active {
            box-shadow: 0px 0px 1px 1px #808080a1;
        }

        .plane-btn {
            padding: 3px;
            background-color: #000000;
            border: 1px solid #3c3c3c;
            /* display: flex; */
            /* border-radius: 3px; */
            cursor: pointer;
            color: white;
            text-align: center;
            margin-right: 5px;
            padding-right: 8px;
            padding-left: 8px;
        }

        .plane-btn.disabled {
            pointer-events: none;
            opacity: 0.2;
        }
    }
    

    .plane-sidebar {
        width: 240px;
        position: relative;

        .macro-info {
            display: flex;
            margin-bottom: 5px;
            background-color: #232323;
            padding: 5px;

            .chrome-picker {
                top: 50px;
            }

            .slider {
                display: none;
            }

            .macro-indicator {
                margin-top: 2px;
                width: 15px;
                height: 15px;
                margin-right: 6px;
                border-radius: 12px;
            }

            .macro-name {
                font-size: 10pt;
                flex: 1;
            }

            .macro-show-btn.selected {
                background-color: white;
                color: black;
                box-shadow: 0px 0px 2px 2px white;
            }
            
            .macro-show-btn {
                margin-left: auto;
                cursor: pointer;
                background-color: black;
                padding: 3px;
            }
        }

        .plane-search-container {
            position: relative;

            input.plane-search {
                outline: none !important;
                border: none !important;
                width: 220px;
                font-size: 9pt;
                background-color: black;
                color: white;
            }

            .clear-btn {
                font-size: 14px;
                position: absolute;
                right: 15px;
                top: 0px;
                cursor: pointer;
                color: white;
            }
        }

        .plane-results.parameters-list {
            font-size: 9pt;
            margin-left: unset;
        }
        
        .plane-results {
          //  max-height: 170px;
            overflow-y: scroll;
            background-color: #10101042;
            padding: 5px;

            .plane-param-group {
                .group-parameters-name {
                    display: flex;

                    .group-thumb:active {
                        box-shadow: 0px 0px 2px 2px white;
                    }

                    .group-thumb {
                        cursor: pointer;
                        width: 12px;
                        height: 12px;
                        margin-right: 5px;
                    }

                    line-height: 12px;
                    margin-bottom: 2px;
                }

                .group-parameters {
                    margin-left: 8px;
                }
            }
            .device {
                background-color: #0000008c;
                margin: 1px;

                .device-name {
                }

                .device-parameters {
                    margin-left: 10px;

                    .device-parameter.selected {
                        background-color: white;
                        color: black;
                    }

                    .device-parameter.locked {
                        opacity: 0.3;
                    }
                    
                    #accumulator-map-source {
                        animation: fadeparam 1s 0.1s infinite;
                        animation-direction: alternate;
                    }

                    .device-parameter:hover {
                        .ungroup-parameter {
                            display: block;
                        }

                        .d-parameter-value, .d-parameter-depth {
                            display: none;
                        }
                    }

                    .device-parameter {
                        display: flex;

                        .dot {
                            margin-right: 4px;
                            height: 8px;
                            width: 8px;
                            margin-top: 3px;
                        }

                        .d-parameter-min, .d-parameter-max {
                            max-width: 80px;
                            overflow: hidden;
                            padding-left: 3px;
                            padding-right: 3px;
                            white-space: nowrap;
                        }

                        .min-max-input {
                            width: 40px;
                            background-color: black;
                            color: white;
                        }

                        .d-parameter-value {
                            width: 40px;
                        }
                        .d-parameter-depth {
                            width: 25px;
                        }
                        .d-parameter-value, .d-parameter-min {
                            margin-left: auto;
                            background-color: #546365;
                        }

                        .d-parameter-depth, .d-parameter-max {
                            margin-left: 2px;
                            background-color: #252525;
                        }

                        .d-parameter-value, .d-parameter-depth {
                            line-height: 15px;
                            overflow: hidden;
                            padding-left: 3px;
                            color: white;
                        }

                        .d-parameter-name {
                            line-height: 15px;
                            overflow: hidden;
                            white-space: nowrap;
                        }

                        .ungroup-parameter {
                            display: none;
                            background-color: white;
                            color: black;
                            margin-left: auto;
                            padding-left: 2px;
                            padding-right: 2px;
                            line-height: 12px;
                            font-size: 8pt;
                            cursor: pointer;
                        }
                    }
                }
            }
        }
    }

   .plane-container {
        display: flex;

        .plane-view {
            display: flex;
            position: relative;
        }
    }

    .plane-group-config {
        .group-sequencer {
            margin-top: 5px;

            .sequencer-toolbar {
                display: flex;

                .sequencer-label {
                }

                .s-play, .s-stop {
                    cursor: pointer;
                    margin-left: 10px;
                }
                
                .s-play {
                     width: 0; 
                     height: 0; 
                     border-top: 8px solid transparent;
                     border-bottom: 8px solid transparent;
                     border-left: 12px solid white;
                }

                .s-stop {
                    border-right: 5px solid #16ff16;
                    border-left: 5px solid #16ff16;
                    background-color: transparent;
                    cursor: pointer;
                    width: 14px;
                    height: 14px;
                }
            }

            .sequencer-steps {
                display: flex;
                flex-wrap: wrap;
            }

            .sequencer-steps.disabled {
                opacity: 0.5;
            }

            .sequencer-step.current {
                background-color: red;
            }

            .sequencer-step.selected {
                box-shadow: 0px 0px 2px 2px white;
            }
            
            .sequencer-step {
                width: 14px;
                height: 14px;
                background-color: #565656;
                cursor: pointer;
                margin-right: 3px;
                text-align: center;
                font-size: 8pt;
            }

            .sequencer-step.on {
                background-color: white;
                color: black;
            }
        }
    }

    .breed-button:active, .genetic-btn:active {
        box-shadow: 0px 0px 2px 2px white;
    }

    .plane-group-config {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-left: auto;
    }

    .plane-group-buttons {
        display: flex;
    }
    
    .user-script-component {
        .script-box {
            height: 160px;
        }
    }
 
    .plane-algo {
        display: flex;
        padding-top: 2px;
        align-items: flex-start;

       .parameter-box {
           width: 180px;
           margin-right: 0px;

           .slider-and-labels {
               display: flex;
           }
            .parameter-label {
                margin-top: unset;
            }
            .parameter-label-value {
                width: 40px;
            }
        }
    }

    .parameter-box {
        display: flex;
        .parameter-label-value {
            width: 80px;
        }
        .slider.slider-horizontal {
            width: 90px !important;
        }
    }

    .mappy-btn:active {
        box-shadow: 0px 0px 1px 1px #808080a1;
    }
    
    .mappy-btn, .macro-map-button {
        background-color: black;
        border: 1px solid #3c3c3c !important;
        margin-right: 3px;
        color: white;
        padding: 3px;
        /* display: flex; */
        cursor: pointer;
        width: 90px;
        text-align: center;
    }

    svg {
        // shape-rendering: optimizeSpeed;
        z-index: 1;
        // background-color: #0000006e !important;

        .region-bounds {
            stroke: #00a4ff;
            fill: #23757b4f;
            stroke-dasharray: 3;
        }

        .group-path {
            fill: transparent;
            stroke-dasharray: 1;
            opacity: 0.5;
        }

        .macro.showing {
            animation: fadeparam 1s 0.1s infinite;
            animation-direction: alternate;
        }

        .macro.selected {
            fill: black;
            stroke-width: 1px;
        }

        text.macro-text.selected {
            fill: white;
        }

        text.macro-text {
            fill: transparent;
        }

        circle.macro {
            opacity: 0.5;
        }

        .star.selected {
            fill: black;
            stroke-width: 1px;
        }
    }
}


.mappy-btn.mapping {
    animation: fadeparam 1s 0.1s infinite;
    animation-direction: alternate;
}

.pad-effect-control.plane-mapping, .effect.track-send.plane-mapping, .parameter-knob.plane-mapping, .track-option.plane-mapping {
    animation: fadeparam 1s 0.1s infinite;
    animation-direction: alternate;
}

.effect.plane-mapping, .effect.midi.plane-mapping {
    .effect-header {
        animation: fadeparam 1s 0.1s infinite;
        animation-direction: alternate;
        cursor: pointer;
    }
}

.lfo-component.plane-mapping, .instrument.plane-mapping {
    animation: fadeparam 1s 0.1s infinite;
    animation-direction: alternate;
    cursor: pointer;
}

.genetic-label {
    margin-left: 5px;
    margin-right: 10px;
}

.breed-done.genetic-btn {
    .inner-btn {
        margin: auto;
        margin-top: 2px;
        width: 14px;
        height: 14px;
        background-color: white;
    }
}

.genetic-btn.snapshot {
    width: 20px;
}

.genetic-btn, .breed-button {
    width: 25px;
    height: 25px;
    margin-right: 5px;
    background-color: #00000059;
    cursor: pointer;
    color: white;
    padding: 3px;
    align-items: flex-start;
}

.genetic-btns {
    display: flex;
    background-color: #35353580;
}

.genetic-algo {
    padding: 5px;
    background-color: #35353580;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .generation {
        display: flex;
        width: 100%;
        
        .generation-label {
            flex: 1;
        }

        .breed-button {
            margin-left: auto;
        }
    }

    .variations {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 5px;
        max-width: 230px;

        .elite, .children, .mutations {
            display: flex;
            flex-wrap: wrap;
            padding: 2px;
            position: relative;

            .label {
                color: white;
                position: absolute;
                top: 0px;
                left: -2px;
                font-size: 6pt;
                margin: 0px;
            }
            background-color: black;

            .variation {
                margin-top: 10px;
                margin-bottom: 5px;
            }
        }

        .elite {
            border-top: 4px solid #ffffff;
        }
        .children {
            border-top: 4px solid #616161;
        }
        .mutations {
            border-top: 4px solid #191919;
        }

        .variation.selected {
            box-shadow: 0px 0px 2px 2px #ffffffbf;
        }

        .variation.best {
            background-color: white !important;
            color: black;
        }

        .variation {
            cursor: pointer;
            background-color: #4a4a4a7d;
            color: white;
            border-radius: 16px;
            margin-right: 3px;
            border-radius: 14px;
            width: 14px;
            line-height: 14px;
            height: 14px;
            font-size: 8pt;
            text-align: center;
        }
    }
}


.param-enabled-btn {
    width: 12px;
    height: 12px;
    margin-top: 3px;
    background-color: #3bff3b;
    border-radius: 12px;
    margin-left: 3px;
    cursor: pointer;
}

.param-enabled-btn.disabled {
    background-color: black;
}

.pulled-instrument {
    .snapshots-container {
        background-color: #000000c7;
        padding: 5px;
    }
}

.snapshots-container {
    width: 230px;
    background-color: #35353580;

   .snapshot-toolbar {
        display: flex;
    }

    .plane-snapshot-btn.delete-snapshot {
        background-color: #867676;
        color: white;
        font-size: 22pt;
        line-height: 8px;
        margin-right: 5px;
    }

    .delete-all {
        margin-left: auto;
        width: 15px;
        height: 15px;
        background-color: white;
        color: black;
        text-align: center;
        line-height: 15px;
        cursor: pointer;
    }

    .plane-snapshot-btn {
        margin-left: 5px;
        height: 15px;
        width: 15px;
        border-radius: 15px;
        background-color: white;
        cursor: pointer;
        font-size: 12pt;
        line-height: 13px;
        text-align: center;
        color: black;
    }

    .plane-shapshot-btn:active {
        box-shadow: 0px 0px 2px 2px white;
    }

    .label {
        padding: 0px;
        margin: 0px;
        font-size: 9pt;
    }

    .snapshots-list {
        display: flex;
        margin-top: 5px;
        flex-wrap: wrap;
        .snapshot {
            width: 20px;
            height: 20px;
            text-align: center;
            line-height: 20px;
            background-color: black;
            color: white;
            margin-right: 2px;
            cursor: pointer;
        }

        .snapshot.current {
            animation: flash-pattern 2s;
            background-color: white;
            color: black;
        }
    }
}
.plane-parameters {
    .plane-toolbar {
        .snapshot-toolbar {
            .parameter-box {
                margin: 0px;
                width: unset;
                .parameter-label {
                    margin: 0px;
                }
                .parameter-label-value {
                    display: none;
                }
            }
        }
    }
}
