.message-edge-group.selected {
    z-index: 1000000;
}

.message-edge-group {
    z-index: 10;
}

.edges-drawer {
    .pad-effect-edge.message-edge {
        stroke: #60708d;
    }
    .pad-effect-edge.message-edge.selected {
        stroke: red;
    }
}

.presentation-mode {
    .pulled-effect, .pulled-pad, .z-object {
        .inlets, .outlets {
            display: none;
        }
    }
}

.pulled-effect, .pulled-pad, .z-object {
    overflow: visible;
    .inlets {
        top: -5px;
    }

    .outlets {
        bottom: -5px;
    }

    .inlets {
        .msg-tooltip {
            top: -30px;
        }
    }

    .outlets {
        .msg-tooltip {
            top: 20px;
        }
    }
    
    .inlets, .outlets {
        position: absolute;
        left: -8px;
        z-index: 1000000000;
        display: flex;
        overflow: visible;

        .msg-tooltip {
            z-index: 100000000000000000000000;
            .dot {
                background-color: black;
                width: 8px;
                height: 8px;
                margin-top: 6px;
                margin-right: 5px;
            }
            
            position:absolute;
            white-space: nowrap;
            pointer-events: none;
            left: 0px;
            background-color: #f5f4e8d2;
            color: black;
            border-radius: 3px;
            padding-left: 5px;
            padding: 5px;
            padding-right: 15px;
            font-size: 9pt;
        }

        .inlet:hover, .outlet:hover, .inlet.connected:hover, .outlet.connected:hover {
            background-color: red;
            border-radius: 1.2ch;
            margin-top: 0px;
            margin-bottom: 0px;
            height: 1.2ch;
            transform: scale(1.5);
        }

        .inlet.connected, .outlet.connected {
            background-color: black;
        }

        .outlet.connected {
            border-left: 1px solid #d98c00;
            border-top: 1px solid #d98c00;
            border-right: 1px solid #d98c00;
        }

        .inlet.connected {
            border-left: 1px solid #d98c00;
            border-bottom: 1px solid #d98c00;
            border-right: 1px solid #d98c00;
        }

        .inlet {
            border-radius: 0 0 1.2ch 1.2ch;
            margin-top: .6ch;
        }

        .outlet {
            border-radius: 1.2ch 1.2ch 0 0;
            margin-bottom: .6ch;
        }

        .inlet, .outlet {
            width: 1.2ch;
            height: .6ch;
            margin-left: 2.5ch;
            background-color: white;
        }
    }
}



.message-button {
    cursor: pointer;
    min-width: 80px;
    height: 25px;
    border-radius: 5px;
    background-color: #c4c4c4;

    display: flex;
    
    .message-content:active {
        box-shadow: 0px 0px 4px 4px white;
        filter: saturate(2);
    }

    input {
        width: 80px;
        height: 25px;
        flex: 1;
        border: none;
        color: black;
    }
    
    .message-content {
        padding-left: 10px;
        line-height: 25px;
        border-radius: 5px;
        color: black;
        flex: 1;
    }
}
