.scenes-container {
    display: flex;

    .Dropdown-control {
        background-color: #252525a3 !important;
    }

    .Dropdown-control.pattern-dropdown {
        width: 120px !important;
    }

    .scenes-component {
        width: 225px;
        padding: 5px;
        background-color: #64636373;
        margin-left: 15px;

        .scene-option {
            display: flex;
            cursor: pointer;
            font-size: 9pt;

            input {
                background-color: #1d1d1d;
                color: white;
            }

            .scene-info {
                margin-right: 5px;
                margin-left: auto;
                color: #cacaca;
            }

            .delete-btn.hide {
                opacity: 0;
                pointer-events: none;
            }

            .delete-btn {
                font-size: 12pt;
                line-height: 15px;
                margin-right: 5px;
                margin-left: 5px;
                cursor: pointer;
            }
        }

        .scene-option.current {
            background-color: white;
            color: black;

            .scene-info {
                color: black;
            }
        }

        .scenes-toolbar {
            display: flex;

            .scenes-header {
                font-size: 14pt;
            }

            .new-scene-btn {
                font-size: 14pt;
                margin-left: auto;
                padding-left: 5px;
                padding-right: 5px;
                background-color: white;
                color: black;
                cursor: pointer;
            }
        }
    }

    .scene-details {
        padding: 5px;
        background-color: #64636373;
        margin-left: 15px;

        .sub-sequence-details {
            display: flex;
            font-size: 10pt;

            .sub-name {
                margin-right: 5px;
            }

            .pattern-number {
                margin-left: 10px;
                margin-right: 10px;
            }

            .Dropdown-control {
                height: 20px;
                width: 100px;
                border-color: #333333;

                .Dropdown-arrow {
                    top: 6px;
                }
                .Dropdown-placeholder {
                    line-height: 4px;
                }
            }
        }
    }
}

.mix-modules {
    display: flex;
    flex: 1;
    .pulled-effects {
        flex: 1;
    }
}

