
.z-object.custom.z-track {
    border-radius: 10px;
    padding: 2px;

    .youtube-info {
        .sound-thumbnail, .youtube-thumbnail {
            border-radius: 8px;
        }
    }
}

.z-object.custom.z-sample-bank {
    border-radius: 10px;
    padding: 10px;
    background-color: black;
    .sample-bank {
        background-color: #1a1a1a;
        border-radius: 8px;
        .samples {
            display: flex;
            flex-direction: column;
            font-size: 9pt;
            padding: 2px;
            max-height: 240px;
            max-width: 220px;
            overflow-y: scroll;

            .sample-option:hover {
                border-color: white;
                .delete-btn {
                    display: block;
                    cursor: pointer;
                }
            }

            .sample-option.current {
                background-color: white;
                color: black;
            }

            .sample-option {
                position: relative;
                width: 205px;

                .delete-btn {
                    display: none;
                    width: 15px;
                    height: 15px;
                    left: 190px;
                    text-align: center;
                    border-radius: 15px;
                    line-height: 13px;
                    position: absolute;
                    background-color: white;
                    color: black;
                }

                white-space: nowrap;
                overflow: hidden;
                cursor: pointer;
                display: flex;
                border: 1px solid transparent;

                .sample-name {
                    font-size: 9pt;
                    margin-top: unset;
                }
                .sample-tag {
                    margin-left: 3px;
                    color: gray;
                    font-size: 9pt;
                }
            }
        }

        .drop-here {
            width: 240px;
            height: 90px;
            display: flex;

            .drop-text {
                font-size: 9pt;
                margin: auto;
            }
        }
    }
}
