.freeze-modal.modal-dialog {
    .modal-content {
        background-color: #607bff82 !important;
        color: #ffffff;
        backdrop-filter: blur(8px);
        border-radius: 25px;
        background-image: linear-gradient(360deg, #841e1e75, transparent);
    }

    .btn.btn-secondary {
        background-color: white !important;
        background-image: linear-gradient(45deg, #11c5fbf2, #fff7009c);
        color: black;
        border-radius: 12px;
    }

    .freeze-options {
        display: flex;
        .freeze-description {
            width: 180px;
            padding: 15px;
            padding-top: 5px;
        }

        .freeze-option:hover {
            background-color: #ffffff3d;
            color: black;
        }
        .freeze-option {
            border: 1px solid gray;
            cursor: pointer;
            padding: 35px;
            font-size: 16pt;
            img {
                height: 30px;
                margin-left: 15px;
            }
        }
        .freeze-option.selected {
            background-color: #ffffff3d;
            border: 1px solid white;
            padding: 35px;
        }
    }
    
    .modal-header, .modal-footer {
        img {
            width: 28px;
            margin-left: 15px;
            filter: contrast(5.5);
        }
        border-bottom: none !important;
        border-top: none !important;
    }
}
